// Simple double-beep ("tin tin") sound to play on confirmations
// Uses Web Audio API and keeps a singleton AudioContext for reuse

let audioCtx: AudioContext | null = null;

function getAudioContext(): AudioContext {
  const AnyWindow = window as any;
  if (!audioCtx) {
    const Ctx = (AnyWindow.AudioContext || AnyWindow.webkitAudioContext);
    audioCtx = new Ctx();
  }
  if (audioCtx.state === 'suspended') {
    // Resume context on user gesture
    audioCtx.resume();
  }
  return audioCtx;
}

function scheduleBeep(ctx: AudioContext, startTime: number, freq: number, durationMs: number) {
  const osc = ctx.createOscillator();
  const gain = ctx.createGain();
  osc.type = 'sine';
  osc.frequency.value = freq;
  osc.connect(gain);
  gain.connect(ctx.destination);

  const durationSec = durationMs / 1000;
  // soft attack then decay
  gain.gain.setValueAtTime(0.0001, startTime);
  gain.gain.exponentialRampToValueAtTime(0.2, startTime + 0.02);
  gain.gain.exponentialRampToValueAtTime(0.0001, startTime + durationSec);

  osc.start(startTime);
  osc.stop(startTime + durationSec + 0.02);
}

export function playTinTin() {
  try {
    const ctx = getAudioContext();
    const now = ctx.currentTime;
    // First beep
    scheduleBeep(ctx, now, 880, 140);
    // Second beep a short moment later
    scheduleBeep(ctx, now + 0.18, 980, 140);
  } catch (e) {
    // fail silently to avoid breaking UX if audio is unavailable
    // console.warn('Audio playback failed', e);
  }
}

// --- Vodafone "Shakshan" style tone ---
// Approximation using a short noise burst ("shak") followed by a brief tone sweep ("shan")
function scheduleNoiseBurst(ctx: AudioContext, startTime: number, durationMs: number) {
  const durationSec = durationMs / 1000;
  const bufferSize = Math.floor(ctx.sampleRate * durationSec);
  const buffer = ctx.createBuffer(1, bufferSize, ctx.sampleRate);
  const data = buffer.getChannelData(0);
  for (let i = 0; i < bufferSize; i++) {
    // white noise
    data[i] = (Math.random() * 2 - 1) * 0.6;
  }
  const source = ctx.createBufferSource();
  source.buffer = buffer;

  const filter = ctx.createBiquadFilter();
  filter.type = 'highpass';
  filter.frequency.value = 1500;
  filter.Q.value = 0.7;

  const gain = ctx.createGain();
  gain.gain.setValueAtTime(0.0001, startTime);
  gain.gain.exponentialRampToValueAtTime(0.4, startTime + 0.015);
  gain.gain.exponentialRampToValueAtTime(0.0001, startTime + durationSec);

  source.connect(filter);
  filter.connect(gain);
  gain.connect(ctx.destination);

  source.start(startTime);
  source.stop(startTime + durationSec + 0.01);
}

function scheduleToneSweep(ctx: AudioContext, startTime: number, startFreq: number, endFreq: number, durationMs: number, type: OscillatorType = 'triangle') {
  const osc = ctx.createOscillator();
  const gain = ctx.createGain();
  osc.type = type;

  const durationSec = durationMs / 1000;
  osc.frequency.setValueAtTime(startFreq, startTime);
  osc.frequency.linearRampToValueAtTime(endFreq, startTime + durationSec);

  // gentle attack/decay envelope
  gain.gain.setValueAtTime(0.0001, startTime);
  gain.gain.exponentialRampToValueAtTime(0.25, startTime + 0.02);
  gain.gain.exponentialRampToValueAtTime(0.0001, startTime + durationSec);

  osc.connect(gain);
  gain.connect(ctx.destination);

  osc.start(startTime);
  osc.stop(startTime + durationSec + 0.02);
}

export function playVodafoneShakshan() {
  try {
    const ctx = getAudioContext();
    const now = ctx.currentTime;
    // "shak": short filtered noise burst
    scheduleNoiseBurst(ctx, now, 90);
    // "shan": brief downward tone sweep
    scheduleToneSweep(ctx, now + 0.12, 1900, 1100, 180, 'sawtooth');
  } catch (e) {
    // silent fail
  }
}
