import React, { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Eye, EyeOff, Lock, AlertCircle } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { ProfitService } from '@/utils/profitService';
import { MasterPinService } from '@/utils/masterPinService';
import { useEffect } from 'react';

interface ProfitPinDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onSuccess: () => void;
  userId?: string;
}

export function ProfitPinDialog({ 
  open, 
  onOpenChange, 
  onSuccess, 
  userId 
}: ProfitPinDialogProps) {
  const [pin, setPin] = useState('');
  const [confirmPin, setConfirmPin] = useState('');
  const [showPin, setShowPin] = useState(false);
  const [showConfirmPin, setShowConfirmPin] = useState(false);
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [hasExistingPin, setHasExistingPin] = useState(false);
  const { toast } = useToast();

  useEffect(() => {
    let mounted = true;
    (async () => {
      const has = await MasterPinService.hasMasterPin(userId);
      if (mounted) setHasExistingPin(has);
    })();
    return () => {
      mounted = false;
    };
  }, [userId, open]);

  const handleClose = () => {
    setPin('');
    setConfirmPin('');
    setError('');
    setShowPin(false);
    setShowConfirmPin(false);
    onOpenChange(false);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    try {
      if (hasExistingPin) {
        //     
        const ok = await MasterPinService.verifyMasterPin(pin, userId);
        if (!ok) {
          setError('   ');
          setIsLoading(false);
          return;
        }
      } else {
        //     
        if (pin.length < 4) {
          setError('     4   ');
          setIsLoading(false);
          return;
        }

        if (pin !== confirmPin) {
          setError('   ');
          setIsLoading(false);
          return;
        }

        const created = await MasterPinService.createMasterPin(pin, userId);
        if (!created) {
          setError('    ');
          setIsLoading(false);
          return;
        }
      }

      toast({
        title: " ",
        description: hasExistingPin 
          ? "      " 
          : "     ",
      });

      onSuccess();
      handleClose();
    } catch (error) {
      setError('     ');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-md" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-lg font-semibold">
            <Lock className="h-5 w-5 text-green-600" />
            {hasExistingPin ? '   ' : '   '}
          </DialogTitle>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="text-sm text-gray-600 bg-blue-50 p-3 rounded-lg">
            {hasExistingPin 
              ? '      '
              : '       .      .'
            }
          </div>

          {/*    */}
          <div className="space-y-2">
            <Label htmlFor="pin">
              {hasExistingPin ? '  ' : '   '}
            </Label>
            <div className="relative">
              <Input
                id="pin"
                type={showPin ? "text" : "password"}
                value={pin}
                onChange={(e) => setPin(e.target.value)}
                placeholder={hasExistingPin ? "  " : "    (4   )"}
                className="pr-10"
                dir="ltr"
                autoComplete="off"
                required
              />
              <Button
                type="button"
                variant="ghost"
                size="sm"
                className="absolute left-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                onClick={() => setShowPin(!showPin)}
              >
                {showPin ? (
                  <EyeOff className="h-4 w-4" />
                ) : (
                  <Eye className="h-4 w-4" />
                )}
              </Button>
            </div>
          </div>

          {/*     (  ) */}
          {!hasExistingPin && (
            <div className="space-y-2">
              <Label htmlFor="confirmPin">  </Label>
              <div className="relative">
                <Input
                  id="confirmPin"
                  type={showConfirmPin ? "text" : "password"}
                  value={confirmPin}
                  onChange={(e) => setConfirmPin(e.target.value)}
                  placeholder="   "
                  className="pr-10"
                  dir="ltr"
                  autoComplete="off"
                  required
                />
                <Button
                  type="button"
                  variant="ghost"
                  size="sm"
                  className="absolute left-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                  onClick={() => setShowConfirmPin(!showConfirmPin)}
                >
                  {showConfirmPin ? (
                    <EyeOff className="h-4 w-4" />
                  ) : (
                    <Eye className="h-4 w-4" />
                  )}
                </Button>
              </div>
            </div>
          )}

          {/*   */}
          {error && (
            <div className="flex items-center gap-2 text-red-600 text-sm">
              <AlertCircle className="h-4 w-4" />
              {error}
            </div>
          )}

          {/*   */}
          <div className="flex gap-2 pt-4">
            <Button
              type="submit"
              disabled={isLoading}
              className="flex-1 bg-green-600 hover:bg-green-700"
            >
              <Lock className="h-4 w-4 mr-2" />
              {isLoading 
                ? " ..." 
                : hasExistingPin 
                  ? " " 
                  : "  "
              }
            </Button>
            <Button
              type="button"
              variant="outline"
              onClick={handleClose}
              disabled={isLoading}
            >
              
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}