import React, { useEffect, useMemo, useState, useRef } from 'react';
import { Capacitor } from '@capacitor/core';
import { useLocation } from 'react-router-dom';
import { createPortal } from 'react-dom';
import { Bell, CheckCircle, ExternalLink, Clock } from 'lucide-react';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { Broadcast, subscribeToActiveBroadcastsList } from '@/utils/broadcastService';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter, DialogTrigger } from '@/components/ui/dialog';
import { Textarea } from '@/components/ui/textarea';
import { addDoc, collection, serverTimestamp, query, where, orderBy, getDocs } from 'firebase/firestore';
import { db } from '@/firebase';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';

interface NotificationBellProps {
  className?: string;
}

function useReadIdsKey(userId?: string) {
  return userId ? `readBroadcastIds_${userId}` : `readBroadcastIds_guest`;
}

export default function NotificationBell({ className }: NotificationBellProps) {
  const { user, profile, isSubscriptionActive } = useFirebaseAuth();
  const location = useLocation();
  const [notifications, setNotifications] = useState<Broadcast[]>([]);
  const [readIds, setReadIds] = useState<Set<string>>(new Set());
  //   " "
  const [queueOpen, setQueueOpen] = useState(false);
  const [queueReason, setQueueReason] = useState('');
  const [queueSubmitting, setQueueSubmitting] = useState(false);
  const [queueMessage, setQueueMessage] = useState<string | null>(null);
  //          
  const [queueLatestStatus, setQueueLatestStatus] = useState<'none' | 'pending' | 'contacted'>('none');
  //        5     
  const [flyAlert, setFlyAlert] = useState<Broadcast | null>(null);
  const [flyVisible, setFlyVisible] = useState(false);
  const prevIdsRef = useRef<Set<string>>(new Set());
  const didInitRef = useRef(false);
  const isFirstFetchRef = useRef(true);
  const flyTimerRef = useRef<number | null>(null);
  //        
  const triggerRef = useRef<HTMLDivElement | null>(null);
  const flyNodeRef = useRef<HTMLDivElement | null>(null);
  const [flyStyle, setFlyStyle] = useState<React.CSSProperties>({ position: 'fixed', top: 0, left: 0, zIndex: 10000 });
  
  //     (fly-out)  
  const ENABLE_FLY_ALERT = true;

  const audienceKeys = useMemo(() => {
    const keys: string[] = ['global'];
    if (user?.uid) keys.push(`user:${user.uid}`);
    if (profile?.shopId) keys.push(`shop:${profile.shopId}`);
    return keys;
  }, [user?.uid, profile?.shopId]);

  const storageKey = useReadIdsKey(user?.uid);

  //          
  const canShowNotifications = useMemo(() => {
    const onPendingApprovalPage = location?.pathname?.includes('/user/pending-approval');
    //         
    //        
    const isSignedIn = Boolean(user?.uid);
    return isSignedIn && !onPendingApprovalPage;
  }, [user?.uid, location?.pathname]);

  useEffect(() => {
    //         
    try {
      const raw = localStorage.getItem(storageKey);
      if (raw) {
        const arr = JSON.parse(raw);
        if (Array.isArray(arr)) setReadIds(new Set(arr.filter(Boolean)));
      } else {
        setReadIds(new Set());
      }
    } catch (e) {
      console.warn('Failed to load read ids', e);
      setReadIds(new Set());
    }
  }, [storageKey]);

  useEffect(() => {
    //         
    if (!canShowNotifications) {
      setNotifications([]);
      return;
    }

    //         (  )
    isFirstFetchRef.current = true;
    
    const fetchNotifications = () => {
        subscribeToActiveBroadcastsList(audienceKeys, (list) => {
            if (isFirstFetchRef.current) {
                //        (  )   ""
                //            
                const now = Date.now();
                const freshThreshold = 60 * 1000; //  
                
                const oldIds = list.filter(n => {
                    //    
                    const createdAt = n.createdAt?.toMillis ? n.createdAt.toMillis() : (n.createdAt?.seconds ? n.createdAt.seconds * 1000 : 0);
                    const isFresh = (now - createdAt) < freshThreshold;
                    return !isFresh; //         
                }).map(n => n.id).filter(Boolean) as string[];

                prevIdsRef.current = new Set(oldIds);
                isFirstFetchRef.current = false;
            }
            setNotifications(list);
        });
    };

    //  
    fetchNotifications();

    //   OneSignal       
    const handleOneSignalNotification = (event: any) => {
        console.log(' OneSignal Notification received', event);
        
        // 1.        (Client-side)  
        try {
            const notif = event.notification || event;
            if (notif && (notif.title || notif.body)) {
                const instantBroadcast: Broadcast = {
                    id: `temp_${Date.now()}`,
                    title: notif.title || ' ',
                    message: notif.body || notif.message || '',
                    audienceKey: 'global',
                    active: true,
                    createdAt: { toMillis: () => Date.now(), seconds: Math.floor(Date.now() / 1000), nanoseconds: 0 } as any,
                };
                
                //  
                setFlyAlert(instantBroadcast);
                setFlyVisible(true);
                
                //   
                if (flyTimerRef.current) clearTimeout(flyTimerRef.current as any);
                flyTimerRef.current = window.setTimeout(() => {
                    setFlyVisible(false);
                    setFlyAlert(null);
                }, 5000);
            }
        } catch (e) {
            console.error('Error parsing OneSignal event for instant alert', e);
        }

        // 2.     
        fetchNotifications();
    };

    //   
    try {
        const OneSignal = (window as any).OneSignal;
        if (OneSignal && OneSignal.Notifications && OneSignal.Notifications.addEventListener) {
            OneSignal.Notifications.addEventListener('foregroundWillDisplay', handleOneSignalNotification);
        }
    } catch (e) {
        console.error('Failed to add OneSignal listener', e);
    }

    return () => {
        try {
            const OneSignal = (window as any).OneSignal;
            if (OneSignal && OneSignal.Notifications && OneSignal.Notifications.removeEventListener) {
                OneSignal.Notifications.removeEventListener('foregroundWillDisplay', handleOneSignalNotification);
            }
        } catch {}
    };
  }, [audienceKeys, canShowNotifications]);

  //            5 
  useEffect(() => {
    if (!ENABLE_FLY_ALERT) return; //    
    const ids = new Set((notifications.map((n) => n.id).filter(Boolean) as string[]));
    
    //     didInitRef    isFirstFetchRef   
    //          
    const newItems = notifications.filter((n) => n.id && !prevIdsRef.current.has(n.id!));
    if (newItems.length > 0) {
      setFlyAlert(newItems[0]);
      setFlyVisible(true);
      if (flyTimerRef.current) {
        clearTimeout(flyTimerRef.current as any);
      }
      flyTimerRef.current = window.setTimeout(() => {
        setFlyVisible(false);
        setFlyAlert(null);
      }, 5000);
    }
    prevIdsRef.current = ids;
  }, [notifications]);

  useEffect(() => {
    return () => {
      if (flyTimerRef.current) {
        clearTimeout(flyTimerRef.current as any);
      }
    };
  }, []);

  const unreadCount = notifications.reduce((acc, n) => acc + (n.id && !readIds.has(n.id) ? 1 : 0), 0);
  //        

  const markAsRead = (id?: string) => {
    if (!id) return;
    if (readIds.has(id)) return;
    const next = new Set(readIds);
    next.add(id);
    setReadIds(next);
    try {
      localStorage.setItem(storageKey, JSON.stringify(Array.from(next)));
    } catch {}
  };

  const markAllAsRead = () => {
    const ids = notifications.map((n) => n.id).filter(Boolean) as string[];
    const next = new Set(readIds);
    ids.forEach((id) => next.add(id));
    setReadIds(next);
    try {
      localStorage.setItem(storageKey, JSON.stringify(Array.from(next)));
    } catch {}
  };

  const openLink = (url?: string, id?: string) => {
    if (url) {
      try {
        const isAndroid = Capacitor?.isNativePlatform && Capacitor.getPlatform() === 'android';
        const isApk = /\.apk(\?|$)/i.test(url);
        if (isAndroid && isApk) {
          const deeplink = `cashy://update?url=${encodeURIComponent(url)}`;
          window.location.href = deeplink;
        } else {
          window.open(url, '_blank');
        }
      } catch {
        try { window.open(url, '_blank'); } catch {}
      }
    }
    if (id) markAsRead(id);
  };

  //      (  )
  const updateFlyPosition = () => {
    if (!triggerRef.current || !flyNodeRef.current) return;
    const rect = triggerRef.current.getBoundingClientRect();
    const margin = 8;
    const width = flyNodeRef.current.offsetWidth || 320;
    const top = rect.bottom + margin;
    let left = rect.right - width; //    
    const viewportWidth = window.innerWidth;
    left = Math.min(Math.max(left, margin), viewportWidth - width - margin);
    setFlyStyle((prev) => ({ ...prev, top, left }));
  };

  useEffect(() => {
    if (!ENABLE_FLY_ALERT) return; //       
    if (!flyVisible) return;
    updateFlyPosition();
    const handler = () => updateFlyPosition();
    window.addEventListener('resize', handler);
    window.addEventListener('scroll', handler, true);
    return () => {
      window.removeEventListener('resize', handler);
      window.removeEventListener('scroll', handler, true);
    };
  }, [flyVisible]);

  const submitQueueRequest = async () => {
    if (!user?.uid) {
      alert('    .');
      return;
    }
    const reason = queueReason.trim();
    if (!reason) {
      alert('    .');
      return;
    }
    try {
      setQueueSubmitting(true);
      setQueueMessage(null);
      const name = (profile?.fullName && profile.fullName.trim())
        ? profile.fullName.trim()
        : (user.email ? user.email.split('@')[0] : '');
      const phone = profile?.phone || null;
      await addDoc(collection(db, 'waiting_list'), {
        userId: user.uid,
        name,
        phone,
        reason,
        status: 'pending',
        createdAt: serverTimestamp(),
      });
      setQueueMessage('       ');
      setQueueReason('');
      //      
      setTimeout(() => setQueueOpen(false), 900);
    } catch (e) {
      console.error('    :', e);
      alert('    .   .');
    } finally {
      setQueueSubmitting(false);
    }
  };

  //          
  useEffect(() => {
    if (!user?.uid) {
      setQueueLatestStatus('none');
      return;
    }
    //        
    const checkQueueStatus = async () => {
      try {
        const q = query(
          collection(db, 'waiting_list'),
          where('userId', '==', user.uid)
        );
        const snapshot = await getDocs(q);
        
        const docs = snapshot.docs
          .map((d) => ({ id: d.id, ...d.data() }))
          .sort((a: any, b: any) => {
            const ta = a?.createdAt?.toMillis?.() ?? a?.createdAt?.seconds * 1000 ?? 0;
            const tb = b?.createdAt?.toMillis?.() ?? b?.createdAt?.seconds * 1000 ?? 0;
            return tb - ta; // 
          });

        if (docs.length === 0) {
          setQueueLatestStatus('none');
          return;
        }

        const latest = docs[0] as any;
        const contacted = latest?.contacted === true || latest?.status === 'contacted';
        setQueueLatestStatus(contacted ? 'contacted' : 'pending');
      } catch (e) {
        console.warn('    waiting_list:', e);
        setQueueLatestStatus('none');
      }
    };

    checkQueueStatus();
  }, [user?.uid]);

  if (!canShowNotifications) {
    return null;
  }

  return (
    <div className={`relative inline-flex items-center gap-2 ${className || ''}`} ref={triggerRef}>
      {/*     */}
      <DropdownMenu onOpenChange={(open) => { if (open && unreadCount > 0) markAllAsRead(); }}>
        <DropdownMenuTrigger asChild>
          <Button
            variant="ghost"
            className="relative h-8 w-8 rounded-full p-0 hover:bg-transparent focus:ring-1 focus:ring-primary focus:ring-offset-1 transition-all"
            title=""
          >
            <div className="relative h-8 w-8 rounded-full bg-primary/10 flex items-center justify-center border border-border shadow-sm hover:shadow-md">
              <Bell className="h-4 w-4 text-primary" />
              {unreadCount > 0 && (
                <span className="absolute -top-1 -right-1 min-w-[18px] h-[18px] px-1 rounded-full bg-red-500 text-white text-[10px] font-bold flex items-center justify-center shadow">
                  {unreadCount}
                </span>
              )}
            </div>
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent className="w-80" align="end" forceMount>
          <DropdownMenuLabel className="flex items-center justify-between">
            <span></span>
            {notifications.length > 0 && (
              <Button variant="outline" size="sm" className="h-7 text-xs" onClick={markAllAsRead}>
                <CheckCircle className="h-3 w-3 mr-1" />
                  
              </Button>
            )}
          </DropdownMenuLabel>
          <DropdownMenuSeparator />

          {notifications.length === 0 ? (
            <div className="p-3 text-sm text-muted-foreground">   </div>
          ) : (
            <div className="max-h-64 overflow-y-auto pr-1">
              {notifications.map((n) => {
                const isUnread = n.id ? !readIds.has(n.id) : false;
                return (
                  <div key={n.id} className={`px-2 py-2 rounded-md ${isUnread ? 'bg-primary/5' : ''}`}>
                    <div className="flex items-start gap-2">
                      <Bell className={`h-4 w-4 ${isUnread ? 'text-primary' : 'text-muted-foreground'}`} />
                      <div className="flex-1">
                        <div className="text-sm font-medium">{n.title || ''}</div>
                        <div className="text-xs text-muted-foreground whitespace-pre-line">{n.message}</div>
                        {n.linkUrl && (
                          <Button variant="link" className="px-0 h-6 text-xs" onClick={() => openLink(n.linkUrl, n.id)}>
                              <ExternalLink className="h-3 w-3 ml-1" />
                          </Button>
                        )}
                      </div>
                      {isUnread ? (
                        <Button variant="ghost" size="icon" className="h-6 w-6" onClick={() => markAsRead(n.id)} title=" ">
                          <CheckCircle className="h-4 w-4 text-primary" />
                        </Button>
                      ) : (
                        <Button variant="ghost" size="icon" className="h-6 w-6" disabled title="">
                          <CheckCircle className="h-4 w-4 text-muted-foreground" />
                        </Button>
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </DropdownMenuContent>
      </DropdownMenu>

      {/*      */}
      <Dialog open={queueOpen} onOpenChange={setQueueOpen}>
        <DialogTrigger asChild>
          <Button
            variant="ghost"
            size="icon"
            className="h-8 w-8 rounded-full hover:bg-transparent focus:ring-1 focus:ring-primary/60 focus:ring-offset-1"
            title=" "
          >
            <Clock className={`h-4 w-4 ${queueLatestStatus === 'pending' ? 'text-red-600' : queueLatestStatus === 'contacted' ? 'text-green-600' : 'text-muted-foreground'}`} />
          </Button>
        </DialogTrigger>
        <DialogContent className="max-w-md border border-purple-200/50 shadow-xl bg-gradient-to-br from-purple-50 to-blue-50">
          <DialogHeader>
            <DialogTitle> </DialogTitle>
            <DialogDescription>
                        .
            </DialogDescription>
          </DialogHeader>
          <div className="grid gap-3">
            <label className="text-sm font-medium text-gray-700"> </label>
            <Textarea
              value={queueReason}
              onChange={(e) => setQueueReason(e.target.value)}
              placeholder="    ..."
              className="min-h-[120px] text-right"
            />
            {queueMessage && (
              <div className="text-sm text-green-700 bg-green-50 border border-green-200 rounded-md p-2">
                {queueMessage}
              </div>
            )}
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setQueueOpen(false)} disabled={queueSubmitting}>
              
            </Button>
            <Button onClick={submitQueueRequest} disabled={queueSubmitting} className="bg-gradient-to-r from-green-600 to-emerald-600 text-white">
              {queueSubmitting ? ' ...' : ''}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      {/*          */}
      {ENABLE_FLY_ALERT && flyAlert && createPortal(
        <div ref={flyNodeRef} style={flyStyle} className={`pointer-events-auto transition-all duration-300 ${flyVisible ? 'opacity-100 translate-y-0' : 'opacity-0 -translate-y-3'}`}>
          <div className="bg-white rounded-xl shadow-2xl border border-gray-200 p-4 w-80 max-w-[calc(100vw-16px)] flex items-start gap-3">
            <div className="w-8 h-8 rounded-full bg-primary/10 flex items-center justify-center">
              <Bell className="w-4 h-4 text-primary" />
            </div>
            <div className="flex-1">
              <div className="text-sm font-semibold">{flyAlert.title || ' '}</div>
              <div className="text-xs text-muted-foreground whitespace-pre-line">{flyAlert.message}</div>
              {flyAlert.linkUrl && (
                <Button variant="link" className="px-0 h-6 text-xs" onClick={() => openLink(flyAlert.linkUrl, flyAlert.id)}>
                   
                </Button>
              )}
            </div>
          </div>
        </div>,
        document.body
      )}
    </div>
  );
}
