import React, { useEffect, useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { useShift } from '@/contexts/ShiftContext';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { db } from '@/firebase';
import { doc, getDoc } from 'firebase/firestore';

interface Props {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

type CashierUser = { name: string; username: string; password: string };

const ShiftProfileDialog: React.FC<Props> = ({ open, onOpenChange }) => {
  const { shiftUser, endShift, setShiftUser } = useShift();
  const [enteredPassword, setEnteredPassword] = useState('');
  const [error, setError] = useState('');
  const [users, setUsers] = useState<CashierUser[]>([]);
  const { user } = useFirebaseAuth();

  useEffect(() => {
    (async () => {
      //             fallback 
      try {
        if (user?.uid) {
          const ref = doc(db, 'profiles', user.uid, 'cashierUsers', 'list');
          const snap = await getDoc(ref);
          if (snap.exists()) {
            const data = snap.data() as any;
            const cloudUsers: CashierUser[] = Array.isArray(data?.users) ? data.users : [];
            if (cloudUsers && cloudUsers.length > 0) {
              setUsers(cloudUsers);
              return;
            }
          }
        }
      } catch {}
      try {
        const key = `cashierUsers_${user?.uid || 'default'}`;
        let saved = localStorage.getItem(key);
        //             
        if (!saved) {
          const legacy = localStorage.getItem('cashierUsers');
          if (legacy) saved = legacy;
        }
        setUsers(saved ? JSON.parse(saved) : []);
      } catch {
        setUsers([]);
      }
    })();
  }, [open, user?.uid]);

  //       
  useEffect(() => {
    if (!open || !user?.uid) return;
    
    const fetchCashierUsers = async () => {
      try {
        const ref = doc(db, 'profiles', user.uid, 'cashierUsers', 'list');
        const snap = await getDoc(ref);
        if (snap.exists()) {
          const data = snap.data() as any;
          const cloudUsers: CashierUser[] = Array.isArray(data?.users) ? data.users : [];
          if (cloudUsers && cloudUsers.length > 0) {
            setUsers(cloudUsers);
          } else {
             // Fallback to local storage
             loadFromLocal();
          }
        } else {
          loadFromLocal();
        }
      } catch (err) {
        console.warn('ShiftProfileDialog cashierUsers fetch error:', err);
        loadFromLocal();
      }
    };

    const loadFromLocal = () => {
      try {
        const key = `cashierUsers_${user?.uid || 'default'}`;
        let saved = localStorage.getItem(key);
        if (!saved) {
          const legacy = localStorage.getItem('cashierUsers');
          if (legacy) saved = legacy;
        }
        setUsers(saved ? JSON.parse(saved) : []);
      } catch {
        setUsers([]);
      }
    };

    fetchCashierUsers();
  }, [open, user?.uid]);

  const handleEndShift = async () => {
    setError('');
    if (!shiftUser) return;
    const match = users.find(u => u.username === shiftUser.username);
    if (!match) {
      setError('     ');
      return;
    }
    if (enteredPassword.trim() !== match.password) {
      setError('   ');
      return;
    }
    //       
    endShift();
    setShiftUser(null);
    setEnteredPassword('');
    onOpenChange(false);
  };

  return (
    <Dialog open={open} onOpenChange={(o) => { setEnteredPassword(''); setError(''); onOpenChange(o); }}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle> </DialogTitle>
        </DialogHeader>

        {shiftUser ? (
          <div className="space-y-3">
            <div className="rounded-lg border p-3 bg-gradient-to-r from-blue-50 to-indigo-50">
              <div className="text-sm font-semibold text-gray-800">{shiftUser.name}</div>
              <div className="text-xs text-gray-600">{shiftUser.username}</div>
            </div>
            <div className="space-y-2">
              <Label className="block">    </Label>
              <Input
                type="password"
                value={enteredPassword}
                onChange={(e) => setEnteredPassword(e.target.value)}
                placeholder="  "
                dir="ltr"
              />
              {error && (
                <div className="text-red-600 text-xs">{error}</div>
              )}
            </div>
          </div>
        ) : (
          <div className="text-sm text-gray-600">     </div>
        )}

        <DialogFooter className="flex justify-between">
          <Button variant="outline" onClick={() => onOpenChange(false)}></Button>
          <Button onClick={handleEndShift} disabled={!shiftUser}> </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};

export default ShiftProfileDialog;