import React, { useEffect, useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Lock, Eye, EyeOff, AlertCircle, Plus, Minus } from 'lucide-react';
import { MasterPinService } from '@/utils/masterPinService';

interface AddToWalletDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onSuccess: (addedAmount: number) => void;
  title: string;
  description: string;
  currentBalance: number;
  balanceLabel: string;
  userId?: string;
}

export function AddToWalletDialog({ 
  open, 
  onOpenChange, 
  onSuccess, 
  title, 
  description,
  currentBalance,
  balanceLabel,
  userId
}: AddToWalletDialogProps) {
  const [pin, setPin] = useState('');
  const [amount, setAmount] = useState('');
  const [operationType, setOperationType] = useState<'add' | 'subtract'>('add');
  const [showPin, setShowPin] = useState(false);
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const [hasMasterPin, setHasMasterPin] = useState(false);

  useEffect(() => {
    let mounted = true;
    (async () => {
      const has = await MasterPinService.hasMasterPin(userId);
      if (mounted) setHasMasterPin(has);
    })();
    return () => { mounted = false; };
  }, [userId, open]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    try {
      //    
      const amountValue = parseFloat(amount);
      if (isNaN(amountValue) || amountValue <= 0) {
        setError('      ');
        setIsLoading(false);
        return;
      }

      //         
      if (operationType === 'subtract' && amountValue > currentBalance) {
        setError('       ');
        setIsLoading(false);
        return;
      }

      if (hasMasterPin) {
        //     
        const ok = await MasterPinService.verifyMasterPin(pin, userId);
        if (ok) {
          const finalAmount = operationType === 'add' ? amountValue : -amountValue;
          
          //   
          handleClose();
          
          //   
          onSuccess(finalAmount);
        } else {
          setError('   ');
        }
      } else {
        setError('     .      ');
      }
    } catch (error) {
      setError('      ');
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setPin('');
    setAmount('');
    setOperationType('add');
    setError('');
    onOpenChange(false);
  };

  const getNewBalance = () => {
    const amountValue = parseFloat(amount) || 0;
    if (operationType === 'add') {
      return currentBalance + amountValue;
    } else {
      return currentBalance - amountValue;
    }
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-md" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-right">
            {operationType === 'add' ? (
              <Plus className="h-5 w-5 text-green-600" />
            ) : (
              <Minus className="h-5 w-5 text-red-600" />
            )}
            {title}
          </DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="text-sm text-muted-foreground text-right">
            {description}
          </div>

          {/*    */}
          <div className="bg-gray-50 p-3 rounded-lg">
            <Label className="text-sm font-medium text-gray-700">
              {balanceLabel} 
            </Label>
            <div className="text-lg font-bold text-gray-900 mt-1">
              {currentBalance.toLocaleString()} 
            </div>
          </div>

          {/*    */}
          <div className="space-y-2">
            <Label className="text-right"> </Label>
            <div className="flex gap-2">
              <Button
                type="button"
                variant={operationType === 'add' ? 'default' : 'outline'}
                onClick={() => setOperationType('add')}
                className="flex-1 flex items-center gap-2"
              >
                <Plus className="h-4 w-4" />
                
              </Button>
              <Button
                type="button"
                variant={operationType === 'subtract' ? 'default' : 'outline'}
                onClick={() => setOperationType('subtract')}
                className="flex-1 flex items-center gap-2"
              >
                <Minus className="h-4 w-4" />
                
              </Button>
            </div>
          </div>

          {/*   */}
          <div className="space-y-2">
            <Label htmlFor="amount" className="text-right">
                {operationType === 'add' ? '' : ''}
            </Label>
            <Input
              id="amount"
              type="number"
              step="0.01"
              min="0.01"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              placeholder={`   ${operationType === 'add' ? '' : ''}`}
              className="text-right"
              required
            />
          </div>

          {/*     */}
          {amount && !isNaN(parseFloat(amount)) && (
            <div className={`p-3 rounded-lg ${operationType === 'add' ? 'bg-green-50 border border-green-200' : 'bg-red-50 border border-red-200'}`}>
              <Label className="text-sm font-medium text-gray-700">
                  
              </Label>
              <div className={`text-lg font-bold mt-1 ${operationType === 'add' ? 'text-green-700' : 'text-red-700'}`}>
                {getNewBalance().toLocaleString()} 
              </div>
            </div>
          )}

          {/*    */}
          <div className="space-y-2">
            <Label htmlFor="pin" className="text-right">
               
            </Label>
            <div className="relative">
              <Input
                id="pin"
                type={showPin ? 'text' : 'password'}
                autoComplete="off"
                value={pin}
                onChange={(e) => setPin(e.target.value)}
                placeholder="  "
                className="text-right pr-10"
                required
              />
              <Button
                type="button"
                variant="ghost"
                size="sm"
                className="absolute left-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                onClick={() => setShowPin(!showPin)}
              >
                {showPin ? (
                  <EyeOff className="h-4 w-4" />
                ) : (
                  <Eye className="h-4 w-4" />
                )}
              </Button>
            </div>
          </div>

          {error && (
            <div className="flex items-center gap-2 text-sm text-red-600 bg-red-50 p-2 rounded">
              <AlertCircle className="h-4 w-4" />
              {error}
            </div>
          )}

          <div className="flex gap-2 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={handleClose}
              className="flex-1"
            >
              
            </Button>
            <Button
              type="submit"
              disabled={isLoading || !pin || !amount}
              className={`flex-1 ${operationType === 'add' ? 'bg-green-600 hover:bg-green-700' : 'bg-red-600 hover:bg-red-700'}`}
            >
              {isLoading ? (
                <div className="flex items-center gap-2">
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
                   ...
                </div>
              ) : (
                <>
                  <Lock className="h-4 w-4 mr-2" />
                  {operationType === 'add' ? ' ' : ' '}
                </>
              )}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}