import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Calculator, X } from 'lucide-react';

interface CalculatorDialogProps {
  isOpen: boolean;
  onClose: () => void;
}

export function CalculatorDialog({ isOpen, onClose }: CalculatorDialogProps) {
  const [display, setDisplay] = useState('0');
  const [previousValue, setPreviousValue] = useState<number | null>(null);
  const [operation, setOperation] = useState<string | null>(null);
  const [waitingForOperand, setWaitingForOperand] = useState(false);

  const inputNumber = (num: string) => {
    if (waitingForOperand) {
      setDisplay(num);
      setWaitingForOperand(false);
    } else {
      setDisplay(display === '0' ? num : display + num);
    }
  };

  const inputOperation = (nextOperation: string) => {
    const inputValue = parseFloat(display);

    if (previousValue === null) {
      setPreviousValue(inputValue);
    } else if (operation) {
      const currentValue = previousValue || 0;
      const newValue = calculate(currentValue, inputValue, operation);

      setDisplay(String(newValue));
      setPreviousValue(newValue);
    }

    setWaitingForOperand(true);
    setOperation(nextOperation);
  };

  const calculate = (firstValue: number, secondValue: number, operation: string): number => {
    switch (operation) {
      case '+':
        return firstValue + secondValue;
      case '-':
        return firstValue - secondValue;
      case '':
        return firstValue * secondValue;
      case '':
        if (secondValue === 0) {
          return 0; //    Infinity    
        }
        return firstValue / secondValue;
      case '=':
        return secondValue;
      default:
        return secondValue;
    }
  };

  const performCalculation = () => {
    const inputValue = parseFloat(display);

    if (previousValue !== null && operation) {
      const newValue = calculate(previousValue, inputValue, operation);
      setDisplay(String(newValue));
      setPreviousValue(null);
      setOperation(null);
      setWaitingForOperand(true);
    }
  };

  const clearAll = () => {
    setDisplay('0');
    setPreviousValue(null);
    setOperation(null);
    setWaitingForOperand(false);
  };

  const clearEntry = () => {
    setDisplay('0');
  };

  const inputDecimal = () => {
    if (waitingForOperand) {
      setDisplay('0.');
      setWaitingForOperand(false);
    } else if (display.indexOf('.') === -1) {
      setDisplay(display + '.');
    }
  };

  const toggleSign = () => {
    if (display !== '0') {
      setDisplay(display.charAt(0) === '-' ? display.slice(1) : '-' + display);
    }
  };

  const percentage = () => {
    const value = parseFloat(display);
    setDisplay(String(value / 100));
  };

  //   
  const handleKeyPress = (event: KeyboardEvent) => {
    const key = event.key;
    
    //    
    event.preventDefault();
    
    //  0-9
    if (key >= '0' && key <= '9') {
      inputNumber(key);
    }
    //  
    else if (key === '+') {
      inputOperation('+');
    }
    else if (key === '-') {
      inputOperation('-');
    }
    else if (key === '*') {
      inputOperation('');
    }
    else if (key === '/') {
      inputOperation('');
    }
    //  
    else if (key === 'Enter' || key === '=') {
      performCalculation();
    }
    else if (key === 'Escape' || key === 'c' || key === 'C') {
      clearAll();
    }
    else if (key === 'Backspace') {
      clearEntry();
    }
    else if (key === '.') {
      inputDecimal();
    }
    else if (key === '%') {
      percentage();
    }
  };

  //   event listener  /  
  useEffect(() => {
    if (isOpen) {
      document.addEventListener('keydown', handleKeyPress);
    } else {
      document.removeEventListener('keydown', handleKeyPress);
    }

    //    
    return () => {
      document.removeEventListener('keydown', handleKeyPress);
    };
  }, [isOpen, display, previousValue, operation, waitingForOperand]);

  return (
    <Dialog open={isOpen} onOpenChange={(open) => !open && onClose()}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-right">
            <Calculator className="h-5 w-5" />
             
          </DialogTitle>
        </DialogHeader>
        
        <div className="space-y-4">
          {/*   */}
          <div className="bg-gray-900 text-white p-4 rounded-lg text-right">
            <div className="text-3xl font-mono font-bold min-h-[3rem] flex items-center justify-end overflow-hidden">
              {display}
            </div>
          </div>

          {/*    */}
          <div className="grid grid-cols-4 gap-2">
            {/*   */}
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-gray-100 hover:bg-gray-200"
              onClick={clearAll}
            >
              AC
            </Button>
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-gray-100 hover:bg-gray-200"
              onClick={clearEntry}
            >
              CE
            </Button>
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-gray-100 hover:bg-gray-200"
              onClick={percentage}
            >
              %
            </Button>
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-orange-500 text-white hover:bg-orange-600"
              onClick={() => inputOperation('')}
            >
              
            </Button>

            {/*   */}
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('7')}
            >
              7
            </Button>
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('8')}
            >
              8
            </Button>
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('9')}
            >
              9
            </Button>
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-orange-500 text-white hover:bg-orange-600"
              onClick={() => inputOperation('')}
            >
              
            </Button>

            {/*   */}
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('4')}
            >
              4
            </Button>
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('5')}
            >
              5
            </Button>
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('6')}
            >
              6
            </Button>
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-orange-500 text-white hover:bg-orange-600"
              onClick={() => inputOperation('-')}
            >
              -
            </Button>

            {/*   */}
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('1')}
            >
              1
            </Button>
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('2')}
            >
              2
            </Button>
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={() => inputNumber('3')}
            >
              3
            </Button>
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-orange-500 text-white hover:bg-orange-600"
              onClick={() => inputOperation('+')}
            >
              +
            </Button>

            {/*   */}
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50 col-span-2"
              onClick={() => inputNumber('0')}
            >
              0
            </Button>
            <Button
              variant="outline"
              className="h-12 text-lg font-semibold bg-white hover:bg-gray-50"
              onClick={inputDecimal}
            >
              .
            </Button>
            <Button
              variant="outline"
              className="h-12 text-sm font-semibold bg-orange-500 text-white hover:bg-orange-600"
              onClick={performCalculation}
            >
              =
            </Button>
          </div>

          {/*     */}
          <Button
            variant="outline"
            className="w-full h-10 text-sm font-semibold bg-gray-100 hover:bg-gray-200"
            onClick={toggleSign}
          >
            +/-
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}

export default CalculatorDialog;