import React, { useEffect, useMemo, useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Receipt, Calendar, Clock, ChevronDown, Trash2, RefreshCcw } from 'lucide-react';
import FawryMachineIcon from '@/components/icons/FawryMachineIcon';
import { useToast } from '@/hooks/use-toast';
import { useShift } from '@/contexts/ShiftContext';
import { AlertDialog, AlertDialogContent, AlertDialogHeader, AlertDialogTitle, AlertDialogDescription, AlertDialogAction, AlertDialogCancel } from '@/components/ui/alert-dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { db } from '@/firebase';
import { addDoc, collection, doc, setDoc, serverTimestamp, deleteDoc, getDocs, orderBy, query } from 'firebase/firestore';
import { useShop } from '@/hooks/useShop';

interface MachineDailyDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

interface MachineReceipt {
  id: string;
  openingBase: number; //   
  openingAir: number;  //   
  deliveryBase: number; //    
  deliveryAir: number;  //    
  netResult: number;    //  : () - ()
  createdAt: string;    //  
  cashierName?: string; //    
  requiredDrawerNoProfit?: number; //     
}

interface TransferReceipt {
  id: string;
  wholesalePrice: number; //    
  retailPrice: number;    //    
  profit: number;         //  :  - 
  createdAt: string;
  cashierName?: string;
  deductFrom?: 'base' | 'air'; //  :   
}

const formatNumber = (n: number) => {
  try { return n.toLocaleString('en-US', { maximumFractionDigits: 2 }); } catch { return String(n); }
};

const formatDateTime = (iso?: string) => {
  try {
    const d = iso ? new Date(iso) : new Date();
    return d.toLocaleString('en-GB', {
      year: 'numeric', month: '2-digit', day: '2-digit',
      hour: '2-digit', minute: '2-digit', second: '2-digit',
      hour12: false,
    });
  } catch {
    return new Date().toISOString();
  }
};

//         
const STORAGE_KEY_OPENING = 'machine_daily_opening_values';
const STORAGE_KEY_OPENING_SNAPSHOT = 'machine_daily_opening_snapshot';

export default function MachineDailyDialog({ open, onOpenChange }: MachineDailyDialogProps) {
  const { toast } = useToast();
  const { shiftUser } = useShift();
  const { shopId, shopName } = useShop();
  const getFallbackShopId = () => {
    try {
      const keys = Object.keys(localStorage || {}).filter(k => k.startsWith('selectedShopId_'));
      for (const k of keys) {
        const v = localStorage.getItem(k);
        if (v) return v;
      }
      return null;
    } catch {
      return null;
    }
  };

  //           
  useEffect(() => {
    try {
      if (open) {
        const prev = document.body.style.overflow;
        document.body.setAttribute('data-prev-overflow', prev);
        document.body.style.overflow = 'hidden';
      } else {
        const prev = document.body.getAttribute('data-prev-overflow') || '';
        document.body.style.overflow = prev;
        document.body.removeAttribute('data-prev-overflow');
      }
    } catch {}
    return () => {
      try {
        const prev = document.body.getAttribute('data-prev-overflow') || '';
        document.body.style.overflow = prev;
        document.body.removeAttribute('data-prev-overflow');
      } catch {}
    };
  }, [open]);

  //   
  const [openingBase, setOpeningBase] = useState<string>('');
  const [openingAir, setOpeningAir] = useState<string>('');
  const [drawerCash, setDrawerCash] = useState<string>('');

  //    
  const [savedOpeningBase, setSavedOpeningBase] = useState<number | null>(null);
  const [savedOpeningAir, setSavedOpeningAir] = useState<number | null>(null);
  const [savedDrawerCash, setSavedDrawerCash] = useState<number | null>(null);
  const [openingSnapshot, setOpeningSnapshot] = useState<{ openingBase: number; openingAir: number; drawerCash: number } | null>(null);

  // /   
  const [isOpeningOpen, setIsOpeningOpen] = useState(false);

  //   
  const [isDeliverySectionOpen, setIsDeliverySectionOpen] = useState(false);
  const [deliveryBase, setDeliveryBase] = useState<string>('');
  const [deliveryAir, setDeliveryAir] = useState<string>('');
  const [netResult, setNetResult] = useState<number | null>(null);

  //    (  )
  const [receipts, setReceipts] = useState<MachineReceipt[]>([]);

  //    (  )
  const [transferReceipts, setTransferReceipts] = useState<TransferReceipt[]>([]);

  //     /
  const [transferDialogOpen, setTransferDialogOpen] = useState(false);
  const [wholesalePrice, setWholesalePrice] = useState<string>('');
  const [retailPrice, setRetailPrice] = useState<string>('');

  //      
  const [deductDialogOpen, setDeductDialogOpen] = useState(false);
  const [pendingTransfer, setPendingTransfer] = useState<{
    wholesalePrice: number;
    retailPrice: number;
    profit: number;
  } | null>(null);

  //   (/)
  const [machines, setMachines] = useState<{ id: string; name: string }[]>([]);
  const [selectedMachineId, setSelectedMachineId] = useState<string | null>(null);
  const [newMachineName, setNewMachineName] = useState<string>('');
  const [transferServerHydrated, setTransferServerHydrated] = useState(false);
  const [deliveriesServerHydrated, setDeliveriesServerHydrated] = useState(false);

  // Hydrate per-machine opening values when dialog opens or machine changes
  useEffect(() => {
    if (!open) return;
    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      const savedOpening = localStorage.getItem(keyOpening);
      if (savedOpening) {
        const obj = JSON.parse(savedOpening);
        if (typeof obj?.openingBase === 'number') setSavedOpeningBase(obj.openingBase);
        if (typeof obj?.openingAir === 'number') setSavedOpeningAir(obj.openingAir);
        if (typeof obj?.drawerCash === 'number') setSavedDrawerCash(obj.drawerCash);
      } else {
        setSavedOpeningBase(null);
        setSavedOpeningAir(null);
        setSavedDrawerCash(null);
      }

      const savedSnap = localStorage.getItem(keySnap);
      if (savedSnap) {
        const snap = JSON.parse(savedSnap);
        if (typeof snap?.openingBase === 'number' && typeof snap?.openingAir === 'number') {
          setOpeningSnapshot({ openingBase: snap.openingBase, openingAir: snap.openingAir, drawerCash: snap.drawerCash || 0 });
        } else {
          setOpeningSnapshot(null);
        }
      } else {
        setOpeningSnapshot(null);
      }
    } catch {}
  }, [open, selectedMachineId]);

  //      
  useEffect(() => {
    (async () => {
      try {
        const sid = shopId || getFallbackShopId();
        if (!open || !sid) return;
        const { getDocs } = await import('firebase/firestore');
        const col = collection(db, 'shops', sid, 'machines');
        const snap = await getDocs(query(col));
        const list = snap.docs.map(d => ({ id: d.id, name: String(d.data().name || '') }));
        setMachines(list);
        try {
          const savedId = localStorage.getItem(`machineDaily_selected_${sid}`);
          const exists = savedId && list.some(m => m.id === savedId);
          if (exists) {
            setSelectedMachineId(savedId as string);
          } else if (!selectedMachineId && list.length > 0) {
            setSelectedMachineId(list[0].id);
            localStorage.setItem(`machineDaily_selected_${sid}`, list[0].id);
          }
        } catch {}
      } catch (err) {
        console.error('Load machines error:', err);
      }
    })();
  }, [open, shopId]);

  //       

  const cashierName = useMemo(() => {
    return shiftUser?.displayName || shiftUser?.name || shiftUser?.username || undefined;
  }, [shiftUser]);

  const totalTransferProfit = useMemo(() => transferReceipts.reduce((sum, t) => sum + (t.profit || 0), 0), [transferReceipts]);
  const totalWholesaleTransferred = useMemo(() => transferReceipts.reduce((sum, t) => sum + (t.wholesalePrice || 0), 0), [transferReceipts]);

  const handleClearTransferReceipts = async () => {
    try {
      const sid = shopId || getFallbackShopId();
      if (sid && selectedMachineId) {
        const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'transfers');
        const snap = await getDocs(query(col));
        await Promise.all(snap.docs.map(d => deleteDoc(d.ref)));
        try { localStorage.setItem(`machineDaily_transfers_${sid}_${selectedMachineId}`, JSON.stringify([])); } catch {}
      }
    } catch {}
    setTransferReceipts([]);
    toast({ title: '  ', description: '    .' });
  };

  const handleClearMachineReceipts = async () => {
    try {
      const sid = shopId || getFallbackShopId();
      if (sid && selectedMachineId) {
        const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'deliveries');
        const snap = await getDocs(query(col));
        await Promise.all(snap.docs.map(d => deleteDoc(d.ref)));
        try { localStorage.setItem(`machineDaily_deliveries_${sid}_${selectedMachineId}`, JSON.stringify([])); } catch {}
      }
    } catch {}
    setReceipts([]);
    toast({ title: '  ', description: '    .' });
  };

  const handleDeleteMachineReceipt = (id: string) => {
    setReceipts(prev => prev.filter(r => r.id !== id));
    toast({ title: '  ', description: '    .' });
  };

  //     
  const handleResetOpening = () => {
    setSavedOpeningBase(null);
    setSavedOpeningAir(null);
    setSavedDrawerCash(null);
    setOpeningSnapshot(null);
  
    setOpeningBase('');
    setOpeningAir('');
    setDrawerCash('');
  
    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      localStorage.removeItem(keyOpening);
      localStorage.removeItem(keySnap);
    } catch {}
  
    toast({ title: ' ', description: '     .' });
  };
  const handleAddOpening = async () => {
    const base = parseFloat(openingBase || '0');
    const air = parseFloat(openingAir || '0');
    const dc = parseFloat(drawerCash || '0');

    if (isNaN(base) || isNaN(air) || isNaN(dc)) {
      toast({ title: '  ', description: '       .', variant: 'destructive' });
      return;
    }
    if (base < 0 || air < 0 || dc < 0) {
      toast({ title: ' ', description: '    .', variant: 'destructive' });
      return;
    }

    setSavedOpeningBase(base);
    setSavedOpeningAir(air);
    setSavedDrawerCash(dc);
    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      localStorage.setItem(keyOpening, JSON.stringify({ openingBase: base, openingAir: air, drawerCash: dc }));
      localStorage.setItem(keySnap, JSON.stringify({ openingBase: base, openingAir: air, drawerCash: dc }));
      setOpeningSnapshot({ openingBase: base, openingAir: air, drawerCash: dc });
      //   Firestore   
      const sid0 = shopId || getFallbackShopId();
      if (sid0 && selectedMachineId) {
        const ref = doc(db, 'shops', sid0, 'machines', selectedMachineId);
        await setDoc(ref, {
          name: machines.find(m => m.id === selectedMachineId)?.name || '',
          openingBase: base,
          openingAir: air,
          drawerCash: dc,
          cashierName,
          shopName: shopName || null,
          updatedAt: serverTimestamp(),
        }, { merge: true });
      }
    } catch {}

    toast({ title: '  ', description: ` : ${formatNumber(base)} : ${formatNumber(air)}  : ${formatNumber(dc)}` });
  };

  const handleStartDelivery = () => {
    if (savedOpeningBase == null || savedOpeningAir == null) {
      toast({ title: '   ', description: '    ""   .' });
      return;
    }
    setIsDeliverySectionOpen(true);
  };

  const handleDoDelivery = () => {
    const dBase = parseFloat(deliveryBase || '0');
    const dAir = parseFloat(deliveryAir || '0');

    if (isNaN(dBase) || isNaN(dAir)) {
      toast({ title: '  ', description: '     .', variant: 'destructive' });
      return;
    }
    if (dBase < 0 || dAir < 0) {
      toast({ title: ' ', description: '    .', variant: 'destructive' });
      return;
    }

    const openingBaseForReceipt = openingSnapshot?.openingBase ?? (savedOpeningBase || 0);
    const openingAirForReceipt = openingSnapshot?.openingAir ?? (savedOpeningAir || 0);

    const openingSum = (openingBaseForReceipt) + (openingAirForReceipt);
    const deliverySum = dBase + dAir;
    const result = deliverySum - openingSum;

    setNetResult(result);

    const receipt: MachineReceipt = {
      id: `${Date.now()}`,
      openingBase: openingBaseForReceipt,
      openingAir: openingAirForReceipt,
      deliveryBase: dBase,
      deliveryAir: dAir,
      netResult: result,
      createdAt: new Date().toISOString(),
      cashierName,
      requiredDrawerNoProfit: result < 0 ? Math.round(-result * 100) / 100 : 0,
    };

    //    Firestore   
    void (async () => {
      try {
        const sid1 = shopId || getFallbackShopId();
        if (sid1 && selectedMachineId) {
          await addDoc(collection(db, 'shops', sid1, 'machines', selectedMachineId, 'deliveries'), {
            cashierName,
            openingBase: openingBaseForReceipt,
            openingAir: openingAirForReceipt,
            deliveryBase: dBase,
            deliveryAir: dAir,
            netResult: result,
            createdAt: serverTimestamp(),
          });
          await setDoc(doc(db, 'shops', sid1, 'machines', selectedMachineId), {
            lastDeliveryAt: serverTimestamp(),
            updatedAt: serverTimestamp(),
          }, { merge: true });
        }
      } catch (err) {
        console.error('Persist delivery error:', err);
      }
    })();

    setReceipts((prev) => [receipt, ...prev]);
    try {
      const sid = shopId || getFallbackShopId();
      const key = sid && selectedMachineId ? `machineDaily_deliveries_${sid}_${selectedMachineId}` : `machineDaily_deliveries_${selectedMachineId}`;
      const current = localStorage.getItem(key);
      const arr = current ? JSON.parse(current) : [];
      const next = [receipt, ...(Array.isArray(arr) ? arr : [])];
      localStorage.setItem(key, JSON.stringify(next));
    } catch {}

    toast({ title: '   ', description: ` : ${formatNumber(result)}` });
  };

  const handleTransferConfirm = () => {
    const w = parseFloat(wholesalePrice || '0');
    const r = parseFloat(retailPrice || '0');
    if (!Number.isFinite(w) || !Number.isFinite(r)) {
      toast({ title: '  ', description: '       .', variant: 'destructive' });
      return;
    }
    if (w < 0 || r < 0) {
      toast({ title: ' ', description: '    .', variant: 'destructive' });
      return;
    }

    if (savedOpeningBase == null || savedOpeningAir == null) {
      toast({ title: '   ', description: '        "".' });
      return;
    }

    const profit = Math.round((r - w) * 100) / 100;
    setPendingTransfer({ wholesalePrice: w, retailPrice: r, profit });
    setDeductDialogOpen(true);
  };

  const finalizeTransfer = async (from: 'base' | 'air') => {
    if (!pendingTransfer) return;
    const receipt: TransferReceipt = {
      id: `transfer_${Date.now()}`,
      wholesalePrice: pendingTransfer.wholesalePrice,
      retailPrice: pendingTransfer.retailPrice,
      profit: pendingTransfer.profit,
      createdAt: new Date().toISOString(),
      cashierName,
      deductFrom: from,
    };

    //     
    const stockDeductAmount = pendingTransfer.wholesalePrice; //     
    const prevBase = savedOpeningBase ?? 0;
    const prevAir = savedOpeningAir ?? 0;
    const prevDrawer = savedDrawerCash ?? 0;

    //      
    const availableStock = from === 'base' ? prevBase : prevAir;
    if (stockDeductAmount > availableStock) {
      toast({ title: '  ', description: '      .', variant: 'destructive' });
      return;
    }

    const newBase = from === 'base' ? Math.round((prevBase - stockDeductAmount) * 100) / 100 : prevBase;
    const newAir = from === 'air' ? Math.round((prevAir - stockDeductAmount) * 100) / 100 : prevAir;
    const totalDrawerAdded = pendingTransfer.retailPrice;
    const newDrawer = Math.round((prevDrawer + totalDrawerAdded) * 100) / 100;

    setSavedOpeningBase(newBase);
    setSavedOpeningAir(newAir);
    setSavedDrawerCash(newDrawer);
    try {
      const keyOpening = selectedMachineId ? `${STORAGE_KEY_OPENING}_${selectedMachineId}` : STORAGE_KEY_OPENING;
      const keySnap = selectedMachineId ? `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}` : STORAGE_KEY_OPENING_SNAPSHOT;
      localStorage.setItem(keyOpening, JSON.stringify({ openingBase: newBase, openingAir: newAir, drawerCash: newDrawer }));
      localStorage.setItem(keySnap, JSON.stringify({ openingBase: newBase, openingAir: newAir, drawerCash: newDrawer }));
      //    Firestore +   
      const sid2 = shopId || getFallbackShopId();
      if (sid2 && selectedMachineId) {
        const machineRef = doc(db, 'shops', sid2, 'machines', selectedMachineId);
        await setDoc(machineRef, {
          openingBase: newBase,
          openingAir: newAir,
          drawerCash: newDrawer,
          cashierName,
          updatedAt: serverTimestamp(),
        }, { merge: true });
        await addDoc(collection(db, 'shops', sid2, 'machines', selectedMachineId, 'transfers'), {
          wholesalePrice: pendingTransfer.wholesalePrice,
          retailPrice: pendingTransfer.retailPrice,
          profit: pendingTransfer.profit,
          deductFrom: from,
          cashierName,
          createdAt: serverTimestamp(),
        });
      }
    } catch {}

    setTransferReceipts(prev => [receipt, ...prev]);
    try {
      const sidKey = shopId || getFallbackShopId();
      const key = sidKey && selectedMachineId ? `machineDaily_transfers_${sidKey}_${selectedMachineId}` : 'machineDaily_transfers';
      const current = localStorage.getItem(key);
      const arr = current ? JSON.parse(current) : [];
      const next = [receipt, ...(Array.isArray(arr) ? arr : [])];
      localStorage.setItem(key, JSON.stringify(next));
    } catch {}
    setTransferDialogOpen(false);
    setWholesalePrice('');
    setRetailPrice('');
    setPendingTransfer(null);
    setDeductDialogOpen(false);
    toast({ title: '  ', description: ` : ${formatNumber(receipt.profit)} |  : -${formatNumber(stockDeductAmount)} (${from === 'base' ? '' : ''}) |  ( ): +${formatNumber(totalDrawerAdded)}` });
  };

  const resetDialogState = () => {
    setOpeningBase('');
    setOpeningAir('');
    setIsDeliverySectionOpen(false);
    setDeliveryBase('');
    setDeliveryAir('');
    setNetResult(null);
  };


  const refreshMachineData = async () => {
    if (!open || !selectedMachineId) return;
    const sid = shopId || getFallbackShopId();
    if (!sid) return;

    // Fetch Deliveries
    try {
      const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'deliveries');
      const snap = await getDocs(query(col));
      const list: MachineReceipt[] = snap.docs.map((d) => {
        const x: any = d.data();
        const created = x?.createdAt?.toDate?.() ? x.createdAt.toDate() : new Date();
        return {
          id: d.id,
          openingBase: Number(x?.openingBase || 0),
          openingAir: Number(x?.openingAir || 0),
          deliveryBase: Number(x?.deliveryBase || 0),
          deliveryAir: Number(x?.deliveryAir || 0),
          netResult: typeof x?.netResult === 'number'
            ? Number(x.netResult)
            : Math.round(((Number(x?.deliveryBase || 0) + Number(x?.deliveryAir || 0)) - (Number(x?.openingBase || 0) + Number(x?.openingAir || 0))) * 100) / 100,
          createdAt: created.toISOString(),
          cashierName: x?.cashierName || undefined,
          requiredDrawerNoProfit: typeof x?.requiredDrawerNoProfit === 'number' ? x.requiredDrawerNoProfit : undefined,
        } as MachineReceipt;
      });
      const sorted = [...list].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
      setReceipts(sorted);
      try { localStorage.setItem(`machineDaily_deliveries_${sid}_${selectedMachineId}`, JSON.stringify(sorted)); } catch {}
    } catch (e) {
      console.error('Error fetching deliveries:', e);
    }

    // Fetch Transfers
    try {
      const col = collection(db, 'shops', sid, 'machines', selectedMachineId, 'transfers');
      const snap = await getDocs(query(col));
      const list: TransferReceipt[] = snap.docs.map((d) => {
        const x: any = d.data();
        const created = x?.createdAt?.toDate?.() ? x.createdAt.toDate() : new Date();
        const ded: any = x?.deductFrom;
        return {
          id: d.id,
          wholesalePrice: Number(x?.wholesalePrice || 0),
          retailPrice: Number(x?.retailPrice || 0),
          profit: Number(x?.profit ?? ((Number(x?.retailPrice || 0) - Number(x?.wholesalePrice || 0)) || 0)),
          createdAt: created.toISOString(),
          cashierName: x?.cashierName || undefined,
          deductFrom: ded === 'base' || ded === 'air' ? ded : undefined,
        } as TransferReceipt;
      });
      const sorted = [...list].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
      setTransferReceipts(sorted);
      try { localStorage.setItem(`machineDaily_transfers_${sid}_${selectedMachineId}`, JSON.stringify(sorted)); } catch {}
    } catch (e) {
      console.error('Error fetching transfers:', e);
    }
    
    toast({ title: ' ', description: '   .' });
  };

  useEffect(() => {
    if (!open || !selectedMachineId) return;
    const sid = shopId || getFallbackShopId();
    // Load from cache first
    try {
      const keyDel = sid ? `machineDaily_deliveries_${sid}_${selectedMachineId}` : `machineDaily_deliveries_${selectedMachineId}`;
      const cachedDel = localStorage.getItem(keyDel);
      if (cachedDel) {
        const parsed = JSON.parse(cachedDel);
        if (Array.isArray(parsed)) setReceipts(parsed);
      }
      
      const keyTrans = sid ? `machineDaily_transfers_${sid}_${selectedMachineId}` : `machineDaily_transfers_${selectedMachineId}`;
      const cachedTrans = localStorage.getItem(keyTrans);
      if (cachedTrans) {
        const parsed = JSON.parse(cachedTrans);
        if (Array.isArray(parsed)) setTransferReceipts(parsed);
      }
    } catch {}

    // Initial Fetch
    refreshMachineData();
  }, [open, shopId, selectedMachineId]);


  const handleClose = (nextOpen: boolean) => {
    if (!nextOpen) {
      //           
      resetDialogState();
    }
    onOpenChange(nextOpen);
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="w-screen h-[100vh] overflow-hidden rounded-none p-0 flex flex-col dark:bg-[#121212]">
        <DialogHeader className="sticky top-0 bg-white dark:bg-[#0F0F0F] z-20 border-b border-gray-200 dark:border-[#333] px-4 py-3">
          <DialogTitle className="flex items-center justify-between text-right">
            <span className="flex items-center gap-2">
              <FawryMachineIcon className="h-5 w-5 text-yellow-500" />
               
            </span>
          </DialogTitle>
          <DialogDescription className="text-right">
                           " ".
          </DialogDescription>
        </DialogHeader>

        <div className="px-4 py-3 overflow-y-auto flex-1 grid grid-cols-1 gap-6">
          {/* /  */}
          <Card>
            <CardHeader className="pb-2">
          <CardTitle className="flex items-center justify-between">
            <span className="text-right"> </span>
            <div className="flex items-center gap-2">
              <Button
                variant="destructive"
                onClick={async () => {
                  try {
                    if (!shopId || !selectedMachineId) return;
                    const baseRef = doc(db, 'shops', shopId, 'machines', selectedMachineId);
                    try {
                      const transSnap = await getDocs(collection(db, 'shops', shopId, 'machines', selectedMachineId, 'transfers'));
                      await Promise.all(transSnap.docs.map(d => deleteDoc(d.ref)));
                    } catch {}
                    try {
                      const delSnap = await getDocs(collection(db, 'shops', shopId, 'machines', selectedMachineId, 'deliveries'));
                      await Promise.all(delSnap.docs.map(d => deleteDoc(d.ref)));
                    } catch {}
                    await deleteDoc(baseRef);
                    setMachines(prev => prev.filter(m => m.id !== selectedMachineId));
                    const next = machines.find(m => m.id !== selectedMachineId);
                    setSelectedMachineId(next ? next.id : null);
                    try {
                      const keyOpening = `${STORAGE_KEY_OPENING}_${selectedMachineId}`;
                      const keySnap = `${STORAGE_KEY_OPENING_SNAPSHOT}_${selectedMachineId}`;
                      localStorage.removeItem(keyOpening);
                      localStorage.removeItem(keySnap);
                    } catch {}
                    toast({ title: '  ', description: '  .' });
                  } catch (err) {
                    toast({ title: ' ', description: '  .' , variant: 'destructive' });
                  }
                }}
                disabled={!selectedMachineId}
                title=" "
              >
                <Trash2 className="h-4 w-4" />
              </Button>
            </div>
          </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 items-end">
                <div>
                  <label className="text-sm font-medium block text-right"> </label>
                  <Select value={selectedMachineId ?? ''} onValueChange={(v) => { setSelectedMachineId(v); try { const sidSel = shopId || getFallbackShopId(); if (sidSel) localStorage.setItem(`machineDaily_selected_${sidSel}`, v); } catch {} }}>
                    <SelectTrigger className="text-right">
                      <SelectValue placeholder=" " />
                    </SelectTrigger>
                    <SelectContent>
                      {machines.map(m => (
                        <SelectItem key={m.id} value={m.id}>{m.name}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-2">
                  <div className="md:col-span-2">
                    <label className="text-sm font-medium block text-right">  </label>
                    <Input value={newMachineName} onChange={(e) => setNewMachineName(e.target.value)} placeholder=" " className="text-right" />
                  </div>
                  <div className="flex justify-end md:justify-start">
                    <Button
                      onClick={async () => {
                        try {
                          if (!shopId) {
                            toast({ title: '  ', description: '     .', variant: 'destructive' });
                            return;
                          }
                          const name = (newMachineName || '').trim();
                          if (!name) {
                            toast({ title: '  ', description: '   .', variant: 'destructive' });
                            return;
                          }
                          const ref = await addDoc(collection(db, 'shops', shopId, 'machines'), {
                            name,
                            shopId,
                            createdAt: serverTimestamp(),
                            updatedAt: serverTimestamp(),
                            isActive: true,
                          });
                          const created = { id: ref.id, name };
                          setMachines(prev => [created, ...prev]);
                          setSelectedMachineId(ref.id);
                          setNewMachineName('');
                          toast({ title: '  ', description: `  ${name}` });
                        } catch (err) {
                          console.error('Create machine error:', err);
                          toast({ title: '  ', description: '  .', variant: 'destructive' });
                        }
                      }}
                      className="bg-violet-600 hover:bg-violet-700"
                    >
                      
                    </Button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
          {/*   */}
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <span className="text-right"> </span>
                <Button variant="ghost" size="sm" onClick={() => setIsOpeningOpen((v) => !v)} className="flex items-center gap-1">
                  <ChevronDown className={`h-4 w-4 transition-transform ${isOpeningOpen ? 'rotate-180' : ''}`} />
                </Button>
              </CardTitle>
            </CardHeader>
            {/*        */}
            <div className="flex items-center justify-between px-6">
              <div className="flex items-center gap-2">
                <Button variant="outline" onClick={() => setTransferDialogOpen(true)} disabled={savedOpeningBase == null || savedOpeningAir == null} className="border-violet-300 text-violet-700">
                  
                </Button>
              </div>
              {savedOpeningBase != null && savedOpeningAir != null && (
                <Badge className="bg-violet-100 text-violet-700 border border-violet-200">
                   :  {formatNumber(savedOpeningBase)} |  {formatNumber(savedOpeningAir)} |  {formatNumber(savedDrawerCash ?? 0)}
                </Badge>
              )}
            </div>
            <CardContent className={`space-y-4 ${isOpeningOpen ? '' : 'hidden'}`}>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right"> </label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={openingBase}
                    onChange={(e) => setOpeningBase(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right"> </label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={openingAir}
                    onChange={(e) => setOpeningAir(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
                <div className="space-y-1">
                  <label className="text-sm font-medium block text-right"> </label>
                  <Input
                    inputMode="decimal"
                    type="number"
                    min={0}
                    step="0.01"
                    value={drawerCash}
                    onChange={(e) => setDrawerCash(e.target.value)}
                    placeholder="0.00"
                    className="text-right"
                  />
                </div>
              </div>
              <div className="flex items-center justify-end">
                <Button variant="outline" className="mr-2" onClick={handleResetOpening}>
                  
                </Button>
                <Button onClick={handleAddOpening} className="bg-violet-600 hover:bg-violet-700">
                  
                </Button>
              </div>
            </CardContent>
          </Card>

          {/*   */}
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <span className="text-right"> </span>
                  <Badge className="bg-green-100 text-green-700 border border-green-200">: {formatNumber(totalTransferProfit)}</Badge>
                </div>
                <div className="flex items-center gap-2">
                  <Button variant="outline" size="sm" onClick={handleClearTransferReceipts} className="border-red-300 text-red-700 hover:bg-red-50"> </Button>
                  <Receipt className="h-4 w-4 text-gray-500" />
                </div>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              {transferReceipts.length === 0 ? (
                <p className="text-sm text-gray-500 text-right">   .</p>
              ) : (
                <div className="space-y-3 max-h-52 overflow-y-auto pr-1">
                  {transferReceipts.map((t) => (
                    <div key={t.id} className="rounded-lg border p-3 grid grid-cols-1 md:grid-cols-3 gap-3">
                      <div className="text-right space-y-1">
                        <p className="text-xs text-gray-500"> </p>
                        <p className="text-sm">{formatNumber(t.wholesalePrice)}</p>
                      </div>
                      <div className="text-right space-y-1">
                        <p className="text-xs text-gray-500"> </p>
                        <p className="text-sm">{formatNumber(t.retailPrice)}</p>
                      </div>
                      <div className="text-right space-y-1">
                        <p className="text-xs text-gray-500"> </p>
                        <p className="text-sm text-green-700">{formatNumber(t.profit)}</p>
                        {t.deductFrom && (
                          <div className="mt-1">
                            <span className="text-[11px] bg-violet-100 text-violet-700 border border-violet-200 rounded px-2 py-0.5"> : {t.deductFrom === 'base' ? '' : ''}</span>
                          </div>
                        )}
                        <div className="flex items-center justify-end gap-2 text-xs text-gray-500">
                          <Calendar className="h-3 w-3" />
                          <span>{formatDateTime(t.createdAt)}</span>
                          {t.cashierName && (
                            <>
                              <Separator orientation="vertical" className="h-3" />
                              <span>: {t.cashierName}</span>
                            </>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>

          {/*   -   */}
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-right"> </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {!isDeliverySectionOpen ? (
                <div className="flex justify-end">
                  <Button variant="outline" onClick={handleStartDelivery} className="border-violet-300 text-violet-700">
                     
                  </Button>
                </div>
              ) : (
                <>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-1">
                      <label className="text-sm font-medium block text-right">  </label>
                      <Input
                        inputMode="decimal"
                        type="number"
                        min={0}
                        step="0.01"
                        value={deliveryBase}
                        onChange={(e) => setDeliveryBase(e.target.value)}
                        placeholder="0.00"
                        className="text-right"
                      />
                    </div>
                    <div className="space-y-1">
                      <label className="text-sm font-medium block text-right">  </label>
                      <Input
                        inputMode="decimal"
                        type="number"
                        min={0}
                        step="0.01"
                        value={deliveryAir}
                        onChange={(e) => setDeliveryAir(e.target.value)}
                        placeholder="0.00"
                        className="text-right"
                      />
                    </div>
                  </div>
                  <div className="flex items-center justify-end gap-2">
                    <Button variant="secondary" onClick={() => setIsDeliverySectionOpen(false)}></Button>
                    <Button onClick={handleDoDelivery} className="bg-green-600 hover:bg-green-700"></Button>
                  </div>

                  {netResult != null && (
                    <div className="mt-3">
                      <div className="text-right">
                        <Badge className={netResult >= 0 ? 'bg-green-100 text-green-700 border border-green-200' : 'bg-red-100 text-red-700 border border-red-200'}>
                           : {formatNumber(netResult)}
                        </Badge>
                      </div>
                      <div className="mt-2 flex items-center justify-end gap-2">
                        <Badge className="bg-blue-100 text-blue-700 border border-blue-200">
                           : {formatNumber(parseFloat(deliveryBase || '0') + parseFloat(deliveryAir || '0'))}
                        </Badge>
                        <Badge className="bg-amber-100 text-amber-700 border border-amber-200">
                             ( ): {formatNumber(totalWholesaleTransferred)}
                        </Badge>
                      </div>
                    </div>
                  )}
                </>
              )}
            </CardContent>
          </Card>

          {/*   */}
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="flex items-center justify-between">
                <span className="text-right"> </span>
                <div className="flex items-center gap-2">
                  <Button variant="outline" size="sm" onClick={handleClearMachineReceipts} className="border-red-300 text-red-700 hover:bg-red-50"> </Button>
                  <Receipt className="h-4 w-4 text-gray-500" />
                </div>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              {receipts.length === 0 ? (
                <p className="text-sm text-gray-500 text-right">    .</p>
              ) : (
                <div className="space-y-3">
                  {receipts.map((r) => (
                    <div key={r.id} className="rounded-lg border p-3 grid grid-cols-1 md:grid-cols-3 gap-3">
                      <div className="text-right space-y-1">
                        <p className="text-xs text-gray-500"></p>
                        <p className="text-sm">: {formatNumber(r.openingBase)}</p>
                        <p className="text-sm">: {formatNumber(r.openingAir)}</p>
                      </div>
                      <div className="text-right space-y-1">
                        <p className="text-xs text-gray-500"></p>
                        <p className="text-sm">: {formatNumber(r.deliveryBase)}</p>
                        <p className="text-sm">: {formatNumber(r.deliveryAir)}</p>
                      </div>
                      <div className="text-right space-y-1">
                        <div className="flex items-center justify-end">
                          <Button variant="outline" size="sm" onClick={() => handleDeleteMachineReceipt(r.id)} className="border-red-300 text-red-700 hover:bg-red-50">
                            <Trash2 className="h-3 w-3 mr-1" /> 
                          </Button>
                        </div>
                        <p className="text-xs text-gray-500"></p>
                        <p className={`text-sm ${r.netResult >= 0 ? 'text-green-700' : 'text-red-700'}`}>{formatNumber(r.netResult)}</p>
                        <div className="mt-1 flex items-center justify-end gap-2">
                          <span className="text-[11px] bg-blue-100 text-blue-700 border border-blue-200 rounded px-2 py-0.5">
                             : {formatNumber(r.deliveryBase + r.deliveryAir)}
                          </span>
                          {typeof r.requiredDrawerNoProfit === 'number' && (
                            <span className="text-[11px] bg-amber-100 text-amber-700 border border-amber-200 rounded px-2 py-0.5">
                                 ( ): {formatNumber(r.requiredDrawerNoProfit || 0)}
                            </span>
                          )}
                        </div>
                        <div className="flex items-center justify-end gap-2 text-xs text-gray-500">
                          <Calendar className="h-3 w-3" />
                          <span>{formatDateTime(r.createdAt)}</span>
                          {r.cashierName && (
                            <>
                              <Separator orientation="vertical" className="h-3" />
                              <span>: {r.cashierName}</span>
                            </>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        <DialogFooter className="sticky bottom-0 bg-white dark:bg-[#0F0F0F] z-20 border-t border-gray-200 dark:border-[#333] px-4 py-3 flex items-center justify-between">
          <Button variant="outline" onClick={() => handleClose(false)}></Button>
          <div className="text-xs text-gray-500 flex items-center gap-1">
            <Clock className="h-3 w-3" />
            <span>     </span>
          </div>
        </DialogFooter>
      </DialogContent>

      {/*    */}
      <Dialog open={transferDialogOpen} onOpenChange={setTransferDialogOpen}>
        <DialogContent className="sm:max-w-sm">
          <DialogHeader>
            <DialogTitle> </DialogTitle>
          </DialogHeader>
          <div className="space-y-3">
            <div>
              <label className="text-sm font-medium block text-right"> </label>
              <Input
                inputMode="decimal"
                type="number"
                min={0}
                step="0.01"
                value={wholesalePrice}
                onChange={(e) => setWholesalePrice(e.target.value)}
                placeholder="0.00"
                className="text-right"
              />
            </div>
            <div>
              <label className="text-sm font-medium block text-right"> </label>
              <Input
                inputMode="decimal"
                type="number"
                min={0}
                step="0.01"
                value={retailPrice}
                onChange={(e) => setRetailPrice(e.target.value)}
                placeholder="0.00"
                className="text-right"
              />
            </div>
            <div className="text-right">
              <Badge className="bg-green-100 text-green-700 border border-green-200">
                 : {formatNumber((parseFloat(retailPrice || '0') - parseFloat(wholesalePrice || '0')) || 0)}
              </Badge>
            </div>
          </div>
          <DialogFooter className="flex items-center justify-end gap-2">
            <Button variant="secondary" onClick={() => setTransferDialogOpen(false)}></Button>
            <Button onClick={handleTransferConfirm} className="bg-violet-600 hover:bg-violet-700"></Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      {/*     */}
      <AlertDialog open={deductDialogOpen} onOpenChange={setDeductDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>  </AlertDialogTitle>
            <AlertDialogDescription>         </AlertDialogDescription>
          </AlertDialogHeader>
          <div className="flex items-center justify-end gap-2 mt-4">
            <AlertDialogCancel onClick={() => setDeductDialogOpen(false)}></AlertDialogCancel>
            <AlertDialogAction className="bg-emerald-600 hover:bg-emerald-700 text-white" onClick={() => finalizeTransfer('base')}>  </AlertDialogAction>
            <AlertDialogAction className="bg-blue-600 hover:bg-blue-700 text-white" onClick={() => finalizeTransfer('air')}>  </AlertDialogAction>
          </div>
        </AlertDialogContent>
      </AlertDialog>
    </Dialog>
  );
}
