import React, { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Lock, Eye, EyeOff, AlertCircle, Settings } from 'lucide-react';
import { SecurityService } from '@/utils/securityService';
import { useToast } from '@/hooks/use-toast';

interface ChangePinDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  userId?: string;
}

export function ChangePinDialog({ open, onOpenChange, userId }: ChangePinDialogProps) {
  const [currentPin, setCurrentPin] = useState('');
  const [newPin, setNewPin] = useState('');
  const [confirmPin, setConfirmPin] = useState('');
  const [showCurrentPin, setShowCurrentPin] = useState(false);
  const [showNewPin, setShowNewPin] = useState(false);
  const [showConfirmPin, setShowConfirmPin] = useState(false);
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();

  const hasExistingPin = SecurityService.hasSecretPin(userId);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    try {
      //     
      if (!newPin || newPin.length < 4) {
        setError('     4   ');
        setIsLoading(false);
        return;
      }

      if (newPin !== confirmPin) {
        setError('     ');
        setIsLoading(false);
        return;
      }

      //          
      if (hasExistingPin) {
        if (!currentPin) {
          setError('    ');
          setIsLoading(false);
          return;
        }

        if (!SecurityService.verifySecretPin(currentPin, userId)) {
          setError('    ');
          setIsLoading(false);
          return;
        }
      }

      //    
      SecurityService.setSecretPin(newPin, userId);

      //   
      toast({
        title: hasExistingPin ? "   " : "   ",
        description: hasExistingPin 
          ? "    " 
          : "    ",
      });

      //     
      handleClose();
    } catch (error) {
      setError('     ');
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setCurrentPin('');
    setNewPin('');
    setConfirmPin('');
    setError('');
    setShowCurrentPin(false);
    setShowNewPin(false);
    setShowConfirmPin(false);
    onOpenChange(false);
  };

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-md" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-right">
            <Settings className="h-5 w-5" />
            {hasExistingPin ? "  " : "  "}
          </DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="text-sm text-muted-foreground text-right">
            {hasExistingPin 
              ? "       "
              : "         "
            }
          </div>

          {/*    (  ) */}
          {hasExistingPin && (
            <div className="space-y-2">
              <Label htmlFor="currentPin" className="text-right block">
                  
              </Label>
              <div className="relative">
                <Input
                  id="currentPin"
                  type={showCurrentPin ? "text" : "password"}
                  autoComplete="off"
                  value={currentPin}
                  onChange={(e) => setCurrentPin(e.target.value)}
                  placeholder="   "
                  className="pr-10"
                  dir="ltr"
                />
                <Button
                  type="button"
                  variant="ghost"
                  size="sm"
                  className="absolute left-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                  onClick={() => setShowCurrentPin(!showCurrentPin)}
                >
                  {showCurrentPin ? (
                    <EyeOff className="h-4 w-4" />
                  ) : (
                    <Eye className="h-4 w-4" />
                  )}
                </Button>
              </div>
            </div>
          )}

          {/*    */}
          <div className="space-y-2">
            <Label htmlFor="newPin" className="text-right block">
                
            </Label>
            <div className="relative">
              <Input
                id="newPin"
                type={showNewPin ? "text" : "password"}
                autoComplete="off"
                value={newPin}
                onChange={(e) => setNewPin(e.target.value)}
                placeholder="   "
                className="pr-10"
                dir="ltr"
              />
              <Button
                type="button"
                variant="ghost"
                size="sm"
                className="absolute left-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                onClick={() => setShowNewPin(!showNewPin)}
              >
                {showNewPin ? (
                  <EyeOff className="h-4 w-4" />
                ) : (
                  <Eye className="h-4 w-4" />
                )}
              </Button>
            </div>
          </div>

          {/*     */}
          <div className="space-y-2">
            <Label htmlFor="confirmPin" className="text-right block">
                 
            </Label>
            <div className="relative">
              <Input
                id="confirmPin"
                type={showConfirmPin ? "text" : "password"}
                autoComplete="off"
                value={confirmPin}
                onChange={(e) => setConfirmPin(e.target.value)}
                placeholder="    "
                className="pr-10"
                dir="ltr"
              />
              <Button
                type="button"
                variant="ghost"
                size="sm"
                className="absolute left-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                onClick={() => setShowConfirmPin(!showConfirmPin)}
              >
                {showConfirmPin ? (
                  <EyeOff className="h-4 w-4" />
                ) : (
                  <Eye className="h-4 w-4" />
                )}
              </Button>
            </div>
          </div>

          {/*   */}
          {error && (
            <div className="flex items-center gap-2 text-red-600 text-sm">
              <AlertCircle className="h-4 w-4" />
              {error}
            </div>
          )}

          {/*   */}
          <div className="flex gap-2 pt-4">
            <Button
              type="submit"
              disabled={isLoading}
              className="flex-1"
            >
              <Lock className="h-4 w-4 mr-2" />
              {isLoading ? " ..." : (hasExistingPin ? "  " : "  ")}
            </Button>
            <Button
              type="button"
              variant="outline"
              onClick={handleClose}
              disabled={isLoading}
            >
              
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}