import React, { useState, useEffect, useMemo } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Separator } from "@/components/ui/separator";
import { Switch } from "@/components/ui/switch";
import { MasterPinDialog } from '@/components/MasterPinDialog';
import { useToast } from '@/hooks/use-toast';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { db } from '@/firebase';
import { collection, addDoc, serverTimestamp, query, where, limit, getDocs, updateDoc, deleteDoc, doc, setDoc } from 'firebase/firestore';
import { X, Plus, Edit, Edit3, Trash2, Save, XCircle, DollarSign, Package, TrendingUp, Calculator, BookOpen, ChevronDown, ChevronUp, Calendar, Clock, BarChart3, Target, Award, TrendingDown, AlertCircle, Info, ChevronLeft, ChevronRight, Eye, EyeOff, Receipt, Search, ScanBarcode, Printer, Lock, RefreshCw } from 'lucide-react';
import { getUserSubscription, SubscriptionPlan } from '@/utils/subscriptionService';
import { useShift } from '@/contexts/ShiftContext';
import { userDataService, telegramNotifier } from '@/integrations/firebase/services';
import { useShop } from '@/hooks/useShop';

interface SalesDataDialogProps {
  isOpen: boolean;
  onClose: () => void;
  initialBarcode?: string | null;
}

interface SaleItem {
  id: string;
  productName: string;
  price: number;
  wholesalePrice?: number;
  quantity: number;
  date: string;
  time: string;
  performedByType?: 'cashier' | 'admin';
  performedByName?: string;
  performedByUsername?: string | null;
  serialNumber?: string;
  barcode?: string;
  firestoreId?: string;
  isDeferred?: boolean;
  customerName?: string;
  customerMobile?: string;
  customerId?: string;
  createdAtMs?: number;
}

interface NewProduct {
  productName: string;
  price: string;
  wholesalePrice?: string;
  quantity: string;
  barcode?: string;
  serialNumber?: string;
}

interface ShopProduct {
  id: string;
  name: string;
  sellingPrice: number;
  wholesalePrice: number;
  quantity: number;
  barcode?: string;
  serialNumber?: string;
}

export function SalesDataDialog({ isOpen, onClose, initialBarcode }: SalesDataDialogProps) {
  const [salesData, setSalesData] = useState<SaleItem[]>([]);
  const [newProduct, setNewProduct] = useState<NewProduct>({ productName: '', price: '', wholesalePrice: '', quantity: '', barcode: '', serialNumber: '' });
  const [isAddProductOpen, setIsAddProductOpen] = useState<boolean>(false);
  const [editingId, setEditingId] = useState<string | null>(null);
  //  :    /  
  const { toast } = useToast();
  const { user, profile } = useFirebaseAuth();
  const { isShiftActive, shiftUser } = useShift();
  const { shopId: shopIdCtx } = useShop();
  const [shopProducts, setShopProducts] = useState<ShopProduct[]>([]);
const [sellingQtyByProduct, setSellingQtyByProduct] = useState<Record<string, string>>({});
const [addQtyByProduct, setAddQtyByProduct] = useState<Record<string, string>>({});
const [serialByProduct, setSerialByProduct] = useState<Record<string, string>>({});
const [scannedBarcode, setScannedBarcode] = useState<string>('');
const [isInvoiceMode, setIsInvoiceMode] = useState<boolean>(false);
const [invoiceSelections, setInvoiceSelections] = useState<Record<string, number>>({});
// /     
const [isWholesaleVisible, setIsWholesaleVisible] = useState<boolean>(false);
//      
  const [isProfitLocked, setIsProfitLocked] = useState<boolean>(true);
  const [isProfitPinDialogOpen, setIsProfitPinDialogOpen] = useState<boolean>(false);
const barcodeBufferRef = React.useRef<string>('');
const lastKeyTimeRef = React.useRef<number>(0);
  const [monthlySalesTotal, setMonthlySalesTotal] = useState<number>(0);
  const [monthlyProfitTotal, setMonthlyProfitTotal] = useState<number>(0);
  const [isMasterPinDialogOpen, setIsMasterPinDialogOpen] = useState<boolean>(false);
  const [isMonthlyLocked, setIsMonthlyLocked] = useState<boolean>(true);
const [isMonthlyCollapsed, setIsMonthlyCollapsed] = useState<boolean>(false);
const [isSalesReceiptsDialogOpen, setIsSalesReceiptsDialogOpen] = useState<boolean>(false);
//  :        
const [isProductStatsCollapsed, setIsProductStatsCollapsed] = useState<boolean>(true);
const [isTipsCollapsed, setIsTipsCollapsed] = useState<boolean>(true);
  //        
  const [sellingPriceOverrideByProduct, setSellingPriceOverrideByProduct] = useState<Record<string, string>>({});
  //        (/)
  const [isMasterPinActionOpen, setIsMasterPinActionOpen] = useState<boolean>(false);
  const [masterPinActionTitle, setMasterPinActionTitle] = useState<string>('  ');
  const [masterPinActionDescription, setMasterPinActionDescription] = useState<string>('    ');
  const protectedActionRef = React.useRef<(() => void) | null>(null);
  const requireMasterPin = (title: string, description: string, action: () => Promise<void> | void) => {
    setMasterPinActionTitle(title);
    setMasterPinActionDescription(description);
    protectedActionRef.current = action;
    setIsMasterPinActionOpen(true);
  };
  const [customers, setCustomers] = useState<{ id: string; name: string; mobile?: string }[]>([]);
  const [isDeferredFormOpen, setIsDeferredFormOpen] = useState<boolean>(false);
  const [deferredCustomerId, setDeferredCustomerId] = useState<string>('');
  const [deferredCustomerName, setDeferredCustomerName] = useState<string>('');
  const [deferredCustomerMobile, setDeferredCustomerMobile] = useState<string>('');
  const [pendingDeferredProduct, setPendingDeferredProduct] = useState<ShopProduct | null>(null);
  useEffect(() => {
    (async () => {
      try {
        if (!isOpen || !user?.uid) return;
        const data = await userDataService.getUserData(user.uid);
        const list = Array.isArray(data?.customers) ? data.customers.map((c: any) => ({ id: String(c.id || ''), name: String(c.name || ''), mobile: c.mobile || undefined })) : [];
        setCustomers(list);
      } catch {}
    })();
  }, [isOpen, user?.uid]);
  //    /
  const [isDeleteConfirmOpen, setIsDeleteConfirmOpen] = useState<boolean>(false);
  const [deleteTargetSale, setDeleteTargetSale] = useState<SaleItem | null>(null);
  const [deleteActionType, setDeleteActionType] = useState<'return' | 'delete' | null>(null);
  const openDeleteConfirm = (sale: SaleItem, type: 'return' | 'delete') => {
    setDeleteTargetSale(sale);
    setDeleteActionType(type);
    setIsDeleteConfirmOpen(true);
  };
  //    
  const [productSearchTerm, setProductSearchTerm] = useState<string>('');
  const filteredShopProducts = useMemo(() => {
    const term = productSearchTerm.trim().toLowerCase();
    if (!term) return shopProducts;
    return shopProducts.filter(p => p.name.toLowerCase().includes(term));
  }, [shopProducts, productSearchTerm]);

  //     (  prompt)
  const [isAddQtyDialogOpen, setIsAddQtyDialogOpen] = useState<boolean>(false);
  const [productToAddQty, setProductToAddQty] = useState<ShopProduct | null>(null);
  const [qtyToAdd, setQtyToAdd] = useState<string>('1');

  const handleOpenAddQtyDialog = (product: ShopProduct) => {
    setProductToAddQty(product);
    setQtyToAdd('1');
    setIsAddQtyDialogOpen(true);
  };

  const handleAddQtyConfirm = () => {
    if (!productToAddQty) return;
    const q = parseInt(qtyToAdd);
    if (q > 0) {
      setIsAddQtyDialogOpen(false);
      requireMasterPin(' ', ` ${q}   ${productToAddQty.name}`, async () => {
         try {
            const newQty = productToAddQty.quantity + q;
            await updateDoc(doc(db, 'products', productToAddQty.id), { quantity: newQty, updatedAt: serverTimestamp() });
            setShopProducts(prev => prev.map(p => p.id === productToAddQty.id ? { ...p, quantity: newQty } : p));
            toast({ title: ' ', description: `  ${q} ` });
         } catch (e) { console.error(e); }
      });
    }
  };

  const getUtcDateYYYYMMDD = () => new Date().toISOString().split('T')[0];

  //        
  useEffect(() => {
    if (isOpen && user?.uid) {
      loadSalesData();
      //          PIN 
      if (!isMonthlyLocked) {
        loadMonthlyStats();
      }
    }
  }, [isOpen, user?.uid, isMonthlyLocked]);

  //       
  //           loadSalesData


  //       (   )
  useEffect(() => {
    if (!isOpen) return;
    const handler = (e: KeyboardEvent) => {
      //        input/textarea    
      const target = e.target as HTMLElement | null;
      const tag = target?.tagName?.toLowerCase();
      const isTyping = tag === 'input' || tag === 'textarea' || !!(target && (target as any).isContentEditable);
      if (isTyping) return;
      //    PIN        
      if (isMasterPinActionOpen || isMasterPinDialogOpen || isProfitPinDialogOpen) return;

      const now = Date.now();
      if (e.key === 'Enter') {
        const code = (barcodeBufferRef.current || '').trim();
        barcodeBufferRef.current = '';
        if (code) {
          processBarcode(code);
        }
        return;
      }
      if (e.key.length === 1) {
        if (now - (lastKeyTimeRef.current || 0) > 100) {
          barcodeBufferRef.current = '';
        }
        barcodeBufferRef.current += e.key;
        lastKeyTimeRef.current = now;
      }
    };
    window.addEventListener('keydown', handler as unknown as EventListener);
    return () => window.removeEventListener('keydown', handler as unknown as EventListener);
  }, [isOpen, shopProducts, isMasterPinActionOpen, isMasterPinDialogOpen, isProfitPinDialogOpen]);

  //        
  useEffect(() => {
    if (isOpen && initialBarcode) {
      processBarcode(initialBarcode);
    }
  }, [isOpen, initialBarcode]);

  //     Firestore   
  const loadShopProducts = async () => {
    if (!user?.uid) return;
    
    const shopId = profile?.shopId || shopIdCtx || 'main';
    const storageKey = `shopProducts_${user.uid}_${shopId}`;

    //      
    try {
      const savedProducts = localStorage.getItem(storageKey);
      if (savedProducts) {
        const parsed = JSON.parse(savedProducts);
        if (Array.isArray(parsed) && parsed.length > 0) {
           setShopProducts(parsed);
        }
      }
    } catch (e) {
      console.warn('Failed to load products from cache', e);
    }

    try {
      let snap;
      let products: ShopProduct[] = [];

      //         
      if (profile?.shopId) {
        const byUserAndShop = query(
          collection(db, 'products'),
          where('shopId', '==', profile.shopId),
          where('userId', '==', user.uid)
        );
        snap = await getDocs(byUserAndShop);
        products = snap.docs.map(d => ({
          id: d.id,
          name: String(d.data().name ?? ''),
          sellingPrice: Number(d.data().sellingPrice ?? 0),
          wholesalePrice: Number(d.data().wholesalePrice ?? 0),
          quantity: Number(d.data().quantity ?? 0),
          barcode: String(d.data().barcode ?? ''),
          serialNumber: String(d.data().serialNumber ?? ''),
        }));

        //  :          
        if (products.length === 0) {
          const byUserOnly = query(
            collection(db, 'products'),
            where('userId', '==', user.uid)
          );
          const fallbackSnap = await getDocs(byUserOnly);
          const fallbackProducts: ShopProduct[] = fallbackSnap.docs.map(d => ({
            id: d.id,
            name: String(d.data().name ?? ''),
            sellingPrice: Number(d.data().sellingPrice ?? 0),
            wholesalePrice: Number(d.data().wholesalePrice ?? 0),
            quantity: Number(d.data().quantity ?? 0),
            barcode: String(d.data().barcode ?? ''),
            serialNumber: String(d.data().serialNumber ?? ''),
          }));

          if (fallbackProducts.length > 0) {
            products = fallbackProducts;
            //        /
            toast({
              title: '  ',
              description: '         .',
            });
          }
        }
      } else {
        const byUser = query(
          collection(db, 'products'),
          where('userId', '==', user.uid)
        );
        snap = await getDocs(byUser);
        products = snap.docs.map(d => ({
          id: d.id,
          name: String(d.data().name ?? ''),
          sellingPrice: Number(d.data().sellingPrice ?? 0),
          wholesalePrice: Number(d.data().wholesalePrice ?? 0),
          quantity: Number(d.data().quantity ?? 0),
          barcode: String(d.data().barcode ?? ''),
          serialNumber: String(d.data().serialNumber ?? ''),
        }));
      }

      setShopProducts(products);
      
      //        
      try {
        localStorage.setItem(storageKey, JSON.stringify(products));
      } catch {}

      //  :        shopId   
      if (profile?.shopId && products.length > 0) {
        const missingShopProducts = products.filter(p => {
          //       
          const original = (snap?.docs || []).find(d => d.id === p.id);
          const hasShopId = !!original && !!(original.data() as any).shopId;
          return !hasShopId;
        });
        if (missingShopProducts.length > 0) {
          try {
            await Promise.all(
              missingShopProducts.map(p => updateDoc(doc(db, 'products', p.id), {
                shopId: profile.shopId,
                updatedAt: serverTimestamp(),
              }))
            );
            toast({
              title: '  ',
              description: '        .',
            });
          } catch (migrationError) {
            console.warn('  shopId  :', migrationError);
          }
        }
      }
    } catch (error) {
      console.error('Error loading shop products:', error);
    }
  };

  useEffect(() => {
    if (isOpen && user?.uid) {
      loadShopProducts();
      loadMonthlyStats();
      loadSalesData();
      recoverUnsyncedSalesToFirestore();
    }
  }, [isOpen, user?.uid, profile?.shopId]);

  const getLocalDateYYYYMMDD = () => {
    const d = new Date();
    const y = d.getFullYear();
    const m = String(d.getMonth() + 1).padStart(2, '0');
    const dd = String(d.getDate()).padStart(2, '0');
    return `${y}-${m}-${dd}`;
  };

  const sortSales = (arr: SaleItem[]) => {
    const key = (it: SaleItem) => {
      if (typeof it.createdAtMs === 'number') return it.createdAtMs;
      const n = parseInt(it.id, 10);
      if (!isNaN(n)) return n;
      const dt = new Date(it.date);
      return isNaN(dt.getTime()) ? 0 : dt.getTime();
    };
    return [...arr].sort((a, b) => key(b) - key(a));
  };

  const loadSalesData = () => {
    if (!user?.uid) {
      console.log('No user authenticated, cannot load sales data');
      return;
    }
    const sid = shopIdCtx || (profile as any)?.shopId || 'main';
    const allKey = `salesData_all_${user.uid}_${sid}`;
    const savedAll = localStorage.getItem(allKey);
    if (savedAll) {
      try {
        const parsed = JSON.parse(savedAll);
        setSalesData(Array.isArray(parsed) ? sortSales(parsed) : []);
        return;
      } catch {}
    }
    const todayLocal = getLocalDateYYYYMMDD();
    const localKey = `salesData_${user.uid}_${sid}_${todayLocal}`;
    const savedLocal = localStorage.getItem(localKey);
    if (savedLocal) {
      const parsed = JSON.parse(savedLocal);
      const sorted = Array.isArray(parsed) ? sortSales(parsed) : [];
      setSalesData(sorted);
      try { localStorage.setItem(allKey, JSON.stringify(sorted)); } catch {}
      return;
    }
    const todayUtc = new Date().toISOString().split('T')[0];
    const utcKey = `salesData_${user.uid}_${sid}_${todayUtc}`;
    const savedUtc = localStorage.getItem(utcKey);
    if (savedUtc) {
      const parsed = JSON.parse(savedUtc);
      const sorted = Array.isArray(parsed) ? sortSales(parsed) : [];
      setSalesData(sorted);
      try {
        localStorage.setItem(localKey, JSON.stringify(sorted));
        localStorage.setItem(allKey, JSON.stringify(sorted));
      } catch {}
      return;
    }
    syncSalesDataFromFirestore();
  };

  const saveSalesData = (data: SaleItem[]) => {
    if (!user?.uid) {
      console.log('No user authenticated, cannot save sales data');
      return;
    }
    const sid = shopIdCtx || (profile as any)?.shopId || 'main';
    const todayLocal = getLocalDateYYYYMMDD();
    const localKey = `salesData_${user.uid}_${sid}_${todayLocal}`;
    const allKey = `salesData_all_${user.uid}_${sid}`;
    const sortedDay = sortSales(data);
    localStorage.setItem(localKey, JSON.stringify(sortedDay));
    try {
      const existingAllRaw = localStorage.getItem(allKey);
      const existingAll = existingAllRaw ? JSON.parse(existingAllRaw) : [];
      const byId: Record<string, SaleItem> = {} as any;
      [...(Array.isArray(existingAll) ? existingAll : []), ...sortedDay].forEach((item: SaleItem) => {
        byId[item.id] = item;
      });
      const mergedAll = Object.values(byId) as SaleItem[];
      const sortedAll = sortSales(mergedAll);
      localStorage.setItem(allKey, JSON.stringify(sortedAll));
    } catch {}
    setSalesData(sortedDay);
  };

  const saveAllSalesData = (data: SaleItem[]) => {
    if (!user?.uid) {
      console.log('No user authenticated, cannot save sales data');
      return;
    }
    const sid = shopIdCtx || (profile as any)?.shopId || 'main';
    const allKey = `salesData_all_${user.uid}_${sid}`;
    try {
      const existingAllRaw = localStorage.getItem(allKey);
      const existingAll = existingAllRaw ? JSON.parse(existingAllRaw) : [];
      const byId: Record<string, SaleItem> = {} as any;
      [...(Array.isArray(existingAll) ? existingAll : []), ...data].forEach((item: SaleItem) => {
        byId[item.id] = item;
      });
      const mergedAll = Object.values(byId) as SaleItem[];
      const sortedAll = sortSales(mergedAll);
      localStorage.setItem(allKey, JSON.stringify(sortedAll));
    } catch {}
    const sortedDay = sortSales(data);
    setSalesData(sortedDay);
  };

  const syncSalesDataFromFirestore = async () => {
    if (!user?.uid) return;
    try {
      const sid = shopIdCtx || (profile as any)?.shopId || null;
      const todayUtc = new Date().toISOString().split('T')[0];
      const todayCairo = new Intl.DateTimeFormat('en-CA', { timeZone: 'Africa/Cairo' }).format(new Date());
      let snap1, snap2;
      try {
        const q1 = query(
          collection(db, 'dailySales'),
          where('userId', '==', user.uid),
          ...(sid ? [where('shopId', '==', sid)] : [])
        );
        snap1 = await getDocs(q1);
      } catch {}
      try {
        const q2 = query(
          collection(db, 'users', user.uid, 'dailySales'),
          ...(sid ? [where('shopId', '==', sid)] : [])
        );
        snap2 = await getDocs(q2);
      } catch {}
      const docs = [
        ...(snap1 ? snap1.docs : []),
        ...(snap2 ? snap2.docs : []),
      ];
      const remote = docs.map(d => {
        const x: any = d.data();
        return {
          id: String(x.clientId || d.id),
          firestoreId: d.id,
          productName: String(x.productName || ''),
          price: Number(x.sellingPrice || 0),
          wholesalePrice: x.wholesalePrice !== undefined ? Number(x.wholesalePrice) : null,
          quantity: Number(x.quantity || 0),
          date: String(x.date || todayUtc),
          time: String(x.time || ''),
          serialNumber: x.serialNumber || undefined,
          barcode: x.barcode || undefined,
          performedByType: x.performedByType || undefined,
          performedByName: x.performedByName || undefined,
          performedByUsername: x.performedByUsername || undefined,
          isDeferred: !!x.isDeferred,
          customerName: x.customerName || undefined,
          customerMobile: x.customerMobile || undefined,
          customerId: x.customerId || undefined,
          createdAtMs: x?.createdAt?.toDate?.() ? x.createdAt.toDate().getTime() : (new Date(String(x.date || todayUtc)).getTime()),
        } as SaleItem;
      });
      const byId: Record<string, SaleItem> = {} as any;
      [...salesData, ...remote].forEach((s: any) => { byId[s.id] = s; });
      const merged = Object.values(byId) as SaleItem[];
      saveAllSalesData(merged);
    } catch {}
  };

  //     Firestore
  const loadMonthlyStats = async () => {
    if (!user?.uid) return;
    try {
      const now = new Date();
      const start = new Date(now.getFullYear(), now.getMonth(), 1);
      const end = new Date(now.getFullYear(), now.getMonth() + 1, 0);
      const toDateStr = (d: Date) => d.toISOString().split('T')[0];
      const startStr = toDateStr(start);
      const endStr = toDateStr(end);
      const sidMonthly = shopIdCtx || (profile as any)?.shopId || null;
      let snap;
      try {
        const byUserShop = query(
          collection(db, 'dailySales'),
          where('userId', '==', user.uid),
          ...(sidMonthly ? [where('shopId', '==', sidMonthly)] : [])
        );
        snap = await getDocs(byUserShop);
      } catch {
        const byUserShopNested = query(
          collection(db, 'users', user.uid, 'dailySales'),
          ...(sidMonthly ? [where('shopId', '==', sidMonthly)] : [])
        );
        snap = await getDocs(byUserShopNested);
      }
      let sales = 0;
      let profit = 0;
      snap.forEach(docSnap => {
        const d = docSnap.data() as any;
        const dateStr = String(d.date ?? '').slice(0, 10);
        if (d.isDeferred) return;
        if (dateStr >= startStr && dateStr <= endStr) {
          const sellingPrice = Number(d.sellingPrice ?? 0);
          const qty = Number(d.quantity ?? 0);
          const pr = Number(d.profit ?? ((Number(d.sellingPrice ?? 0) - Number(d.wholesalePrice ?? 0)) * qty));
          sales += sellingPrice * qty;
          profit += pr;
        }
      });

      setMonthlySalesTotal(sales);
      setMonthlyProfitTotal(profit);
    } catch (error) {
      console.error('Error loading monthly stats:', error);
    }
  };

  //     Firebase   
  const saveSaleToFirebase = async (sale: SaleItem): Promise<string | null> => {
    if (!user?.uid) return null;
    try {
      const profit = ((sale.price ?? 0) - (sale.wholesalePrice ?? 0)) * (sale.quantity ?? 0);
      const performedByType = isShiftActive && shiftUser ? 'cashier' : 'admin';
      const performedByName = performedByType === 'cashier' ? (shiftUser?.name || '') : (profile?.fullName || user?.displayName || ' ');
      const performedByUsername = performedByType === 'cashier' ? (shiftUser?.username || null) : null;
      const docRef = await addDoc(collection(db, 'dailySales'), {
        userId: user.uid,
        clientId: sale.id,
        productName: sale.productName,
        quantity: sale.quantity,
        wholesalePrice: sale.wholesalePrice ?? null,
        sellingPrice: sale.price,
        profit,
        date: sale.date,
        time: sale.time,
        performedByType,
        performedByName,
        performedByUsername,
        serialNumber: sale.serialNumber ?? null,
        barcode: sale.barcode ?? null,
        isDeferred: !!sale.isDeferred,
        customerName: sale.customerName || null,
        customerMobile: sale.customerMobile || null,
        customerId: sale.customerId || null,
        ...(profile?.shopId ? { shopId: profile.shopId } : (shopIdCtx ? { shopId: shopIdCtx } : {})),
        createdAt: serverTimestamp()
      });
      try {
        await setDoc(doc(db, 'users', user.uid, 'dailySales', docRef.id), {
          userId: user.uid,
          clientId: sale.id,
          productName: sale.productName,
          quantity: sale.quantity,
          wholesalePrice: sale.wholesalePrice ?? null,
          sellingPrice: sale.price,
          profit,
          date: sale.date,
          time: sale.time,
          performedByType,
          performedByName,
          performedByUsername,
          serialNumber: sale.serialNumber ?? null,
          barcode: sale.barcode ?? null,
          isDeferred: !!sale.isDeferred,
          customerName: sale.customerName || null,
          customerMobile: sale.customerMobile || null,
          customerId: sale.customerId || null,
          ...(profile?.shopId ? { shopId: profile.shopId } : (shopIdCtx ? { shopId: shopIdCtx } : {})),
          createdAt: serverTimestamp()
        });
      } catch {}
      return docRef.id;
    } catch {
      try {
        const docRef2 = await addDoc(collection(db, 'users', user.uid, 'dailySales'), {
          userId: user.uid,
          clientId: sale.id,
          productName: sale.productName,
          quantity: sale.quantity,
          wholesalePrice: sale.wholesalePrice ?? null,
          sellingPrice: sale.price,
          profit,
          date: sale.date,
          time: sale.time,
          performedByType,
          performedByName,
          performedByUsername,
          serialNumber: sale.serialNumber ?? null,
          barcode: sale.barcode ?? null,
          isDeferred: !!sale.isDeferred,
          customerName: sale.customerName || null,
          customerMobile: sale.customerMobile || null,
          customerId: sale.customerId || null,
          ...(profile?.shopId ? { shopId: profile.shopId } : (shopIdCtx ? { shopId: shopIdCtx } : {})),
          createdAt: serverTimestamp()
        });
        try {
          await setDoc(doc(db, 'dailySales', docRef2.id), {
            userId: user.uid,
            clientId: sale.id,
            productName: sale.productName,
            quantity: sale.quantity,
            wholesalePrice: sale.wholesalePrice ?? null,
            sellingPrice: sale.price,
            profit,
            date: sale.date,
            time: sale.time,
            performedByType,
            performedByName,
            performedByUsername,
            serialNumber: sale.serialNumber ?? null,
            barcode: sale.barcode ?? null,
            isDeferred: !!sale.isDeferred,
            customerName: sale.customerName || null,
            customerMobile: sale.customerMobile || null,
            customerId: sale.customerId || null,
            ...(profile?.shopId ? { shopId: profile.shopId } : (shopIdCtx ? { shopId: shopIdCtx } : {})),
            createdAt: serverTimestamp()
          });
        } catch {}
        return docRef2.id;
      } catch (error) {
        console.error('Error saving sale:', error);
        return null;
      }
    }
  };

  //      
  const getCashBalanceKey = () => {
    const sid = shopIdCtx || (profile as any)?.shopId || 'main';
    return `cashBalance_${user?.uid || 'default'}_${sid}`;
  };

  //     Firebase
  const updateSaleInFirebase = async (id: string, updatedFields: Partial<SaleItem>) => {
    if (!user?.uid) return;
    try {
      const saleDoc = doc(db, 'dailySales', id);
      await updateDoc(saleDoc, updatedFields as any);
    } catch {
      try {
        const saleDoc2 = doc(db, 'users', user.uid, 'dailySales', id);
        await updateDoc(saleDoc2, updatedFields as any);
      } catch (error) {
        console.error('Error updating sale:', error);
      }
    }
  };

  const markDeferredSalePaid = async (sale: SaleItem) => {
    try {
      const id = sale.firestoreId;
      if (id) {
        await updateSaleInFirebase(id, { isDeferred: false } as any);
      } else {
        try {
          const q = query(
            collection(db, 'dailySales'),
            where('userId', '==', user?.uid || ''),
            where('date', '==', sale.date),
            where('productName', '==', sale.productName),
            limit(5)
          );
          const snap = await getDocs(q);
          const target = snap.docs[0];
          if (target) await updateDoc(target.ref, { isDeferred: false } as any);
        } catch {}
      }
      setSalesData(prev => prev.map(s => s.id === sale.id ? { ...s, isDeferred: false } : s));
      try { await loadMonthlyStats(); } catch {}
      toast({ title: ' ', description: `   ${sale.productName}` });
    } catch (error) {
      console.error('Error marking deferred sale as paid:', error);
      toast({ title: ' ', description: '    ', variant: 'destructive' });
    }
  };

  const recoverUnsyncedSalesToFirestore = async () => {
    if (!user?.uid) return;
    const unsynced = salesData.filter(s => !s.firestoreId);
    if (unsynced.length === 0) return;
    for (const sale of unsynced) {
      try {
        let existingSnap;
        try {
          const existingQ = query(
            collection(db, 'dailySales'),
            where('userId', '==', user.uid),
            where('clientId', '==', sale.id),
            limit(1)
          );
          existingSnap = await getDocs(existingQ);
        } catch {
          const existingQ2 = query(
            collection(db, 'users', user.uid, 'dailySales'),
            where('clientId', '==', sale.id),
            limit(1)
          );
          existingSnap = await getDocs(existingQ2);
        }
        if (!existingSnap.empty) {
          const docId = existingSnap.docs[0].id;
          const updated = salesData.map(s => s.id === sale.id ? { ...s, firestoreId: docId } : s);
          saveSalesData(updated);
          continue;
        }
        const docId = await saveSaleToFirebase(sale);
        if (docId) {
          const updated = salesData.map(s => s.id === sale.id ? { ...s, firestoreId: docId } : s);
          saveSalesData(updated);
        }
      } catch {}
    }
  };

  //       (Firestore   )
  const addOutOfStockDeficiency = async (productName: string) => {
    try {
      if (user?.uid) {
        try {
          const ref = collection(db, 'deficiencies');
          const docRef = await addDoc(ref, {
            shopId: (profile?.shopId || user.uid || 'default-shop'),
            name: productName,
            quantity: 1,
            status: 'pending',
            createdAt: serverTimestamp(),
          });
          //         /
          try {
            const key = `deficiencies_${(profile?.shopId || user?.uid || 'default-shop')}`;
            const raw = localStorage.getItem(key);
            const list = raw ? JSON.parse(raw) : [];
            const safeList = Array.isArray(list) ? list : [];
            const exists = safeList.some((d: any) => String(d?.id || '') === docRef.id);
            const optimisticItem = {
              id: docRef.id,
              name: productName,
              quantity: 1,
              status: 'pending',
              createdAt: new Date().toISOString(),
            };
            const updatedOptimistic = exists ? safeList : [...safeList, optimisticItem];
            localStorage.setItem(key, JSON.stringify(updatedOptimistic));
          } catch {}
          toast({ title: '  ', description: ` ${productName}      ` });
          return;
        } catch (err) {
          console.warn('Failed to add deficiency to Firestore, falling back to local storage:', err);
        }
      }
      const key = `deficiencies_${(profile?.shopId || user?.uid || 'default-shop')}`;
      const raw = localStorage.getItem(key);
      const list = raw ? JSON.parse(raw) : [];
      const safeList = Array.isArray(list) ? list : [];
      const exists = safeList.some((d: any) => String(d?.name || '').trim() === productName && String(d?.status || 'pending') === 'pending');
      if (exists) return;
      const newItem = {
        id: Date.now().toString(),
        name: productName,
        quantity: 1,
        status: 'pending',
        createdAt: new Date().toISOString(),
      };
      const updated = [...safeList, newItem];
      localStorage.setItem(key, JSON.stringify(updated));
      toast({ title: '  ', description: ` ${productName}      ` });
    } catch (e) {
      console.error('Error adding deficiency for out-of-stock:', e);
    }
  };

  //     
  const sellProduct = async (product: ShopProduct, isDeferred = false, customer?: { id?: string; name: string; mobile?: string }) => {
    const qtyStr = sellingQtyByProduct[product.id] || '';
    const qty = parseInt(qtyStr);
    if (isNaN(qty) || qty <= 0) {
      toast({ title: '  ', description: '   ', variant: 'destructive' });
      return;
    }
    if (qty > product.quantity) {
      toast({ title: '   ', description: '     ', variant: 'destructive' });
      return;
    }

    const now = new Date();
    const performedByType = isShiftActive && shiftUser ? 'cashier' : 'admin';
    const performedByName = performedByType === 'cashier' ? (shiftUser?.name || '') : (profile?.fullName || user?.displayName || ' ');
    const performedByUsername = performedByType === 'cashier' ? (shiftUser?.username || null) : null;

    const serial = (product.serialNumber || '').trim();
    //       
    const overrideStr = sellingPriceOverrideByProduct[product.id] || '';
    const overridePrice = parseFloat(overrideStr);
    const usedPrice = (!isNaN(overridePrice) && overridePrice > 0) ? overridePrice : product.sellingPrice;
    const sale: SaleItem = {
      id: Date.now().toString(),
      productName: product.name,
      price: usedPrice,
      wholesalePrice: product.wholesalePrice,
      quantity: qty,
      date: now.toISOString().split('T')[0],
      time: now.toLocaleTimeString('ar-SA', { hour: '2-digit', minute: '2-digit' }),
      createdAtMs: now.getTime(),
      performedByType,
      performedByName,
      performedByUsername,
      serialNumber: serial || undefined,
      barcode: product.barcode,
      isDeferred,
      customerName: customer?.name,
      customerMobile: customer?.mobile,
      customerId: customer?.id,
    };

    try {
      //            
      const docId = await saveSaleToFirebase(sale);
      const saleWithId: SaleItem = docId ? { ...sale, firestoreId: docId } : sale;
      saveSalesData([...salesData, saleWithId]);
      //       Firestore
      const newQty = product.quantity - qty;
      await updateDoc(doc(db, 'products', product.id), { quantity: newQty, updatedAt: serverTimestamp() });
      setShopProducts(prev => prev.map(p => p.id === product.id ? { ...p, quantity: newQty } : p));
      setSellingQtyByProduct(prev => ({ ...prev, [product.id]: '' }));
      setSellingPriceOverrideByProduct(prev => ({ ...prev, [product.id]: '' }));
      if (newQty === 0) {
        addOutOfStockDeficiency(product.name);
      }

      if (!isDeferred) {
        try {
          const saleTotal = usedPrice * qty;
          const cashKey = getCashBalanceKey();
          const savedCash = localStorage.getItem(cashKey);
          const currentCash = savedCash ? parseFloat(savedCash) : 0;
          const newCash = currentCash + saleTotal;
          localStorage.setItem(cashKey, newCash.toString());
          const localDataKey = `userData_${user?.uid}`;
          const updateData = { cashBalance: newCash, lastUpdated: new Date().toISOString() } as any;
          localStorage.setItem(localDataKey, JSON.stringify(updateData));
          if (user?.uid) {
            userDataService.updateUserData(user.uid, updateData).catch(() => {});
          }
          const shopId = (profile as any)?.shopId || shopIdCtx;
          if (shopId) {
            try { await updateDoc(doc(db, 'dashboardData', shopId), { cashBalance: newCash }); } catch {}
          }
          try { window.dispatchEvent(new CustomEvent('cashBalanceChanged', { detail: { value: newCash } })); } catch {}
          try {
            if (user?.uid) {
              const receiptRef = `SALECASH-${user.uid}-${Date.now()}`;
              const txToCloud: any = {
                txnType: 'cash_addition',
                type: 'cash_addition',
                amount: saleTotal,
                status: 'confirmed',
                senderMsisdn: ' ',
                receiverMsisdn: '',
                reference: receiptRef,
                title: '  ',
                merchantUserId: user.uid,
                createdBy: user.uid,
                shopId: (profile as any)?.shopId || shopIdCtx || undefined,
                cashierName: performedByName,
                performedByType,
                performedByUsername,
                createdAt: new Date()
              };
              await userDataService.saveUserTransaction(user.uid, txToCloud);
            }
          } catch {}
        } catch (e) {
          console.warn('      :', e);
        }
      }

      toast({ title: ' ', description: `  ${qty}   ${product.name}` });
    } catch (error) {
      console.error('Error selling product:', error);
      toast({ title: ' ', description: '   ', variant: 'destructive' });
    }
  };

  //          
  const deleteSale = async (sale: SaleItem) => {
    if (!user?.uid) return;
    if (isShiftActive) {
      toast({ title: '    ', description: '      ', variant: 'destructive' });
      return;
    }
    requireMasterPin(
      '   ',
      '       ',
      async () => {
        try {
          //       
          const todayLocal = getLocalDateYYYYMMDD();
          const localKey = `salesData_${user.uid}_${todayLocal}`;
          const allKey = `salesData_all_${user.uid}`;
          const updatedDay = salesData.filter(s => s.id !== sale.id);
          try {
            localStorage.setItem(localKey, JSON.stringify(updatedDay));
            const existingAllRaw = localStorage.getItem(allKey);
            const existingAll = existingAllRaw ? JSON.parse(existingAllRaw) : [];
            const updatedAll = (Array.isArray(existingAll) ? existingAll : []).filter((s: any) => s?.id !== sale.id);
            localStorage.setItem(allKey, JSON.stringify(updatedAll));
          } catch {}
          setSalesData(updatedDay);

          //          
          const bgTasks: Promise<any>[] = [];
          if (!sale.isDeferred) {
            try {
              const saleTotal = (sale.price ?? 0) * (sale.quantity ?? 0);
              const cashKey = getCashBalanceKey();
              const savedCash = localStorage.getItem(cashKey);
              const currentCash = savedCash ? parseFloat(savedCash) : 0;
              const newCash = currentCash - saleTotal;
              localStorage.setItem(cashKey, newCash.toString());
              const localDataKey = `userData_${user?.uid}`;
              const updateData = { cashBalance: newCash, lastUpdated: new Date().toISOString() } as any;
              try { localStorage.setItem(localDataKey, JSON.stringify(updateData)); } catch {}
              if (user?.uid) {
                bgTasks.push(userDataService.updateUserData(user.uid, updateData));
              }
              const shopId = (profile as any)?.shopId || shopIdCtx;
              if (shopId) {
                bgTasks.push(updateDoc(doc(db, 'dashboardData', shopId), { cashBalance: newCash }).catch(() => {}));
              }
              try { window.dispatchEvent(new CustomEvent('cashBalanceChanged', { detail: { value: newCash } })); } catch {}
            } catch (e) {
              console.warn('       :', e);
            }
          }

          //  :       
          try {
            const matched = shopProducts.find(p => (sale.barcode && p.barcode === sale.barcode) || p.name === sale.productName);
            if (matched) {
              const newQty = (matched.quantity || 0) + (sale.quantity || 0);
              setShopProducts(prev => prev.map(p => p.id === matched.id ? { ...p, quantity: newQty } : p));
              bgTasks.push(updateDoc(doc(db, 'products', matched.id), { quantity: newQty, updatedAt: serverTimestamp() }));
            }
          } catch (e) {
            console.warn('     :', e);
          }

          //     Firestore  
          try {
            if (sale.firestoreId) {
              bgTasks.push(deleteDoc(doc(db, 'dailySales', sale.firestoreId)).catch(() => {}));
              bgTasks.push(deleteDoc(doc(db, 'users', user.uid, 'dailySales', sale.firestoreId)).catch(() => {}));
            } else {
              bgTasks.push((async () => {
                try {
                  const q = query(
                    collection(db, 'dailySales'),
                    where('userId', '==', user.uid),
                    where('date', '==', sale.date),
                    where('productName', '==', sale.productName),
                    limit(5)
                  );
                  let snap = await getDocs(q);
                  if (snap.empty) {
                    const q2 = query(
                      collection(db, 'users', user.uid, 'dailySales'),
                      where('date', '==', sale.date),
                      where('productName', '==', sale.productName),
                      limit(5)
                    );
                    snap = await getDocs(q2);
                  }
                  if (!snap.empty) {
                    const target = snap.docs.find((d: any) => {
                      const data: any = d.data();
                      return Number(data?.sellingPrice ?? 0) === Number(sale.price ?? 0)
                        && Number(data?.quantity ?? 0) === Number(sale.quantity ?? 0)
                        && String(data?.time ?? '') === String(sale.time ?? '');
                    }) || snap.docs[0];
                    if (target) {
                      await deleteDoc(target.ref);
                    }
                  }
                } catch (e) {
                  console.warn('     Firestore ( ):', e);
                }
              })());
            }
          } catch (e) {
            console.warn('      Firestore:', e);
          }

          //       ( )
          try {
            if (!isMonthlyLocked) {
              setTimeout(() => { try { loadMonthlyStats(); } catch {} }, 0);
            }
          } catch {}

          //      
          try { await Promise.allSettled(bgTasks); } catch {}

          toast({ title: '  ', description: `     ${sale.productName}   ` });
        } catch (error) {
          console.error('Error deleting sale:', error);
          toast({ title: '  ', description: '   ', variant: 'destructive' });
        }
      }
    );
  };

  //    :   = 1
  const sellProductByBarcode = async (product: ShopProduct) => {
    try {
      setSellingQtyByProduct(prev => ({ ...prev, [product.id]: '1' }));
      await sellProduct(product);
    } catch (err) {
      console.error('Error selling by barcode:', err);
    }
  };

  //      
  const addPieces = async (product: ShopProduct) => {
    if (isShiftActive) {
      toast({ title: '    ', description: '     ', variant: 'destructive' });
      return;
    }
    const qtyStr = addQtyByProduct[product.id] || '';
    const qty = parseInt(qtyStr);
    if (isNaN(qty) || qty <= 0) {
      toast({ title: '  ', description: '   ', variant: 'destructive' });
      return;
    }
    requireMasterPin(
      '  ',
      '        ',
      async () => {
        try {
          const newQty = product.quantity + qty;
          await updateDoc(doc(db, 'products', product.id), { quantity: newQty, updatedAt: serverTimestamp() });
          setShopProducts(prev => prev.map(p => p.id === product.id ? { ...p, quantity: newQty } : p));
          setAddQtyByProduct(prev => ({ ...prev, [product.id]: '' }));
          toast({ title: ' ', description: `  ${qty}   ${product.name}` });
        } catch (error) {
          console.error('Error adding pieces:', error);
          toast({ title: ' ', description: '    ', variant: 'destructive' });
        }
      }
    );
  };

  //      
  const deleteProduct = async (product: ShopProduct) => {
    if (!user?.uid) return;
    if (isShiftActive) {
      toast({ title: '    ', description: '     ', variant: 'destructive' });
      return;
    }
    const ok = window.confirm(`    "${product.name}"`);
    if (!ok) return;
    requireMasterPin(
      '  ',
      '        ',
      async () => {
        try {
          await deleteDoc(doc(db, 'products', product.id));
          setShopProducts(prev => prev.filter(p => p.id !== product.id));
          toast({ title: ' ', description: `   ${product.name}` });
        } catch (error) {
          console.error('Error deleting product:', error);
          toast({ title: ' ', description: '  ', variant: 'destructive' });
        }
      }
    );
  };

  //     Firestore  
  const createProduct = async () => {
    try {
      if (!user?.uid) {
        toast({ title: '', description: '  ', variant: 'destructive' });
        return;
      }
      if (isShiftActive) {
        toast({ title: '    ', description: '     ', variant: 'destructive' });
        return;
      }
      const name = newProduct.productName.trim();
      const selling = parseFloat(newProduct.price);
      const wholesale = parseFloat(newProduct.wholesalePrice || '0');
      const qty = parseInt(newProduct.quantity);

      if (!name || isNaN(selling) || isNaN(qty)) {
        toast({ title: '  ', description: '    ', variant: 'destructive' });
        return;
      }
      //           PIN 
      //     undefined   Firestore
      const payload: any = {
        name,
        sellingPrice: selling,
        wholesalePrice: isNaN(wholesale) ? 0 : wholesale,
        quantity: isNaN(qty) ? 0 : qty,
        userId: user.uid,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
      };

      //        
      const trimmedBarcode = (newProduct.barcode || '').trim();
      if (trimmedBarcode) payload.barcode = trimmedBarcode;
      const trimmedSerial = (newProduct.serialNumber || '').trim();
      if (trimmedSerial) payload.serialNumber = trimmedSerial;

      if (profile?.shopId) {
        payload.shopId = profile.shopId;
      }

      const docRef = await addDoc(collection(db, 'products'), payload);

      setShopProducts((prev) => [
        {
          id: docRef.id,
          name,
          sellingPrice: selling,
          wholesalePrice: isNaN(wholesale) ? 0 : wholesale,
          quantity: isNaN(qty) ? 0 : qty,
          barcode: (newProduct.barcode || '').trim() || '',
          serialNumber: (newProduct.serialNumber || '').trim() || '',
        },
        ...prev,
      ]);

      setNewProduct({ productName: '', price: '', wholesalePrice: '', quantity: '', barcode: '', serialNumber: '' });

      toast({ title: ' ', description: `   ${name} ` });
    } catch (error) {
      console.error('createProduct error', error);
      toast({ title: ' ', description: '  .  ', variant: 'destructive' });
    }
  };

  //  :    
  const processBarcode = async (code: string) => {
    const trimmed = (code || '').trim();
    if (!trimmed) return;
    const product = shopProducts.find((p) => (p.barcode || '') === trimmed);
    if (!product) {
      toast({ title: '  ', description: '      ' });
      return;
    }
    await sellProductByBarcode(product);
  };

  //    
  const printSelectedInvoice = () => {
    const items = Object.entries(invoiceSelections)
      .map(([id, qty]) => {
        const q = Number(qty);
        const prod = shopProducts.find(p => p.id === id);
        if (!prod || !q || q <= 0) return null as any;
        return { name: prod.name, qty: q, price: prod.sellingPrice, total: q * prod.sellingPrice };
      })
      .filter(Boolean) as { name: string; qty: number; price: number; total: number }[];

    if (items.length === 0) {
      toast({ title: '   ', description: '      ' });
      return;
    }

    const shopName = (profile as any)?.shopName || user?.displayName || '';
    const phone = (profile as any)?.phone || user?.phoneNumber || '';
    const invoiceTotal = items.reduce((sum, it) => sum + it.total, 0);
    const dateStr = new Date().toLocaleString('ar-EG');

    const rowsHtml = items
      .map((it) => `
        <tr>
          <td style="border:1px solid #ddd;padding:6px">${it.name}</td>
          <td style="border:1px solid #ddd;padding:6px;text-align:center">${it.qty}</td>
          <td style="border:1px solid #ddd;padding:6px;text-align:center">${formatMoney(it.price)}</td>
          <td style="border:1px solid #ddd;padding:6px;text-align:center;font-weight:bold;color:#0a7">${formatMoney(it.total)}</td>
        </tr>
      `)
      .join('');

    const html = `
      <html>
        <head>
          <meta charset="utf-8" />
          <title></title>
          <style>
            body { font-family: Tahoma, Arial, sans-serif; direction: rtl; }
            .header { text-align: center; margin-bottom: 12px; }
            .header h2 { margin: 0; }
            .meta { text-align: center; color: #555; margin-bottom: 8px; }
            table { width: 100%; border-collapse: collapse; }
            .total { text-align: left; font-weight: bold; margin-top: 10px; }
          </style>
        </head>
        <body>
          <div class="header">
            <h2>${shopName}</h2>
            ${phone ? `<div class="meta"> : ${phone}</div>` : ''}
            <div class="meta">: ${dateStr}</div>
          </div>
          <table>
            <thead>
              <tr>
                <th style="border:1px solid #ddd;padding:6px"></th>
                <th style="border:1px solid #ddd;padding:6px"></th>
                <th style="border:1px solid #ddd;padding:6px"></th>
                <th style="border:1px solid #ddd;padding:6px"></th>
              </tr>
            </thead>
            <tbody>
              ${rowsHtml}
            </tbody>
          </table>
          <div class="total"> : ${formatMoney(invoiceTotal)}</div>
        </body>
      </html>
    `;

    const w = window.open('', '_blank');
    if (!w) return;
    w.document.write(html);
    w.document.close();
    w.focus();
    w.print();
    w.close();
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-none w-[100vw] h-[100dvh] p-0 overflow-hidden bg-gray-50/50">
        <div className="flex flex-col h-full" dir="rtl">
          {/* Header */}
          <div className="bg-white border-b px-3 py-2 flex items-center justify-between shrink-0 z-10 h-14 sm:h-auto">
             <div className="flex items-center gap-2">
                <div className="bg-primary/10 p-1.5 rounded-full">
                  <BarChart3 className="h-4 w-4 text-primary" />
                </div>
                <div className="flex flex-col justify-center">
                  <DialogTitle className="text-sm font-bold leading-tight"> </DialogTitle>
                  <p className="text-[10px] text-muted-foreground hidden sm:block leading-none mt-0.5">   </p>
                </div>
             </div>
             <div className="flex items-center gap-2">
                <Button variant="outline" size="sm" onClick={() => recoverUnsyncedSalesToFirestore()} className="hidden sm:flex h-7 text-[10px] px-2">
                   <RefreshCw className="h-3 w-3 ml-1.5" />
                   
                </Button>
                <Button variant="ghost" size="icon" onClick={onClose} className="rounded-full h-8 w-8">
                   <X className="h-4 w-4" />
                </Button>
             </div>
          </div>

          <div className="flex-1 overflow-hidden flex flex-col md:flex-row">
            {/* Sidebar */}
            <div className="w-full md:w-80 bg-white border-l flex flex-col overflow-y-auto shrink-0 order-2 md:order-1">
               <div className="p-4 space-y-4">
                  {/* Summary Card */}
                  <Card>
                     <CardHeader className="p-4 pb-2">
                        <CardTitle className="text-sm font-medium flex items-center gap-2">
                           <DollarSign className="h-4 w-4 text-primary" />
                            
                        </CardTitle>
                     </CardHeader>
                     <CardContent className="p-4 pt-2 space-y-3">
                        <div className="flex justify-between items-center p-3 bg-gray-50 rounded-lg border">
                           <span className="text-sm text-muted-foreground"></span>
                           <span className="text-lg font-bold text-primary">
                              {formatMoney(salesData.filter(s => !s.isDeferred).reduce((sum, item) => sum + item.price * item.quantity, 0))}
                           </span>
                        </div>
                        <div className="relative overflow-hidden p-3 bg-emerald-50/50 rounded-lg border border-emerald-100">
                           <div className={`flex justify-between items-center ${isProfitLocked ? "blur-sm" : ""}`}>
                              <span className="text-sm text-emerald-700"></span>
                              <span className="text-lg font-bold text-emerald-700">
                                 {formatMoney(salesData.filter(s => !s.isDeferred).reduce((sum, item) => sum + ((item.price - (item.wholesalePrice ?? 0)) * item.quantity), 0))}
                              </span>
                           </div>
                           {isProfitLocked && (
                              <div className="absolute inset-0 flex items-center justify-center bg-white/50">
                                 <Button size="sm" variant="outline" onClick={() => setIsProfitPinDialogOpen(true)} className="h-7 text-xs bg-white">
                                    <Lock className="h-3 w-3 ml-1" />
                                    
                                 </Button>
                              </div>
                           )}
                        </div>
                     </CardContent>
                  </Card>

                  {/* Monthly Stats Accordion */}
                  <Card className="shadow-none border-none bg-transparent">
                     <button onClick={() => setIsMonthlyCollapsed(!isMonthlyCollapsed)} className="flex items-center justify-between w-full p-2 text-sm font-medium text-gray-600 hover:text-primary transition-colors">
                        <span className="flex items-center gap-2">
                           <Calendar className="h-4 w-4" />
                            
                        </span>
                        {isMonthlyCollapsed ? <ChevronDown className="h-4 w-4" /> : <ChevronUp className="h-4 w-4" />}
                     </button>
                     {!isMonthlyCollapsed && (
                        <div className="mt-2 grid grid-cols-2 gap-2 animate-in slide-in-from-top-2">
                           <div className="col-span-2 relative">
                              <div className={`grid grid-cols-2 gap-2 ${isMonthlyLocked ? 'blur-sm select-none' : ''}`}>
                                 <div className="bg-indigo-50 p-3 rounded-xl border border-indigo-100 text-center">
                                    <span className="text-[10px] text-indigo-600 block mb-1"></span>
                                    <span className="text-sm font-bold text-indigo-700">{formatMoney(monthlySalesTotal)}</span>
                                 </div>
                                 <div className="bg-pink-50 p-3 rounded-xl border border-pink-100 text-center">
                                    <span className="text-[10px] text-pink-600 block mb-1"></span>
                                    <span className="text-sm font-bold text-pink-700">{formatMoney(monthlyProfitTotal)}</span>
                                 </div>
                              </div>
                              {isMonthlyLocked && (
                                 <div className="absolute inset-0 flex items-center justify-center z-10">
                                    <Button size="sm" variant="outline" onClick={() => setIsMasterPinDialogOpen(true)} className="h-8 bg-white/80 backdrop-blur-sm">
                                       <Lock className="h-3 w-3 ml-1" />
                                        
                                    </Button>
                                 </div>
                              )}
                           </div>
                        </div>
                     )}
                  </Card>
                  
                  <Separator />
                  
                  <Button variant="outline" className="w-full justify-start h-10" onClick={() => setIsSalesReceiptsDialogOpen(true)}>
                     <Receipt className="h-4 w-4 ml-2" />
                      
                  </Button>
               </div>
            </div>

            {/* Main Content Area */}
            <div className="flex-1 overflow-hidden flex flex-col order-1 md:order-2 bg-gray-50/30">
              <Tabs defaultValue="products" className="flex-1 flex flex-col overflow-hidden">
                <div className="px-2 py-1.5 bg-white border-b flex items-center justify-between shrink-0">
                  <TabsList className="h-8">
                    <TabsTrigger value="products" className="text-xs px-2 sm:px-3"> </TabsTrigger>
                    <TabsTrigger value="sales-log" className="text-xs px-2 sm:px-3">
                       
                      <Badge variant="secondary" className="mr-2 h-5 px-1.5 text-[10px] hidden sm:inline-flex">
                        {salesData.filter(s => !s.isDeferred).length}
                      </Badge>
                    </TabsTrigger>
                  </TabsList>

                  <div className="flex items-center gap-2 text-xs text-gray-500 font-mono" dir="ltr">
                    <span className="hidden sm:inline">{new Date().toLocaleDateString('ar-EG')}</span>
                    <Clock className="h-3.5 w-3.5 text-gray-400" />
                  </div>
                </div>

                {/* Products Tab */}
                <TabsContent value="products" className="flex-1 overflow-y-auto p-4 m-0 space-y-4">
                  {/* Add Product Card */}
                  <Card className="border-dashed border-2 border-gray-200 hover:border-gray-300 transition-all bg-gray-50/30 overflow-hidden">
                    <div 
                      className="p-4 flex items-center justify-between cursor-pointer hover:bg-gray-50 transition-colors"
                      onClick={() => setIsAddProductOpen(!isAddProductOpen)}
                    >
                       <div className="flex items-center gap-3">
                          <div className="bg-emerald-100 p-2 rounded-full">
                            <Plus className="h-5 w-5 text-emerald-600" />
                          </div>
                          <div className="text-right">
                             <h3 className="font-semibold text-gray-900 text-sm">  </h3>
                             <p className="text-xs text-gray-500">    </p>
                          </div>
                       </div>
                       <Button variant="ghost" size="sm" className="rounded-full h-8 w-8 p-0">
                         <ChevronDown className={`h-5 w-5 text-gray-400 transition-transform duration-200 ${isAddProductOpen ? 'rotate-180' : ''}`} />
                       </Button>
                    </div>
                    
                    {isAddProductOpen && (
                      <div className="px-4 pb-4 pt-0 animate-in slide-in-from-top-2">
                         <div className="h-px bg-gray-200 mb-4" />
                         <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-1.5">
                              <Label className="text-xs font-medium text-gray-700">  <span className="text-red-500">*</span></Label>
                              <Input 
                                value={newProduct.productName} 
                                onChange={(e) => setNewProduct(p => ({ ...p, productName: e.target.value }))}
                                className="h-9 bg-white"
                                placeholder=":   "
                              />
                            </div>
                            <div className="grid grid-cols-2 gap-3">
                              <div className="space-y-1.5">
                                <Label className="text-xs font-medium text-gray-700">  <span className="text-red-500">*</span></Label>
                                <Input 
                                  type="number" 
                                  value={newProduct.price} 
                                  onChange={(e) => setNewProduct(p => ({ ...p, price: e.target.value }))}
                                  className="h-9 bg-white"
                                  placeholder="0.00"
                                />
                              </div>
                              <div className="space-y-1.5">
                                <Label className="text-xs font-medium text-gray-700"> </Label>
                                <Input 
                                  type="number" 
                                  value={newProduct.wholesalePrice || ''} 
                                  onChange={(e) => setNewProduct(p => ({ ...p, wholesalePrice: e.target.value }))}
                                  className="h-9 bg-white"
                                  placeholder="0.00"
                                />
                              </div>
                            </div>
                            <div className="grid grid-cols-2 gap-3">
                              <div className="space-y-1.5">
                                <Label className="text-xs font-medium text-gray-700"> <span className="text-red-500">*</span></Label>
                                <Input 
                                  type="number" 
                                  value={newProduct.quantity} 
                                  onChange={(e) => setNewProduct(p => ({ ...p, quantity: e.target.value }))}
                                  className="h-9 bg-white"
                                  placeholder="1"
                                />
                              </div>
                              <div className="space-y-1.5">
                                <Label className="text-xs font-medium text-gray-700"> ()</Label>
                                <div className="relative">
                                  <Input 
                                    value={newProduct.barcode || ''} 
                                    onChange={(e) => setNewProduct(p => ({ ...p, barcode: e.target.value }))}
                                    className="h-9 bg-white pl-8"
                                    placeholder="Scan..."
                                  />
                                  <ScanBarcode className="absolute left-2.5 top-1/2 -translate-y-1/2 h-3.5 w-3.5 text-gray-400" />
                                </div>
                              </div>
                            </div>
                            <div className="flex items-end">
                               <Button 
                                className="w-full h-9 bg-emerald-600 hover:bg-emerald-700 text-white shadow-sm"
                                onClick={() => {
                                  if (isShiftActive) {
                                    toast({ title: ' ', description: '     ', variant: 'destructive' });
                                    return;
                                  }
                                  requireMasterPin(' ', '   ', createProduct);
                                }}
                              >
                                <Plus className="h-4 w-4 ml-2" />
                                  
                               </Button>
                            </div>
                         </div>
                      </div>
                    )}
                  </Card>

                  {/* Products Toolbar */}
                  <div className="flex flex-col sm:flex-row gap-3 items-stretch sm:items-center justify-between bg-white p-2 rounded-xl border border-gray-100 shadow-sm">
                     <div className="relative flex-1 w-full sm:max-w-md group">
                        <Search className="absolute right-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400 group-focus-within:text-primary transition-colors" />
                        <Input
                          value={productSearchTerm}
                          onChange={(e) => setProductSearchTerm(e.target.value)}
                          placeholder="   ..."
                          className="pl-3 pr-9 h-10 border-0 bg-transparent focus-visible:ring-0 placeholder:text-gray-400"
                        />
                     </div>
                     <div className="h-8 w-px bg-gray-200 hidden sm:block" />
                     <div className="flex items-center gap-2 w-full sm:w-auto">
                        <div className="relative flex-1 sm:w-48 group">
                           <ScanBarcode className="absolute right-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400 group-focus-within:text-primary transition-colors" />
                           <Input
                             value={scannedBarcode}
                             onChange={(e) => setScannedBarcode(e.target.value)}
                             onKeyDown={async (e) => {
                               if (e.key === 'Enter' && scannedBarcode.trim()) {
                                 await processBarcode(scannedBarcode.trim());
                                 setScannedBarcode('');
                               }
                             }}
                             placeholder=" ..."
                             className="pr-9 h-9 bg-gray-50 border-gray-200 focus:bg-white transition-all text-sm"
                           />
                        </div>
                        <div className="flex items-center gap-1 border-r pr-2 mr-1">
                          <Button
                            variant={isInvoiceMode ? "default" : "ghost"}
                            size="icon"
                            onClick={() => setIsInvoiceMode(!isInvoiceMode)}
                            className={`h-9 w-9 shrink-0 transition-all rounded-full ${isInvoiceMode ? 'bg-primary text-primary-foreground' : 'text-gray-500 hover:text-primary hover:bg-primary/10'}`}
                            title=" "
                          >
                            <Receipt className="h-4 w-4" />
                          </Button>
                          {isInvoiceMode && (
                             <Button 
                              variant="outline"
                              size="icon"
                              onClick={printSelectedInvoice}
                              disabled={Object.values(invoiceSelections).every(v => !v)}
                              className="h-9 w-9 shrink-0 rounded-full border-gray-200"
                              title=" "
                             >
                               <Printer className="h-4 w-4 text-gray-600" />
                             </Button>
                          )}
                        </div>
                     </div>
                  </div>

                  {/* Products Grid/List */}
                  <div className="space-y-4">
                    {filteredShopProducts.length === 0 ? (
                      <div className="flex flex-col items-center justify-center py-16 px-4 bg-white rounded-xl border border-dashed border-gray-200 text-center shadow-sm">
                        <div className="bg-gray-50 p-4 rounded-full mb-4">
                           <Package className="h-10 w-10 text-gray-300" />
                        </div>
                        <h3 className="text-lg font-semibold text-gray-900 mb-1">  </h3>
                        <p className="text-gray-500 text-sm max-w-xs mx-auto mb-4">
                                 .
                        </p>
                        <Button variant="outline" onClick={() => setIsAddProductOpen(true)} className="gap-2">
                          <Plus className="h-4 w-4" />
                            
                        </Button>
                      </div>
                    ) : (
                      <>
                        {/* Desktop Table View */}
                        <div className="hidden md:block bg-white rounded-xl border border-gray-100 shadow-sm overflow-hidden">
                           <Table>
                             <TableHeader className="bg-gray-50/80">
                               <TableRow className="hover:bg-transparent border-gray-100">
                                 <TableHead className="text-right h-11 font-semibold text-gray-700"></TableHead>
                                 <TableHead className="text-center h-11 font-semibold text-gray-700 w-28"></TableHead>
                                 <TableHead className="text-center h-11 font-semibold text-gray-700 w-28">
                                   <div className="flex items-center justify-center gap-1.5 cursor-pointer hover:text-primary transition-colors select-none" onClick={() => setIsWholesaleVisible(!isWholesaleVisible)}>
                                     <span></span>
                                     {isWholesaleVisible ? <EyeOff className="h-3.5 w-3.5" /> : <Eye className="h-3.5 w-3.5" />}
                                   </div>
                                 </TableHead>
                                 <TableHead className="text-center h-11 font-semibold text-gray-700 w-24"></TableHead>
                                 <TableHead className="text-center h-11 font-semibold text-gray-700 w-[420px]"> </TableHead>
                               </TableRow>
                             </TableHeader>
                             <TableBody>
                               {filteredShopProducts.map(product => (
                                 <TableRow key={product.id} className="hover:bg-gray-50/60 transition-colors border-gray-100">
                                   <TableCell className="font-medium py-3">
                                     <div className="text-gray-900 font-bold">{product.name}</div>
                                     {product.barcode && (
                                       <div className="flex items-center gap-1 text-[11px] text-gray-500 font-mono mt-0.5">
                                          <ScanBarcode className="h-3 w-3" />
                                          {product.barcode}
                                       </div>
                                     )}
                                   </TableCell>
                                   <TableCell className="text-center py-3">
                                      <span className="font-bold text-emerald-600 bg-emerald-50 px-2 py-1 rounded-md">{formatMoney(product.sellingPrice)}</span>
                                   </TableCell>
                                   <TableCell className="text-center py-3">
                                      <span className={`font-medium text-xs ${isWholesaleVisible ? "text-gray-600" : "blur-sm select-none text-gray-300"}`}>
                                        {formatMoney(product.wholesalePrice)}
                                      </span>
                                   </TableCell>
                                   <TableCell className="text-center py-3">
                                     <div className="flex items-center justify-center gap-1">
                                       <Badge variant={product.quantity > 0 ? "outline" : "destructive"} className={`font-mono ${product.quantity > 0 ? 'bg-gray-50 text-gray-700 border-gray-200' : ''}`}>
                                         {product.quantity}
                                       </Badge>
                                       <Button
                                          size="icon"
                                          variant="ghost"
                                          className="h-5 w-5 rounded-full text-emerald-600 hover:bg-emerald-50 hover:text-emerald-700"
                                          onClick={() => handleOpenAddQtyDialog(product)}
                                          title=" "
                                       >
                                          <Plus className="h-3 w-3" />
                                       </Button>
                                     </div>
                                   </TableCell>
                                   <TableCell className="py-3">
                                     <div className="flex items-center gap-2 justify-end">
                                        <div className="relative group">
                                           <Input 
                                             type="number" 
                                             className="h-8 w-14 text-center px-1 text-sm bg-white border-gray-200 focus:border-primary/50" 
                                             placeholder="1"
                                             value={sellingQtyByProduct[product.id] || ''}
                                             onChange={e => setSellingQtyByProduct(prev => ({ ...prev, [product.id]: e.target.value }))}
                                           />
                                           <span className="absolute -top-2 -right-1 text-[9px] bg-white px-0.5 text-gray-400 group-hover:text-primary transition-colors"></span>
                                        </div>
                                        
                                        <div className="relative group">
                                           <Input 
                                             type="number" 
                                             className="h-8 w-16 text-center px-1 bg-gray-50 border-gray-200 focus:border-primary/50 text-sm focus:bg-white transition-colors" 
                                             placeholder=""
                                             value={sellingPriceOverrideByProduct[product.id] || ''}
                                             onChange={e => setSellingPriceOverrideByProduct(prev => ({ ...prev, [product.id]: e.target.value }))}
                                             title="  "
                                           />
                                           <span className="absolute -top-2 -right-1 text-[9px] bg-white px-0.5 text-gray-400 group-hover:text-primary transition-colors"></span>
                                        </div>

                                        <Button 
                                          size="sm" 
                                          className="h-8 bg-indigo-600 hover:bg-indigo-700 text-white shadow-sm px-3 font-medium"
                                          onClick={() => sellProduct(product)}
                                        >
                                          
                                        </Button>
                                        <Button 
                                          size="sm" 
                                          variant="secondary"
                                          className="h-8 text-amber-700 bg-amber-50 hover:bg-amber-100 border border-amber-200 px-3 font-medium"
                                          onClick={() => { setPendingDeferredProduct(product); setIsDeferredFormOpen(true); }}
                                        >
                                          
                                        </Button>
                                        
                                        {isInvoiceMode && (
                                           <Input 
                                             type="number"
                                             className="h-8 w-12 text-center border-indigo-200 focus:border-indigo-400 bg-indigo-50/30"
                                             placeholder="#"
                                             value={invoiceSelections[product.id] ?? ''}
                                             onChange={(e) => {
                                                const val = parseInt(e.target.value || '0');
                                                setInvoiceSelections(prev => ({ ...prev, [product.id]: isNaN(val) ? 0 : val }));
                                             }}
                                           />
                                        )}
                                        
                                        <div className="w-px h-4 bg-gray-200 mx-1" />
                                        
                                        <Button
                                          size="icon"
                                          variant="ghost"
                                          className="h-8 w-8 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded-full"
                                          onClick={() => deleteProduct(product)}
                                          disabled={isShiftActive}
                                        >
                                          <Trash2 className="h-4 w-4" />
                                        </Button>
                                     </div>
                                   </TableCell>
                                 </TableRow>
                               ))}
                             </TableBody>
                           </Table>
                        </div>

                        {/* Mobile Cards View */}
                        <div className="md:hidden grid grid-cols-1 gap-3">
                           {filteredShopProducts.map(product => (
                             <Card key={product.id} className="overflow-hidden border-gray-100 shadow-sm hover:shadow-md transition-shadow">
                                <CardContent className="p-4 space-y-4">
                                   <div className="flex justify-between items-start">
                                      <div>
                                        <h4 className="font-bold text-gray-900 text-base">{product.name}</h4>
                                        <div className="flex flex-wrap items-center gap-2 mt-2">
                                           <Badge variant="secondary" className="bg-emerald-50 text-emerald-700 border-emerald-100">
                                              {formatMoney(product.sellingPrice)}
                                           </Badge>
                                           <span className={`text-xs text-gray-500 flex items-center gap-1 ${!isWholesaleVisible && 'blur-sm select-none'}`}>
                                              <span className="text-gray-400">:</span>
                                              {formatMoney(product.wholesalePrice)}
                                           </span>
                                           <span className="text-gray-300">|</span>
                                           <span className="text-xs text-gray-600 font-medium flex items-center gap-1">
                                              : {product.quantity}
                                              <Button
                                                size="icon"
                                                variant="ghost"
                                                className="h-5 w-5 rounded-full text-emerald-600 hover:bg-emerald-50"
                                                onClick={() => handleOpenAddQtyDialog(product)}
                                              >
                                                <Plus className="h-3 w-3" />
                                              </Button>
                                           </span>
                                        </div>
                                      </div>
                                      <Button 
                                        size="icon" 
                                        variant="ghost" 
                                        className="h-8 w-8 -mt-1 -ml-2 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded-full"
                                        onClick={() => deleteProduct(product)}
                                        disabled={isShiftActive}
                                      >
                                        <Trash2 className="h-4 w-4" />
                                      </Button>
                                   </div>
                                   
                                   <div className="flex items-center gap-2 bg-gray-50/50 p-2 rounded-lg border border-gray-100">
                                      <div className="relative flex-1">
                                        <Input 
                                           type="number" 
                                           className="h-9 w-full bg-white text-center border-gray-200 focus:border-indigo-300" 
                                           placeholder=""
                                           value={sellingQtyByProduct[product.id] || ''}
                                           onChange={e => setSellingQtyByProduct(prev => ({ ...prev, [product.id]: e.target.value }))}
                                        />
                                        <span className="absolute inset-y-0 right-2 flex items-center text-[10px] text-gray-400 pointer-events-none"></span>
                                      </div>
                                      <div className="relative w-28">
                                        <Input 
                                           type="number" 
                                           className="h-9 w-full bg-white text-center border-gray-200 focus:border-indigo-300" 
                                           placeholder=" "
                                           value={sellingPriceOverrideByProduct[product.id] || ''}
                                           onChange={e => setSellingPriceOverrideByProduct(prev => ({ ...prev, [product.id]: e.target.value }))}
                                        />
                                        <span className="absolute inset-y-0 right-2 flex items-center text-[10px] text-gray-400 pointer-events-none"></span>
                                      </div>
                                   </div>
                                   
                                   <div className="grid grid-cols-2 gap-3">
                                      <Button 
                                         className="h-10 bg-indigo-600 text-white hover:bg-indigo-700 shadow-sm font-bold"
                                         onClick={() => sellProduct(product)}
                                      >
                                         <DollarSign className="h-4 w-4 ml-2" />
                                          
                                      </Button>
                                      <Button 
                                         variant="outline"
                                         className="h-10 border-amber-200 text-amber-700 bg-amber-50 hover:bg-amber-100 font-bold"
                                         onClick={() => { setPendingDeferredProduct(product); setIsDeferredFormOpen(true); }}
                                      >
                                         <Clock className="h-4 w-4 ml-2" />
                                          
                                      </Button>
                                   </div>
                                   
                                   {isInvoiceMode && (
                                      <div className="pt-3 border-t border-dashed border-gray-200">
                                         <div className="flex items-center gap-3">
                                            <span className="text-xs font-medium text-gray-500">:</span>
                                            <Input 
                                               type="number"
                                               className="h-8 flex-1 bg-indigo-50/30 border-indigo-100"
                                               value={invoiceSelections[product.id] ?? ''}
                                               onChange={(e) => {
                                                  const val = parseInt(e.target.value || '0');
                                                  setInvoiceSelections(prev => ({ ...prev, [product.id]: isNaN(val) ? 0 : val }));
                                               }}
                                               placeholder="  "
                                            />
                                         </div>
                                      </div>
                                   )}
                                </CardContent>
                             </Card>
                           ))}
                        </div>
                      </>
                    )}
                  </div>

                </TabsContent>

                {/* Sales Log Tab */}
                <TabsContent value="sales-log" className="flex-1 overflow-y-auto p-4 m-0 space-y-4">
                  {salesData.length === 0 ? (
                    <div className="h-full flex flex-col items-center justify-center py-12 px-4 bg-gray-50/50 rounded-xl border border-dashed border-gray-200 text-center">
                       <div className="bg-white p-4 rounded-full shadow-sm mb-4">
                         <Receipt className="h-8 w-8 text-gray-400" />
                       </div>
                       <h3 className="text-lg font-semibold text-gray-900 mb-1">  </h3>
                       <p className="text-gray-500 text-sm">      .</p>
                    </div>
                  ) : (
                    <div className="space-y-4">
                       {/* Desktop Log View */}
                       <div className="hidden md:block bg-white rounded-xl border border-gray-100 shadow-sm overflow-hidden">
                          <Table>
                             <TableHeader className="bg-gray-50/80">
                                <TableRow className="hover:bg-transparent">
                                   <TableHead className="text-right h-11 font-semibold text-gray-700"></TableHead>
                                   <TableHead className="text-center h-11 font-semibold text-gray-700"></TableHead>
                                   <TableHead className="text-center h-11 font-semibold text-gray-700"></TableHead>
                                   <TableHead className="text-center h-11 font-semibold text-gray-700"></TableHead>
                                   <TableHead className="text-center h-11 font-semibold text-gray-700"></TableHead>
                                   <TableHead className="text-center h-11 font-semibold text-gray-700"></TableHead>
                                   <TableHead className="text-center h-11 font-semibold text-gray-700"></TableHead>
                                </TableRow>
                             </TableHeader>
                             <TableBody>
                                {salesData.map((sale) => (
                                   <TableRow key={sale.id} className="hover:bg-gray-50/60 transition-colors">
                                      <TableCell className="py-3">
                                         <div className="font-medium text-gray-900">{sale.productName}</div>
                                         {sale.isDeferred && <Badge variant="secondary" className="mt-1 text-[10px] bg-amber-100 text-amber-700 border-amber-200"></Badge>}
                                      </TableCell>
                                      <TableCell className="text-center py-3">{formatMoney(sale.price)}</TableCell>
                                      <TableCell className="text-center py-3">
                                        <Badge variant="outline" className="font-mono bg-gray-50">
                                          {sale.quantity}
                                        </Badge>
                                      </TableCell>
                                      <TableCell className="text-center font-bold text-green-600 py-3">
                                         {formatMoney(sale.price * sale.quantity)}
                                      </TableCell>
                                      <TableCell className="text-center text-indigo-600 py-3">
                                         <span className={isWholesaleVisible ? "" : "blur-sm select-none text-gray-300"}>
                                            {formatMoney(((sale.price ?? 0) - (sale.wholesalePrice ?? 0)) * sale.quantity)}
                                         </span>
                                      </TableCell>
                                      <TableCell className="text-center text-gray-500 text-xs py-3 font-mono">
                                         {sale.time}
                                      </TableCell>
                                      <TableCell className="py-3">
                                         <div className="flex justify-center gap-2">
                                            {sale.isDeferred && (
                                              <Button size="sm" variant="outline" className="h-8 text-xs border-amber-200 text-amber-700 bg-amber-50 hover:bg-amber-100" onClick={() => markDeferredSalePaid(sale)}>
                                                
                                              </Button>
                                            )}
                                            <Button size="sm" variant="ghost" className="h-8 w-8 p-0 text-red-500 hover:text-red-700 hover:bg-red-50 rounded-full" onClick={() => openDeleteConfirm(sale, 'return')} title="">
                                               <TrendingDown className="h-4 w-4" />
                                            </Button>
                                            <Button size="sm" variant="ghost" className="h-8 w-8 p-0 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded-full" onClick={() => openDeleteConfirm(sale, 'delete')} title=" ">
                                               <XCircle className="h-4 w-4" />
                                            </Button>
                                         </div>
                                      </TableCell>
                                   </TableRow>
                                ))}
                             </TableBody>
                          </Table>
                       </div>

                       {/* Mobile Log View */}
                       <div className="md:hidden space-y-3">
                          {salesData.map((sale) => (
                             <Card key={sale.id} className="overflow-hidden border-gray-100 shadow-sm">
                                <CardContent className="p-4">
                                  <div className="flex justify-between items-start mb-3">
                                     <div>
                                        <h4 className="font-bold text-gray-900 text-sm">{sale.productName}</h4>
                                        <div className="text-xs text-gray-500 mt-1 flex items-center gap-2">
                                           <span className="bg-gray-100 px-1.5 py-0.5 rounded text-gray-600 font-mono">{sale.time}</span>
                                           {sale.isDeferred && <Badge variant="outline" className="text-[10px] bg-amber-50 text-amber-700 border-amber-100 px-1.5 py-0 h-5"></Badge>}
                                        </div>
                                     </div>
                                     <div className="text-left">
                                        <div className="font-bold text-green-600 text-sm">{formatMoney(sale.price * sale.quantity)}</div>
                                        <div className="text-xs text-gray-400 mt-0.5">{sale.quantity}  {formatMoney(sale.price)}</div>
                                     </div>
                                  </div>
                                  
                                  <div className="flex items-center justify-end gap-2 pt-3 border-t border-gray-50">
                                     {sale.isDeferred && (
                                        <Button size="sm" variant="ghost" className="h-8 text-xs text-amber-600 bg-amber-50 hover:bg-amber-100" onClick={() => markDeferredSalePaid(sale)}>
                                          
                                        </Button>
                                     )}
                                     <Button size="sm" variant="ghost" className="h-8 text-xs text-red-600 hover:bg-red-50" onClick={() => openDeleteConfirm(sale, 'return')}>
                                        <TrendingDown className="h-3.5 w-3.5 ml-1.5" />
                                        
                                     </Button>
                                     <Button size="sm" variant="ghost" className="h-8 w-8 p-0 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded-full" onClick={() => openDeleteConfirm(sale, 'delete')}>
                                        <Trash2 className="h-4 w-4" />
                                     </Button>
                                  </div>
                                </CardContent>
                             </Card>
                          ))}
                       </div>
                    </div>
                  )}
                </TabsContent>

              </Tabs>
            </div>
          </div>
        </div>
      </DialogContent>
      <Dialog open={isDeferredFormOpen} onOpenChange={setIsDeferredFormOpen}>
        <DialogContent className="max-w-sm mx-auto bg-white border border-gray-100 shadow-xl rounded-2xl sm:max-w-md p-6 gap-5">
          <DialogHeader className="text-center space-y-2">
            <div className="mx-auto bg-amber-100 p-3 rounded-full w-fit">
              <Clock className="h-6 w-6 text-amber-600" />
            </div>
            <DialogTitle className="text-xl font-bold text-gray-900">  </DialogTitle>
            <DialogDescription className="text-gray-500 text-sm">
                    
            </DialogDescription>
          </DialogHeader>
          
          <div className="space-y-4">
            <div className="grid grid-cols-1 gap-1.5">
              <Label className="text-sm font-medium text-gray-700">  ()</Label>
              <div className="relative">
                 <select
                   className="flex h-10 w-full items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 appearance-none"
                   value={deferredCustomerId}
                   onChange={(e) => {
                     const val = e.target.value;
                     setDeferredCustomerId(val);
                     const c = customers.find(x => x.id === val);
                     if (c) {
                       setDeferredCustomerName(c.name);
                       setDeferredCustomerMobile(c.mobile || '');
                     }
                   }}
                 >
                   <option value="">--    --</option>
                   {customers.map(c => (
                     <option key={c.id} value={c.id}>{c.name}{c.mobile ? ` - ${c.mobile}` : ''}</option>
                   ))}
                 </select>
                 <ChevronDown className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400 pointer-events-none" />
              </div>
            </div>
            
            <div className="grid grid-cols-1 gap-1.5">
              <Label className="text-sm font-medium text-gray-700">  <span className="text-red-500">*</span></Label>
              <Input
                value={deferredCustomerName}
                onChange={(e) => setDeferredCustomerName(e.target.value)}
                placeholder="  ..."
                className="h-10"
              />
            </div>
            
            <div className="grid grid-cols-1 gap-1.5">
              <Label className="text-sm font-medium text-gray-700">  ()</Label>
              <Input
                value={deferredCustomerMobile}
                onChange={(e) => setDeferredCustomerMobile(e.target.value)}
                placeholder="  ..."
                className="h-10"
              />
            </div>
          </div>
          
          <div className="flex items-center gap-3 mt-2">
            <Button variant="outline" className="flex-1 h-10 border-gray-200" onClick={() => setIsDeferredFormOpen(false)}></Button>
            <Button
              className="flex-1 h-10 bg-amber-600 hover:bg-amber-700 text-white shadow-sm"
              onClick={async () => {
                try {
                  const prod = pendingDeferredProduct;
                  if (!prod) { setIsDeferredFormOpen(false); return; }
                  const name = deferredCustomerName.trim();
                  const mobile = deferredCustomerMobile.trim() || undefined;
                  const id = deferredCustomerId || undefined;
                  if (!name) { toast({ title: ' ', description: '  ', variant: 'destructive' }); return; }
                  await sellProduct(prod, true, { id, name, mobile });
                try {
                  if (user?.uid) {
                    const saleTotal = prod.sellingPrice * (parseInt(sellingQtyByProduct[prod.id] || '0') || 0);
                    const userData = await userDataService.getUserData(user.uid);
                    const list: any[] = Array.isArray(userData?.debts) ? userData.debts : [];
                    const newDebt = {
                      id: Date.now().toString(),
                      debtorName: name,
                      customerId: id || null,
                      mobile: mobile || null,
                      amount: saleTotal,
                      reason: `   ${prod.name}`,
                      status: 'pending',
                      createdAt: new Date(),
                      partialPayments: []
                    };
                    const updated = [newDebt, ...list];
                    userDataService.updateUserData(user.uid, { debts: updated }).catch(() => {});
                    try {
                      const sid = (shopIdCtx || (profile as any)?.shopId || 'main');
                      localStorage.setItem(`debts_${user.uid}_${sid}`, JSON.stringify(updated));
                      localStorage.setItem(`debts_default_${sid}`, JSON.stringify(updated));
                      localStorage.setItem(`debts_last_write_${user.uid}_${sid}`, Date.now().toString());
                    } catch {}
                    try { window.dispatchEvent(new CustomEvent('debtsUpdated', { detail: { count: updated.length } })); } catch {}
                    try { telegramNotifier.send(user.uid, `    ${newDebt.debtorName}  ${newDebt.amount} `).catch(() => {}); } catch {}
                  }
                } catch {}
                  setIsDeferredFormOpen(false);
                  setPendingDeferredProduct(null);
                } catch {}
              }}
            >
               
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      {/*     */}
      <Dialog open={isSalesReceiptsDialogOpen} onOpenChange={setIsSalesReceiptsDialogOpen}>
        <DialogContent className="sm:max-w-4xl max-h-[85vh] flex flex-col p-0 overflow-hidden gap-0">
          <div className="p-4 border-b flex items-center justify-between bg-gray-50/50">
             <div className="flex items-center gap-3">
                <div className="bg-white p-2 rounded-full border shadow-sm">
                   <Receipt className="h-5 w-5 text-indigo-600" />
                </div>
                <div>
                   <DialogTitle className="text-lg font-bold"> </DialogTitle>
                   <DialogDescription className="text-xs">    </DialogDescription>
                </div>
             </div>
             <Button variant="ghost" size="icon" onClick={() => setIsSalesReceiptsDialogOpen(false)} className="rounded-full">
                <X className="h-5 w-5" />
             </Button>
          </div>
          
          <div className="flex-1 overflow-y-auto p-4">
            {salesData.filter(s => !s.isDeferred).length === 0 ? (
              <div className="flex flex-col items-center justify-center py-12 text-center">
                 <div className="bg-gray-50 p-4 rounded-full mb-3">
                    <Receipt className="h-8 w-8 text-gray-300" />
                 </div>
                 <p className="text-gray-500 font-medium">     </p>
              </div>
            ) : (
              <>
                {/* Desktop View */}
                <div className="hidden md:block border rounded-xl overflow-hidden shadow-sm">
                  <Table>
                    <TableHeader className="bg-gray-50/80">
                      <TableRow className="hover:bg-transparent">
                        <TableHead className="text-right h-10 font-semibold text-gray-700"></TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"> </TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"> </TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"></TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"></TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"></TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"></TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"></TableHead>
                        <TableHead className="text-right h-10 font-semibold text-gray-700"></TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {salesData.filter(s => !s.isDeferred).map((item) => (
                        <TableRow key={item.id} className="hover:bg-gray-50/60">
                          <TableCell className="font-medium text-gray-900 py-3">{item.productName}</TableCell>
                          <TableCell className="text-gray-700 py-3">{formatMoney(item.price)}</TableCell>
                          <TableCell className="text-gray-700 py-3">{formatMoney(item.wholesalePrice ?? 0)}</TableCell>
                          <TableCell className="text-gray-700 py-3 font-mono">{formatNumber(item.quantity)}</TableCell>
                          <TableCell className="text-gray-700 py-3 font-bold text-emerald-600">{formatMoney((item.price ?? 0) * (item.quantity ?? 0))}</TableCell>
                          <TableCell className="text-gray-700 py-3 text-indigo-600">{formatMoney(((item.price ?? 0) - (item.wholesalePrice ?? 0)) * (item.quantity ?? 0))}</TableCell>
                          <TableCell className="text-gray-500 py-3 text-xs">{formatDate(item.date)}</TableCell>
                          <TableCell className="text-gray-500 py-3 text-xs font-mono">{item.time}</TableCell>
                          <TableCell className="text-gray-700 py-3 text-xs">{item.executedBy || '-'}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>

                {/* Mobile View */}
                <div className="md:hidden space-y-3">
                  {salesData.filter(s => !s.isDeferred).map((item) => (
                    <Card key={item.id} className="overflow-hidden border-gray-100 shadow-sm">
                      <CardContent className="p-4 space-y-3">
                         <div className="flex justify-between items-start">
                            <div>
                               <h4 className="font-bold text-gray-900 text-sm">{item.productName}</h4>
                               <div className="text-xs text-gray-500 mt-1 flex items-center gap-2">
                                  <span className="bg-gray-100 px-1.5 py-0.5 rounded text-gray-600 font-mono">{item.time}</span>
                                  <span className="text-gray-400">|</span>
                                  <span>{formatDate(item.date)}</span>
                               </div>
                            </div>
                            <div className="text-left">
                               <div className="font-bold text-emerald-600 text-sm">{formatMoney((item.price ?? 0) * (item.quantity ?? 0))}</div>
                               <div className="text-xs text-gray-400 mt-0.5">{item.quantity}  {formatMoney(item.price)}</div>
                            </div>
                         </div>
                         
                         <div className="grid grid-cols-2 gap-2 pt-3 border-t border-dashed border-gray-100 text-xs">
                            <div className="bg-gray-50 p-2 rounded flex flex-col items-center justify-center">
                               <span className="text-gray-400 mb-1"></span>
                               <span className="font-bold text-indigo-600">{formatMoney(((item.price ?? 0) - (item.wholesalePrice ?? 0)) * (item.quantity ?? 0))}</span>
                            </div>
                            <div className="bg-gray-50 p-2 rounded flex flex-col items-center justify-center">
                               <span className="text-gray-400 mb-1"></span>
                               <span className="font-medium text-gray-700 truncate max-w-full px-1">{item.executedBy || '-'}</span>
                            </div>
                         </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              </>
            )}
          </div>
          
          <div className="p-4 border-t bg-gray-50/30 flex justify-end">
            <Button variant="outline" onClick={() => setIsSalesReceiptsDialogOpen(false)} className="px-6"></Button>
          </div>
        </DialogContent>
      </Dialog>
      {/*   /    */}
      <Dialog open={isDeleteConfirmOpen} onOpenChange={setIsDeleteConfirmOpen}>
        <DialogContent className="max-w-sm mx-auto bg-white border border-gray-100 shadow-xl rounded-2xl sm:max-w-md p-6 gap-6">
          <div className="flex flex-col items-center text-center gap-2">
             <div className={`p-3 rounded-full ${deleteActionType === 'return' ? 'bg-amber-100 text-amber-600' : 'bg-red-100 text-red-600'}`}>
                {deleteActionType === 'return' ? <TrendingDown className="h-6 w-6" /> : <Trash2 className="h-6 w-6" />}
             </div>
             <DialogTitle className="text-xl font-bold text-gray-900 mt-2">
               {deleteActionType === 'return' ? ' ' : ' '}
             </DialogTitle>
             <div className="text-sm text-gray-500 space-y-1">
               <p>
                     {deleteActionType === 'return' ? '' : ''} 
                 <span className="font-bold text-gray-900 mx-1">{deleteTargetSale?.productName}</span>
                 
                 <span className="font-bold text-gray-900 mx-1 font-mono">{formatNumber(deleteTargetSale?.quantity || 0)}</span>
               </p>
               <p className="text-xs text-gray-400">    .</p>
             </div>
          </div>
          
          <div className="grid grid-cols-2 gap-3">
            <Button
              variant="outline"
              className="h-10 border-gray-200"
              onClick={() => setIsDeleteConfirmOpen(false)}
            >
              
            </Button>
            <Button
              className={`h-10 ${deleteActionType === 'return' ? 'bg-amber-600 hover:bg-amber-700' : 'bg-red-600 hover:bg-red-700'} text-white shadow-sm`}
              onClick={() => {
                const tgt = deleteTargetSale;
                setIsDeleteConfirmOpen(false);
                if (tgt) deleteSale(tgt);
              }}
            >
              {deleteActionType === 'return' ? ' ' : ' '}
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      {/*        */}
      <MasterPinDialog
        open={isProfitPinDialogOpen}
        onOpenChange={setIsProfitPinDialogOpen}
        mode="verify"
        title="  "
        description="      "
        onSuccess={() => {
          setIsProfitLocked(false);
          setIsProfitPinDialogOpen(false);
        }}
      />
      {/*         */}
      <MasterPinDialog
        open={isMasterPinDialogOpen}
        onOpenChange={setIsMasterPinDialogOpen}
        mode="verify"
        title="  "
        description="          "
        onSuccess={async () => {
          try {
            const uid = user?.uid;
            if (uid) {
              const sub = await getUserSubscription(uid);
              const rawPlan: any = (sub as any)?.planType ?? (sub as any)?.plan ?? null;
              const planStr = typeof rawPlan === 'string' ? rawPlan.toLowerCase() : null;
              const isZero = rawPlan === SubscriptionPlan.ZERO || planStr === 'zero';
              if (isZero) {
                toast({
                  title: ' ',
                  description: '      .     .',
                  variant: 'destructive',
                });
                //    
                return;
              }
            }
          } catch (e) {}
          setIsMonthlyLocked(false);
          setIsMasterPinDialogOpen(false);
          //       
          loadMonthlyStats();
        }}
      />
      {/*       (/) */}
      <MasterPinDialog
        open={isMasterPinActionOpen}
        onOpenChange={setIsMasterPinActionOpen}
        mode="verify"
        title={masterPinActionTitle}
        description={masterPinActionDescription}
        onSuccess={() => {
          const act = protectedActionRef.current;
          protectedActionRef.current = null;
          setIsMasterPinActionOpen(false);
          if (act) act();
        }}
      />

      {/*    */}
      <Dialog open={isAddQtyDialogOpen} onOpenChange={setIsAddQtyDialogOpen}>
        <DialogContent className="sm:max-w-[400px]">
          <DialogHeader>
            <DialogTitle className="text-center"> </DialogTitle>
            <DialogDescription className="text-center">
                : <span className="font-bold text-black">{productToAddQty?.name}</span>
            </DialogDescription>
          </DialogHeader>
          <div className="py-6 flex flex-col items-center gap-4">
             <div className="flex items-center gap-4 w-full justify-center">
                <Button variant="outline" size="icon" onClick={() => setQtyToAdd(prev => String(Math.max(1, parseInt(prev || '0') - 1)))}>
                   <TrendingDown className="h-4 w-4" />
                </Button>
                <Input
                  id="qty"
                  type="number"
                  value={qtyToAdd}
                  onChange={(e) => setQtyToAdd(e.target.value)}
                  className="w-32 text-center text-2xl font-bold h-12"
                  autoFocus
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      handleAddQtyConfirm();
                    }
                  }}
                />
                <Button variant="outline" size="icon" onClick={() => setQtyToAdd(prev => String(parseInt(prev || '0') + 1))}>
                   <TrendingUp className="h-4 w-4" />
                </Button>
             </div>
          </div>
          <DialogFooter className="flex gap-2 sm:justify-center">
            <Button variant="outline" onClick={() => setIsAddQtyDialogOpen(false)} className="flex-1"></Button>
            <Button onClick={handleAddQtyConfirm} className="flex-1 bg-emerald-600 hover:bg-emerald-700"> </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </Dialog>
  );
}

//  //     
const formatNumber = (n: number) => new Intl.NumberFormat('ar-EG').format(Number(n || 0));
const formatMoney = (n: number) => `${new Intl.NumberFormat('ar-EG', { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(Number(n || 0))} .`;
const formatDate = (dateStr: string) => {
  try {
    const d = new Date(dateStr);
    if (isNaN(d.getTime())) return new Date(`${dateStr}T00:00:00`).toLocaleDateString('ar-EG');
    return d.toLocaleDateString('ar-EG');
  } catch {
    return dateStr;
  }
};
