async function waitForOAuthCode(popup: Window): Promise<string> {
  return new Promise((resolve, reject) => {
    const timer = setInterval(() => {
      try {
        const url = popup.location.href;
        if (url.includes('/oauth2/callback') && url.includes('code=')) {
          const u = new URL(url);
          const code = u.searchParams.get('code');
          popup.close();
          clearInterval(timer);
          if (code) return resolve(code);
          return reject(new Error('    '));
        }
      } catch { }
      if (popup.closed) {
        clearInterval(timer);
        reject(new Error('     '));
      }
    }, 500);
  });
}

async function exchangeCodeForRefreshToken(code: string, clientId: string, redirectUri: string): Promise<{ access_token: string; refresh_token: string }> {
  const clientSecret = String((import.meta as any)?.env?.VITE_GOOGLE_OAUTH_CLIENT_SECRET || '').trim();
  const body = new URLSearchParams({
    code,
    client_id: clientId,
    grant_type: 'authorization_code',
    redirect_uri: redirectUri,
  });
  if (clientSecret) body.set('client_secret', clientSecret);
  const resp = await fetch('https://oauth2.googleapis.com/token', {
    method: 'POST',
    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
    body: body.toString(),
  });
  const json = await resp.json();
  if (!resp.ok || !json.refresh_token || !json.access_token) throw new Error('    tokens');
  return { access_token: String(json.access_token), refresh_token: String(json.refresh_token) };
}

async function fetchGoogleEmail(accessToken: string): Promise<string> {
  const resp = await fetch('https://www.googleapis.com/oauth2/v2/userinfo', {
    headers: { Authorization: `Bearer ${accessToken}` },
  });
  const json = await resp.json();
  return String(json?.email || '');
}

function GoogleBackupBar({ userId }: { userId?: string }) {
  const [loading, setLoading] = React.useState<'link' | 'backup' | 'restore' | null>(null);
  const [message, setMessage] = React.useState<string>('');

  async function linkGoogle() {
    setLoading('link');
    setMessage('');
    try {
      const clientId = String((import.meta as any)?.env?.VITE_GOOGLE_OAUTH_CLIENT_ID || '').trim();
      const redirectUri = window.location.origin + '/oauth2/callback';
      const scope = encodeURIComponent('https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/userinfo.email');
      const url = `https://accounts.google.com/o/oauth2/v2/auth?client_id=${clientId}&response_type=code&access_type=offline&prompt=consent&scope=${scope}&redirect_uri=${encodeURIComponent(redirectUri)}`;
      const popup = window.open(url, '_blank', 'width=500,height=600');
      if (!popup) throw new Error('    ');
      const code = await waitForOAuthCode(popup);
      const token = await exchangeCodeForRefreshToken(code, clientId, redirectUri);
      const email = await fetchGoogleEmail(token.access_token);
      const ok = await saveGoogleBackupToken(userId || 'current', email, token.refresh_token);
      setMessage(ok ? '   ' : '   ');
    } catch (e: any) {
      setMessage(e?.message || '  ');
    } finally {
      setLoading(null);
    }
  }

  async function backup() {
    setLoading('backup');
    setMessage('');
    const res = await backupToGoogleDrive(userId || 'current');
    setMessage(res.ok ? '   ' : (res.error || ' '));
    setLoading(null);
  }

  async function restore() {
    setLoading('restore');
    setMessage('');
    const res = await restoreFromGoogleDrive(userId || 'current');
    setMessage(res.ok ? '  ' : (res.error || ' '));
    setLoading(null);
  }

  //             
  return null;
}

