  // Helper: normalize Arabic/Persian digits to ASCII
  const normalizeDigits = (s: string) => {
    const arabicIndic = '';
    const easternIndic = '';
    return (s || '').split('').map(ch => {
      const ai = arabicIndic.indexOf(ch);
      if (ai > -1) return String(ai);
      const ei = easternIndic.indexOf(ch);
      if (ei > -1) return String(ei);
      return ch;
    }).join('');
  };

  // Prefill latest pending SMS into withdraw form
  const normalizeToLocalMsisdn = (input: string): string => {
    const digits = normalizeDigits(input || '').replace(/\D/g, '');
    if (!digits) return '';
    // Handle common Egypt formats: 201XXXXXXXXX, +201XXXXXXXXX, 010XXXXXXXXX, 011, 012, 015
    if (digits.startsWith('201') && digits.length === 12) {
      // 2010XXXXXXXX -> 010XXXXXXXX
      return '0' + digits.slice(2);
    }
    if (digits.startsWith('00201') && digits.length === 14) {
      return '0' + digits.slice(4);
    }
    if (digits.startsWith('01') && digits.length === 11) {
      return digits;
    }
    // Fallback: return original digits; matching will be by digits only
    return digits;
  };

  const prefillFromLatest = async () => {
    try {
      const res = await fetch('/api/pending_sms');
      if (!res.ok) throw new Error('   /api/pending_sms');
      const data = await res.json();
      if (!Array.isArray(data) || data.length === 0) {
        toast({ title: '  ', description: '  SMS  ' });
        return;
      }
      const s = data[0] || {};
      const rawMsisdn = (s.msisdn || s.address || '') as string;
      const localMsisdn = normalizeToLocalMsisdn(rawMsisdn);

      // Try to select wallet by msisdn
      if (localMsisdn) {
        const targetDigits = localMsisdn.replace(/\D/g, '');
        const found = wallets.find(w => normalizeDigits(w.phone || '').replace(/\D/g, '') === targetDigits);
        if (found) {
          setWalletSelection(found.id, 'prefillFromLatest');
        }
      }

      // Fill amount
      const rawAmount = (s.amount ?? s.value ?? s.amountEgp ?? '') as any;
      let nextAmount = '';
      if (typeof rawAmount === 'number') {
        nextAmount = String(rawAmount);
      } else if (typeof rawAmount === 'string' && rawAmount.trim() !== '') {
        const parsed = parseFloat(normalizeDigits(rawAmount).replace(/[^0-9.]/g, ''));
        if (!Number.isNaN(parsed)) nextAmount = String(parsed);
      } else if ((s.message || s.body)) {
        const msg = normalizeDigits(String(s.message || s.body));
        const m = msg.match(/([0-9]+(?:\.[0-9]+)?)/);
        if (m) nextAmount = m[1];
      }
      setAmount(nextAmount || '');
      setTransactionType('withdraw');
      toast({ title: '  ', description: '       SMS' });
    } catch (err: any) {
      toast({ title: '  ', description: err?.message || '  ', variant: 'destructive' });
    }
  };

