  //          
  const applyLocalDeleteEffects = async (tx: Transaction) => {
    try {
      if (!user) return;
      //          
      const cashKey = getCashBalanceKey();
      const walletKey = getWalletBalancesKey();
      const savedCash = localStorage.getItem(cashKey);
      const savedWallets = localStorage.getItem(walletKey);
      let newCashBalance = savedCash ? parseFloat(savedCash) : cashBalance;
      let newWalletBalances: Record<string, number> = savedWallets ? JSON.parse(savedWallets) : { ...walletBalances };

      const amountVal = Number((tx as any).withdrawnAmount ?? (tx as any).sentAmount ?? (tx as any).transferredAmount ?? tx.amount ?? 0);

      if ((tx as any).type === 'send' && ((tx as any).fromWallet || selectedWallet)) {
        // :      
        newCashBalance -= amountVal;
        const walletId = (tx as any).fromWallet || selectedWallet!;
        newWalletBalances[walletId] = (newWalletBalances[walletId] || 0) + amountVal;
      } else if ((tx as any).type === 'withdraw' && ((tx as any).fromWallet || selectedWallet)) {
        // :      
        newCashBalance += amountVal;
        const walletId = (tx as any).fromWallet || selectedWallet!;
        newWalletBalances[walletId] = Math.max(0, (newWalletBalances[walletId] || 0) - amountVal);
      }

      //    
      setCashBalance(newCashBalance);
      setWalletBalances(newWalletBalances);
      const timestamp = new Date().toISOString();
      localStorage.setItem(cashKey, newCashBalance.toString());
      localStorage.setItem(cashKey + '_lastUpdated', timestamp);
      localStorage.setItem(walletKey, JSON.stringify(newWalletBalances));
      localStorage.setItem(walletKey + '_lastUpdated', timestamp);

      //      Firebase ( )
      const updateData = {
        cashBalance: newCashBalance,
        walletBalances: newWalletBalances,
        lastUpdated: new Date().toISOString()
      };
      const localDataKey = `userData_${user?.uid}`;
      localStorage.setItem(localDataKey, JSON.stringify(updateData));
      try {
        await userDataService.updateUserData(user.uid, updateData);
        localStorage.removeItem(localDataKey);
        setHasLocalDataToSync(false);
      } catch (err) {
        console.warn('    Firebase   ( ):', err);
        setHasLocalDataToSync(true);
      }

      //    /
      try {
        const walletId = (tx as any).fromWallet || selectedWallet!;
        const opType = (tx as any).type === 'send' ? 'transfer' : 'withdraw';
        //  rollbackUsage        
        await walletDailyLimitsService.rollbackUsage(walletId, amountVal, opType as any, tx.createdAt);
      } catch (e) {
        console.warn('      ():', e);
      }

      //     /
      try {
        reportsService.removeTransactionEffects(tx as any, user.uid);
      } catch (e) {
        console.warn('     ():', e);
      }
    } catch (e) {
      console.warn('     :', e);
    }
  };

  //         
  const handleDeleteReceipt = async (transactionId: string) => {
    try {
      const tx = transactions.find(t => t.id === transactionId);
      if (!tx || !user) return;
      const idStr = String(transactionId);
      const refStr = String((tx as any)?.transactionId || (tx as any)?.reference || '');
      const alreadyApplied = deletedEffectsAppliedRef.current.has(idStr) || (refStr && deletedEffectsAppliedRef.current.has(refStr));

      //        (   Firestore )
      const updatedTransactions = transactions.filter(t => t.id !== transactionId);
      setTransactions(updatedTransactions);

      //    Firebase   
      try {
        await userDataService.removeUserTransaction(user.uid, transactionId);
        await auditLogService.permanentlyDeleteTransaction(transactionId, user.uid);
      } catch (e) {
        console.warn('        :', e);
      }
      //      
      if (!alreadyApplied) {
        deletedEffectsAppliedRef.current.add(idStr);
        if (refStr) deletedEffectsAppliedRef.current.add(refStr);
        await applyLocalDeleteEffects(tx);
        try { if (user?.uid) userDataService.removeLocalReceiptById(user.uid, transactionId); } catch { }
      }

      toast({
        title: '  ',
        description: '         .',
      });

      setIsReceiptOpen(false);
    } catch (err) {
      console.error('   :', err);
      toast({
        title: '   ',
        description: '     .',
        variant: 'destructive',
      });
    }
  };

  //  :     
  const handleCompleteReceipt = async (transactionId: string) => {
    try {
      const tx = transactions.find(t => t.id === transactionId);
      if (!tx || !user) return;

      //   : 5   /
      try {
        const plan = userSubscription?.subscription?.planType;
        if (plan === SubscriptionPlan.ZERO) {
          const { dailyOperationCountService } = await import('@/utils/dailyOperationCountService');
          const opType = (tx as any).type === 'send' ? 'transfer' : 'withdraw';
          const res = await dailyOperationCountService.checkAndIncrement(user.uid, opType, 5);
          if (!res.allowed) {
            toast({
              title: '    ',
              description: '   5   /.     .',
              variant: 'destructive',
            });
            return;
          }
        } else if (plan === SubscriptionPlan.TAHWEELA) {
          const { dailyOperationCountService } = await import('@/utils/dailyOperationCountService');
          const opType = (tx as any).type === 'send' ? 'transfer' : 'withdraw';
          const res = await dailyOperationCountService.checkAndIncrementCombined(user.uid, opType, 40);
          if (!res.allowed) {
            toast({
              title: '    ',
              description: '     40  / .',
              variant: 'destructive',
            });
            return;
          }
        }
      } catch (limitErr) {
        console.warn('     :', limitErr);
      }

      await updateDoc(doc(db, 'userTransactions', transactionId), {
        status: 'completed',
        confirmedAt: new Date()
      });

      setTransactions(prev => prev.map(t => t.id === transactionId ? { ...t, status: 'completed' } : t));

      toast({
        title: ' ',
        description: '   .'
      });

      setIsReceiptOpen(false);
    } catch (err) {
      console.error('   :', err);
      toast({
        title: ' ',
        description: '      .',
        variant: 'destructive'
      });
    }
  };

