  async function composeTelegramDailySummary(): Promise<string> {
    const now = new Date();
    const dateStr = new Intl.DateTimeFormat('ar-EG', { timeZone: 'Africa/Cairo', year: 'numeric', month: '2-digit', day: '2-digit' }).format(now);
    const start = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 0, 0, 0, 0);
    const end = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 23, 59, 59, 999);
    const txToday = (transactions || []).filter((t: any) => {
      const d = new Date(t.createdAt || 0);
      return d >= start && d <= end;
    });
    const totalOps = txToday.length;
    const totalWithdrawn = txToday
      .filter((t: any) => String(t.type || t.txnType || '').toLowerCase() === 'withdraw' || String(t.type || '').toLowerCase() === 'receive')
      .reduce((s: number, t: any) => s + (t.withdrawnAmount != null ? Number(t.withdrawnAmount) : Number(t.amount || 0)), 0);
    const totalSent = txToday
      .filter((t: any) => String(t.type || t.txnType || '').toLowerCase() === 'send' || String(t.type || '').toLowerCase() === 'transfer')
      .reduce((s: number, t: any) => s + (t.sentAmount != null ? Number(t.sentAmount) : Number(t.amount || 0)), 0);
    let profit = 0;
    try { profit = txToday.reduce((sum: number, t: any) => sum + reportsService.calculateTransactionProfit(t), 0); } catch { }
    let accessoriesSales = 0;
    try {
      const cairoDate = new Intl.DateTimeFormat('en-CA', { timeZone: 'Africa/Cairo' }).format(now);
      const byUser = fsQuery(collection(db, 'dailySales'), where('userId', '==', user?.uid || ''));
      const snap = await getDocs(byUser);
      const list = snap.docs.map(d => d.data() as any).filter(v => String(v.date || cairoDate) === cairoDate);
      accessoriesSales = list.reduce((s, v) => s + Number(v.sellingPrice || v.price || 0) * Number(v.quantity || 1), 0);
    } catch { }
    let totalDebts = 0;
    try {
      const ds = debts || [];
      totalDebts = ds.filter(d => String(d.status || '') === 'pending').reduce((s, d: any) => {
        const paid = Number(d.paidAmount || 0) + ((Array.isArray(d.partialPayments) ? d.partialPayments : []).reduce((pp: number, p: any) => pp + Number(p.amount || 0), 0));
        return s + Math.max(0, Number(d.amount || 0) - paid);
      }, 0);
    } catch { }
    let deficienciesText = '';
    try {
      const sid = shopIdCtx || (profile as any)?.shopId || null;
      const q = sid ? fsQuery(collection(db, 'deficiencies'), where('shopId', '==', sid)) : fsQuery(collection(db, 'deficiencies'), where('userId', '==', user?.uid || ''));
      const snap = await getDocs(q);
      const items = snap.docs.map(d => d.data() as any).filter(v => String(v.status || 'pending') === 'pending');
      const top = items.slice(0, 10);
      deficienciesText = top.map((v: any, i: number) => `${i + 1}- ${String(v.name || v.productName || '')}  ${Number(v.quantity || v.qty || 0)}`).join('\n');
    } catch { }
    const lines = [
      `  - ${dateStr}`,
      ` : ${totalOps}`,
      ` : ${totalWithdrawn} `,
      ` : ${totalSent} `,
      `: ${profit} `,
      `  : ${accessoriesSales} `,
      ` : ${totalDebts} `,
      ` :`,
      deficienciesText || '   '
    ];
    return lines.join('\n');
  }

