  const withRetry = async <T,>(fn: () => Promise<T>, retries = 2, delayMs = 700): Promise<T> => {
    let lastErr: any;
    for (let i = 0; i <= retries; i++) {
      try {
        return await fn();
      } catch (e) {
        lastErr = e;
        if (i === retries) break;
        await new Promise((r) => setTimeout(r, delayMs * Math.pow(2, i)));
      }
    }
    throw lastErr;
  };

