  // Keyboard controls: Left/Right to toggle type, Enter to confirm
  useEffect(() => {
    const onKeyDown = (e: KeyboardEvent) => {
      const target = e.target as HTMLElement | null;
      const isEditable = !!target && (target.tagName === 'INPUT' || target.tagName === 'TEXTAREA' || target.tagName === 'SELECT' || target.isContentEditable);

      // Toggle type with arrows only when not editing an input
      if (!isEditable) {
        if (e.key === 'ArrowLeft') {
          e.preventDefault();
          handleTransactionTypeChange('withdraw');
        } else if (e.key === 'ArrowRight') {
          e.preventDefault();
          handleTransactionTypeChange('transfer');
        }
      }

      // Confirm current action with Enter
      if (e.key === 'Enter') {
        const id = transactionType === 'transfer' ? 'transferSubmitButton' : 'withdrawSubmitButton';
        const btn = document.getElementById(id) as HTMLButtonElement | null;
        if (btn && !btn.disabled) {
          e.preventDefault();
          btn.click();
        }
      }
    };

    window.addEventListener('keydown', onKeyDown);
    return () => window.removeEventListener('keydown', onKeyDown);
  }, [transactionType]);

