    if (reportSearchPhone && reportSearchPhone.trim() !== '') {
      monthlyTransactions = monthlyTransactions.filter(tx =>
        (tx.senderPhone && tx.senderPhone.includes(reportSearchPhone)) ||
        (tx.receiverPhone && tx.receiverPhone.includes(reportSearchPhone))
      );
    }

