//      
export class SecurityService {
  private static readonly SECRET_KEY_BASE = 'dashboard_secret_pin';
  private static secretCache: Map<string, string> = new Map();

  //    localStorage  
  private static getSecretKey(userId?: string): string {
    return userId ? `${this.SECRET_KEY_BASE}_${userId}` : this.SECRET_KEY_BASE;
  }

  //    (  )
  static setSecretPin(pin: string, userId?: string): void {
    const encodedPin = btoa(pin); //  
    this.secretCache.set(this.getSecretKey(userId), encodedPin);
  }

  //      
  static hasSecretPin(userId?: string): boolean {
    return this.secretCache.has(this.getSecretKey(userId));
  }

  //     
  static verifySecretPin(pin: string, userId?: string): boolean {
    const savedPin = this.secretCache.get(this.getSecretKey(userId));
    if (!savedPin) return false;
    
    try {
      const decodedPin = atob(savedPin);
      return decodedPin === pin;
    } catch {
      return false;
    }
  }

  //    ( )
  static clearSecretPin(userId?: string): void {
    this.secretCache.delete(this.getSecretKey(userId));
  }
}