import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Progress } from '@/components/ui/progress';
import { Separator } from '@/components/ui/separator';
import { 
  Wallet, 
  Phone, 
  DollarSign, 
  Star, 
  CreditCard, 
  TrendingUp, 
  TrendingDown, 
  ArrowLeft, 
  IdCard, 
  Building, 
  Calendar,
  Activity,
  BarChart3,
  Clock,
  CheckCircle,
  XCircle,
  AlertCircle
} from 'lucide-react';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { transactionService } from '@/integrations/firebase/services';
import { useToast } from '@/hooks/use-toast';
import { calculateProfit } from '@/utils/commissionCalculator';

interface Wallet {
  id: string;
  name: string;
  phone: string;
  nationalId?: string;
  isDefault: boolean;
  monthlyWithdrawalLimit: number;
  monthlyTransferLimit: number;
  monthlyWithdrawalUsage?: number;
  monthlyTransferUsage?: number;
  lastResetDate?: string;
  walletProvider?: string;
}

interface Transaction {
  id: string;
  type: 'withdrawal' | 'transfer' | 'deposit';
  amount: number;
  status: 'completed' | 'pending' | 'failed';
  createdAt: string;
  description?: string;
  walletId: string;
}

interface WalletDetailsPageProps {
  wallet: Wallet | null;
  isOpen: boolean;
  onClose: () => void;
  onBack: () => void;
}

//   
const walletProviders = [
  {
    id: 'vodafone',
    name: ' ',
    ownerName: '  ',
    nationalId: '29012345678901'
  },
  {
    id: 'etisalat',
    name: ' ',
    ownerName: '  ',
    nationalId: '28506789012345'
  },
  {
    id: 'orange',
    name: ' ',
    ownerName: '  ',
    nationalId: '29309876543210'
  },
  {
    id: 'we_pay',
    name: ' ',
    ownerName: '  ',
    nationalId: '29112345678901'
  },
  {
    id: 'smart_wallet',
    name: ' ',
    ownerName: '   ',
    nationalId: '28809876543210'
  },
  {
    id: 'access_pay',
    name: ' ',
    ownerName: '  ',
    nationalId: '29456789012345'
  },
  {
    id: 'insta_pay',
    name: ' ',
    ownerName: '  ',
    nationalId: '29012345678901'
  }
];

const WalletDetailsPage: React.FC<WalletDetailsPageProps> = ({ wallet, isOpen, onClose, onBack }) => {
  const { user } = useFirebaseAuth();
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [selectedPeriod, setSelectedPeriod] = useState<'week' | 'month' | 'all'>('month');
  const { toast } = useToast();

  //  
  useEffect(() => {
    if (!wallet || !user?.uid || !isOpen) return;

    const loadTransactions = async () => {
      setIsLoading(true);
      try {
        const allTransactions = await transactionService.getByUserId(user.uid);
        const walletTransactions = allTransactions
          .filter(t => t.walletId === wallet.id)
          .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
        
        setTransactions(walletTransactions);
      } catch (error) {
        console.error('Error loading transactions:', error);
        toast({
          title: '   ',
          description: '     Firestore  .     .        .',
          variant: 'destructive'
        });
      } finally {
        setIsLoading(false);
      }
    };

    loadTransactions();
  }, [wallet, user?.uid, isOpen]);

  if (!wallet) return null;

  //      
  const getProviderInfo = (providerId: string) => {
    return walletProviders.find(p => p.id === providerId);
  };

  //     
  const getUsagePercentage = (used: number, limit: number) => {
    return limit > 0 ? Math.min((used / limit) * 100, 100) : 0;
  };

  //   
  const formatNumber = (num: number) => {
    return new Intl.NumberFormat('ar-EG').format(num);
  };

  //   
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ar-EG', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  //     
  const getFilteredTransactions = () => {
    const now = new Date();
    let startDate: Date;

    switch (selectedPeriod) {
      case 'week':
        startDate = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
        break;
      case 'month':
        startDate = new Date(now.getFullYear(), now.getMonth(), 1);
        break;
      default:
        return transactions;
    }

    return transactions.filter(t => new Date(t.createdAt) >= startDate);
  };

  //  
  const filteredTransactions = getFilteredTransactions();
  //          
  const isWithdrawTx = (t: any) => {
    const type = t.type;
    const xn = (t as any).txnType;
    return (
      type === 'withdraw' ||
      type === 'withdrawal' ||
      type === 'receive' ||
      xn === 'receive'
    );
  };
  const isSendTx = (t: any) => {
    const type = t.type;
    const xn = (t as any).txnType;
    return (
      type === 'send' ||
      type === 'transfer' ||
      xn === 'send'
    );
  };

  const totalWithdrawals = filteredTransactions
    .filter(t => isWithdrawTx(t) && t.status === 'completed')
    .reduce((sum, t) => {
      const amount = (t as any).withdrawnAmount !== undefined ? (t as any).withdrawnAmount : t.amount;
      return sum + (amount || 0);
    }, 0);
  const totalTransfers = filteredTransactions
    .filter(t => isSendTx(t) && t.status === 'completed')
    .reduce((sum, t) => {
      const amount = (t as any).sentAmount !== undefined ? (t as any).sentAmount : t.amount;
      return sum + (amount || 0);
    }, 0);
  const totalDeposits = filteredTransactions
    .filter(t => t.type === 'deposit' && t.status === 'completed')
    .reduce((sum, t) => sum + t.amount, 0);

  //       
  const completedTransactions = filteredTransactions.filter(t => t.status === 'completed');
  const operationsCount = completedTransactions.length;
  const totalProfit = completedTransactions.reduce((sum, t: any) => {
    if (isWithdrawTx(t)) {
      const amount = t.withdrawnAmount ?? t.receivedAmount ?? t.amount ?? 0;
      return sum + calculateProfit(Number(amount) || 0, 'receive');
    }
    if (isSendTx(t)) {
      const amount = t.sentAmount ?? t.transferredAmount ?? t.amount ?? 0;
      return sum + calculateProfit(Number(amount) || 0, 'send');
    }
    return sum;
  }, 0);

  const providerInfo = getProviderInfo(wallet.walletProvider || '');
  const withdrawalPercentage = getUsagePercentage(
    wallet.monthlyWithdrawalUsage || 0,
    wallet.monthlyWithdrawalLimit
  );
  const transferPercentage = getUsagePercentage(
    wallet.monthlyTransferUsage || 0,
    wallet.monthlyTransferLimit
  );

  //      
  const getTransactionIcon = (type: string) => {
    switch (type) {
      case 'withdrawal':
        return <TrendingDown className="h-4 w-4 text-red-500" />;
      case 'transfer':
        return <TrendingUp className="h-4 w-4 text-blue-500" />;
      case 'deposit':
        return <DollarSign className="h-4 w-4 text-green-500" />;
      default:
        return <Activity className="h-4 w-4 text-gray-500" />;
    }
  };

  //      
  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'completed':
        return <CheckCircle className="h-4 w-4 text-green-500" />;
      case 'pending':
        return <Clock className="h-4 w-4 text-yellow-500" />;
      case 'failed':
        return <XCircle className="h-4 w-4 text-red-500" />;
      default:
        return <AlertCircle className="h-4 w-4 text-gray-500" />;
    }
  };

  //      
  const getTransactionTypeText = (type: string) => {
    switch (type) {
      case 'withdrawal':
        return '';
      case 'transfer':
        return '';
      case 'deposit':
        return '';
      default:
        return '';
    }
  };

  //      
  const getStatusText = (status: string) => {
    switch (status) {
      case 'completed':
        return '';
      case 'pending':
        return ' ';
      case 'failed':
        return '';
      default:
        return ' ';
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-6xl max-h-[90vh] overflow-y-auto">
        <DialogHeader className="pb-4">
          <DialogTitle className="flex items-center gap-2 text-lg">
            <CreditCard className="h-5 w-5" />
             : {wallet.name}
            {wallet.isDefault && (
              <Badge variant="default" className="mr-2">
                <Star className="h-3 w-3 mr-1" />
                
              </Badge>
            )}
          </DialogTitle>
        </DialogHeader>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/*    */}
          <div className="lg:col-span-1 space-y-4">
            <Card>
              <CardHeader>
                <CardTitle className="text-sm flex items-center gap-2">
                  <IdCard className="h-4 w-4" />
                   
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-3">
                <div className="flex items-center gap-2 text-sm">
                  <Phone className="h-4 w-4 text-gray-500" />
                  <span className="font-mono">{wallet.phone}</span>
                </div>
                
                {wallet.nationalId && (
                  <div className="flex items-center gap-2 text-sm">
                    <IdCard className="h-4 w-4 text-gray-500" />
                    <span className="font-mono">{wallet.nationalId}</span>
                  </div>
                )}
                
                {providerInfo && (
                  <div className="space-y-2">
                    <div className="flex items-center gap-2 text-sm">
                      <Building className="h-4 w-4 text-gray-500" />
                      <span>{providerInfo.name}</span>
                    </div>
                    <div className="text-xs text-gray-600 pr-6">
                      <div>: {providerInfo.ownerName}</div>
                      <div className="font-mono"> : {providerInfo.nationalId}</div>
                    </div>
                  </div>
                )}

                {wallet.lastResetDate && (
                  <div className="flex items-center gap-2 text-sm">
                    <Calendar className="h-4 w-4 text-gray-500" />
                    <span>  : {new Date(wallet.lastResetDate).toLocaleDateString('ar-EG')}</span>
                  </div>
                )}
              </CardContent>
            </Card>

            {/*   */}
            <Card>
              <CardHeader>
                <CardTitle className="text-sm flex items-center gap-2">
                  <BarChart3 className="h-4 w-4" />
                    
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {/*   */}
                <div className="space-y-2">
                  <div className="flex items-center justify-between text-xs">
                    <span className="flex items-center gap-1">
                      <TrendingDown className="h-3 w-3 text-red-500" />
                       
                    </span>
                    <span className="font-mono">
                      {formatNumber(wallet.monthlyWithdrawalUsage || 0)} / {formatNumber(wallet.monthlyWithdrawalLimit)}
                    </span>
                  </div>
                  <Progress 
                    value={withdrawalPercentage} 
                    className="h-2"
                  />
                  <div className="text-xs text-gray-600">
                    : {formatNumber(wallet.monthlyWithdrawalLimit - (wallet.monthlyWithdrawalUsage || 0))} 
                  </div>
                </div>

                <Separator />

                {/*   */}
                <div className="space-y-2">
                  <div className="flex items-center justify-between text-xs">
                    <span className="flex items-center gap-1">
                      <TrendingUp className="h-3 w-3 text-green-500" />
                       
                    </span>
                    <span className="font-mono">
                      {formatNumber(wallet.monthlyTransferUsage || 0)} / {formatNumber(wallet.monthlyTransferLimit)}
                    </span>
                  </div>
                  <Progress 
                    value={transferPercentage} 
                    className="h-2"
                  />
                  <div className="text-xs text-gray-600">
                    : {formatNumber(wallet.monthlyTransferLimit - (wallet.monthlyTransferUsage || 0))} 
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/*   */}
          <div className="lg:col-span-2 space-y-4">
            {/*   */}
            <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
              <Card>
                <CardContent className="p-4 text-center">
                  <div className="flex items-center justify-center mb-2">
                    <TrendingDown className="h-5 w-5 text-red-500" />
                  </div>
                  <div className="text-lg font-bold">{formatNumber(totalWithdrawals)}</div>
                  <div className="text-xs text-gray-600"> </div>
                </CardContent>
              </Card>
              
              <Card>
                <CardContent className="p-4 text-center">
                  <div className="flex items-center justify-center mb-2">
                    <TrendingUp className="h-5 w-5 text-blue-500" />
                  </div>
                  <div className="text-lg font-bold">{formatNumber(totalTransfers)}</div>
                  <div className="text-xs text-gray-600"> </div>
                </CardContent>
              </Card>
              
              <Card>
                <CardContent className="p-4 text-center">
                  <div className="flex items-center justify-center mb-2">
                    <DollarSign className="h-5 w-5 text-green-500" />
                  </div>
                  <div className="text-lg font-bold">{formatNumber(totalDeposits)}</div>
                  <div className="text-xs text-gray-600"> </div>
                </CardContent>
              </Card>

              {/*   */}
              <Card>
                <CardContent className="p-4 text-center">
                  <div className="flex items-center justify-center mb-2">
                    <BarChart3 className="h-5 w-5 text-emerald-600" />
                  </div>
                  <div className="text-lg font-bold">{formatNumber(totalProfit)}</div>
                  <div className="text-xs text-gray-600"> </div>
                </CardContent>
              </Card>

              {/*   */}
              <Card>
                <CardContent className="p-4 text-center">
                  <div className="flex items-center justify-center mb-2">
                    <Activity className="h-5 w-5 text-purple-600" />
                  </div>
                  <div className="text-lg font-bold">{formatNumber(operationsCount)}</div>
                  <div className="text-xs text-gray-600"> </div>
                </CardContent>
              </Card>
            </div>

            {/*    */}
            <Card>
              <CardHeader>
                <CardTitle className="text-sm flex items-center justify-between">
                  <span className="flex items-center gap-2">
                    <Activity className="h-4 w-4" />
                     
                  </span>
                  <div className="flex gap-2">
                    <Button
                      size="sm"
                      variant={selectedPeriod === 'week' ? 'default' : 'outline'}
                      onClick={() => setSelectedPeriod('week')}
                      className="text-xs"
                    >
                      
                    </Button>
                    <Button
                      size="sm"
                      variant={selectedPeriod === 'month' ? 'default' : 'outline'}
                      onClick={() => setSelectedPeriod('month')}
                      className="text-xs"
                    >
                      
                    </Button>
                    <Button
                      size="sm"
                      variant={selectedPeriod === 'all' ? 'default' : 'outline'}
                      onClick={() => setSelectedPeriod('all')}
                      className="text-xs"
                    >
                      
                    </Button>
                  </div>
                </CardTitle>
              </CardHeader>
              <CardContent>
                {isLoading ? (
                  <div className="flex justify-center items-center py-8">
                    <div className="text-center">
                      <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600 mx-auto mb-2"></div>
                      <p className="text-sm text-gray-600">  ...</p>
                    </div>
                  </div>
                ) : filteredTransactions.length === 0 ? (
                  <div className="text-center py-8">
                    <Activity className="h-8 w-8 text-gray-400 mx-auto mb-2" />
                    <p className="text-gray-600">     </p>
                  </div>
                ) : (
                  <div className="space-y-2 max-h-96 overflow-y-auto">
                    {filteredTransactions.map((transaction) => (
                      <div key={transaction.id} className="flex items-center justify-between p-3 border rounded-lg">
                        <div className="flex items-center gap-3">
                          {getTransactionIcon(transaction.type)}
                          <div>
                            <div className="text-sm font-medium">
                              {getTransactionTypeText(transaction.type)} - {formatNumber(transaction.amount)} 
                            </div>
                            <div className="text-xs text-gray-600">
                              {formatDate(transaction.createdAt)}
                            </div>
                            {transaction.description && (
                              <div className="text-xs text-gray-500 mt-1">
                                {transaction.description}
                              </div>
                            )}
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          {getStatusIcon(transaction.status)}
                          <span className="text-xs">{getStatusText(transaction.status)}</span>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>

        <div className="flex justify-between pt-4 border-t">
          <Button onClick={onBack} variant="outline">
            <ArrowLeft className="h-4 w-4 mr-2" />
             
          </Button>
          <Button onClick={onClose} variant="outline">
            
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default WalletDetailsPage;