import { getFunctions, httpsCallable } from 'firebase/functions';
import { functions } from '../../firebase';
import { Transaction } from './types';

//      
interface GetLastTransactionsData {
  limit?: number;
  walletId?: string;
  lineId?: string;
  merchantUserId?: string;
}

//      
interface GetLastTransactionsResult {
  transactions: Transaction[];
}

//    
interface CancelTransactionData {
  transactionId: string;
  reason: string;
  cancelledBy: string;
}

interface UpdateTransactionStatusData {
  transactionId: string;
  status: 'pending' | 'completed' | 'failed' | 'cancelled';
  updatedBy: string;
  note?: string;
}

//    
interface CancelTransactionResult {
  success: boolean;
  message?: string;
}

interface UpdateTransactionStatusResult {
  success: boolean;
  message?: string;
}

//   Functions  
// const functions = getFunctions(app); //    firebase.ts

//    
interface RecordTransactionData {
  lineId: string;
  walletId: string;
  txnType: 'send' | 'receive';
  amount: number;
  senderMsisdn?: string;
  receiverMsisdn?: string;
  reference?: string;
  fee?: number;
}

//    
interface RecordTransactionResult {
  id: string;
  shopId: string;
  lineId: string;
  walletId: string;
  merchantUserId: string;
  provider: string;
  txnType: 'send' | 'receive';
  amount: number;
  commission: number;
  profit: number;
  sender_msisdn: string;
  receiver_msisdn: string;
  reference: string | null;
  status: 'confirmed';
  createdAt: string;
  fee?: number;
}

/**
 *     Cloud Function
 * @param data  
 * @returns   
 */
export const recordTransaction = async (data: RecordTransactionData): Promise<RecordTransactionResult> => {
  try {
    //   Cloud Function
    const recordTransactionFn = httpsCallable<RecordTransactionData, RecordTransactionResult>(
      functions,
      'recordTransaction'
    );

    //  
    const result = await recordTransactionFn(data);
    
    //  
    return result.data;
  } catch (error: any) {
    console.error('Error recording transaction:', error);
    
    //      
    if (error.code === 'functions/unauthenticated') {
      throw new Error('    ');
    } else if (error.code === 'functions/invalid-argument') {
      throw new Error('   : ' + error.message);
    } else if (error.code === 'functions/resource-exhausted') {
      throw new Error('  : ' + error.message);
    } else if (error.code === 'functions/failed-precondition') {
      throw new Error('   : ' + error.message);
    } else {
      throw new Error('    : ' + (error.message || '  '));
    }
  }
};

/**
 *    Cloud Function
 * @param data   
 * @returns   
 */
export const cancelTransaction = async (data: CancelTransactionData): Promise<CancelTransactionResult> => {
  try {
    //   Cloud Function
    const cancelTransactionFn = httpsCallable<CancelTransactionData, CancelTransactionResult>(
      functions,
      'cancelTransaction'
    );

    //  
    const result = await cancelTransactionFn(data);
    
    //  
    return result.data;
  } catch (error: any) {
    console.error('Error cancelling transaction:', error);
    
    //      
    if (error.code === 'functions/unauthenticated') {
      throw new Error('    ');
    } else if (error.code === 'functions/invalid-argument') {
      throw new Error('   : ' + error.message);
    } else if (error.code === 'functions/not-found') {
      throw new Error('  : ' + error.message);
    } else if (error.code === 'functions/permission-denied') {
      throw new Error('     : ' + error.message);
    } else {
      throw new Error('    : ' + (error.message || '  '));
    }
  }
};

