export const sendTelegramMessage = async (data: { chatId: string; text: string }): Promise<{ ok: boolean }> => {
  const fn = httpsCallable<typeof data, { ok: boolean }>(functions, 'sendTelegramMessage');
  const res = await fn(data);
  return res.data;
};

export const sendTelegramDailySummary = async (data: { userId?: string; chatId: string; text: string }): Promise<{ ok: boolean }> => {
  return sendTelegramMessage({ chatId: data.chatId, text: data.text });
};
