import React from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { DollarSign, Package, ArrowRightLeft } from 'lucide-react';
import { db } from '@/firebase';
import { useShop } from '@/hooks/useShop';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { collection, getDocs, query, where, doc, updateDoc, getDoc } from 'firebase/firestore';
import { reportsService } from '@/utils/reportsService';
import { Button } from '@/components/ui/button';
import { useToast } from '@/hooks/use-toast';
import { MasterPinDialog } from '@/components/MasterPinDialog';
import { userDataService } from '@/integrations/firebase/services';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';

interface CashSummaryDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  userId?: string | null;
  cashBalance: number;
  walletBalances: Record<string, number>;
  transactions: any[];
}

export const CashSummaryDialog: React.FC<CashSummaryDialogProps> = ({ open, onOpenChange, userId, cashBalance, walletBalances, transactions }) => {
  const [accessorySalesToday, setAccessorySalesToday] = React.useState(0);
  const [ackAccessoryToday, setAckAccessoryToday] = React.useState(0);
  const [maintenanceTotalToday, setMaintenanceTotalToday] = React.useState(0);
  const [ackMaintenanceToday, setAckMaintenanceToday] = React.useState(0);
  const [localCashBalance, setLocalCashBalance] = React.useState<number>(cashBalance || 0);
  const [isReceiptPinOpen, setIsReceiptPinOpen] = React.useState(false);
  const [pendingReceiptType, setPendingReceiptType] = React.useState<'accessory' | 'maintenance' | null>(null);
  const { toast } = useToast();
  const { shopId: shopIdCtx } = useShop() || ({} as any);
  const { profile } = useFirebaseAuth();
  const [isDeductReasonOpen, setIsDeductReasonOpen] = React.useState(false);
  const [deductReason, setDeductReason] = React.useState('');
  const [isDeductPinOpen, setIsDeductPinOpen] = React.useState(false);
  const [isDeductAmountOpen, setIsDeductAmountOpen] = React.useState(false);
  const [deductAmount, setDeductAmount] = React.useState('');

  const getCashBalanceKey = () => `cashBalance_${userId || 'default'}_${(shopIdCtx || (profile as any)?.shopId || 'main')}`;
  const getGenericCashKey = () => `cashBalance_${userId || 'default'}`;

  const formatCurrency = (n: number) => `${new Intl.NumberFormat('ar-EG').format(Number(n || 0))} `;

  React.useEffect(() => {
    const loadAccessorySales = async () => {
      try {
        if (!userId) { setAccessorySalesToday(0); return; }
        const d = new Date();
        const y = d.getFullYear();
        const m = String(d.getMonth() + 1).padStart(2, '0');
        const dd = String(d.getDate()).padStart(2, '0');
        const todayStr = `${y}-${m}-${dd}`;
        let snap;
        try {
          const q = query(
            collection(db, 'dailySales'),
            where('userId', '==', userId),
            ...(shopIdCtx ? [where('shopId', '==', shopIdCtx)] : []),
            where('date', '==', todayStr)
          );
          snap = await getDocs(q);
        } catch {
          const q2 = query(
            collection(db, 'users', userId, 'dailySales'),
            ...(shopIdCtx ? [where('shopId', '==', shopIdCtx)] : []),
            where('date', '==', todayStr)
          );
          snap = await getDocs(q2);
        }
        let total = 0;
        snap.forEach(docSnap => {
          const d: any = docSnap.data();
          const price = Number(d.sellingPrice ?? 0);
          const qty = Number(d.quantity ?? 0);
          if (price > 0 && qty > 0) {
            total += price * qty;
          }
        });
        setAccessorySalesToday(total);
      } catch (e) {
        console.warn('    :', e);
      }
    };

    const loadMaintenanceTotal = async () => {
      try {
        if (!userId) { setMaintenanceProfitToday(0); return; }
        const d = new Date();
        const start = new Date(d.getFullYear(), d.getMonth(), d.getDate(), 0, 0, 0, 0);
        const end = new Date(d.getFullYear(), d.getMonth(), d.getDate(), 23, 59, 59, 999);
        let snap;
        try {
          const q = query(
            collection(db, 'maintenance_items'),
            where('userId', '==', userId),
            where('status', '==', 'completed')
          );
          snap = await getDocs(q);
        } catch (e) {
          snap = await getDocs(query(collection(db, 'maintenance_items'), where('userId', '==', userId)));
        }
        let totalAmount = 0;
        snap.forEach(docSnap => {
          const d: any = docSnap.data();
          if ((d.status || 'in_progress') !== 'completed') return;
          const completedAt = d.completedAt?.toDate?.();
          if (!completedAt) return;
          if (completedAt.getTime() >= start.getTime() && completedAt.getTime() <= end.getTime()) {
            const repair = Number(d.repairPrice || 0);
            if (Number.isFinite(repair) && repair > 0) totalAmount += repair;
          }
        });
        setMaintenanceTotalToday(Math.max(0, totalAmount));
      } catch (e) {
        console.warn('     :', e);
      }
    };

    

    const loadDailyReceipts = async () => {
      try {
        if (!userId) { setAckAccessoryToday(0); return; }
        const data = await userDataService.getUserData(userId);
        const todayStr = new Date().toISOString().slice(0, 10);
        const r = data?.dailyReceipts || {};
        if (r?.date && String(r.date).slice(0,10) === todayStr) {
          setAckAccessoryToday(Number(r.accessory || 0));
          setAckMaintenanceToday(Number(r.maintenance || 0));
        } else {
          setAckAccessoryToday(0);
          setAckMaintenanceToday(0);
        }
        try {
          const sid = (shopIdCtx || (profile as any)?.shopId || 'main');
          const snap = await getDoc(doc(db, 'dashboardData', sid));
          if (snap.exists()) {
            const v = Number((snap.data() as any)?.cashBalance || 0);
            setLocalCashBalance(v);
            try { localStorage.setItem(`cashBalance_${userId}`, String(v)); localStorage.setItem(getCashBalanceKey(), String(v)); } catch {}
          } else {
            setLocalCashBalance(Number(data?.cashBalance || cashBalance || 0));
          }
        } catch {
          setLocalCashBalance(Number(data?.cashBalance || cashBalance || 0));
        }
      } catch (e) {
        setAckAccessoryToday(0);
        setAckMaintenanceToday(0);
      }
    };

    if (open) {
      loadAccessorySales();
      loadMaintenanceTotal();
      loadDailyReceipts();
    }
  }, [open, userId, transactions, cashBalance, shopIdCtx]);

  React.useEffect(() => {
    const handler = (e: any) => {
      try {
        const v = Number(e?.detail?.value);
        if (Number.isFinite(v)) {
          setLocalCashBalance(v);
        }
      } catch {}
    };
    window.addEventListener('cashBalanceChanged', handler as EventListener);
    return () => window.removeEventListener('cashBalanceChanged', handler as EventListener);
  }, [open]);

  React.useEffect(() => {
    if (!open || !userId) return;
    const sid = (shopIdCtx || (profile as any)?.shopId || 'main');
    //  onSnapshot  getDoc  
    const fetchDashboardData = async () => {
      try {
        const snap = await getDoc(doc(db, 'dashboardData', sid));
        if (snap.exists()) {
          const v = Number((snap.data() as any)?.cashBalance || 0);
          setLocalCashBalance(v);
          try { 
            localStorage.setItem(`cashBalance_${userId}`, String(v)); 
            localStorage.setItem(getCashBalanceKey(), String(v)); 
          } catch {}
        }
      } catch (e) {
        console.warn('Error fetching dashboard data:', e);
      }
    };
    
    fetchDashboardData();
    
    return () => { };
  }, [open, userId, shopIdCtx]);

  const effectiveAccessoryToday = Math.max(0, Number(accessorySalesToday) - Number(ackAccessoryToday));
  const effectiveMaintenanceToday = Math.max(0, Number(maintenanceTotalToday) - Number(ackMaintenanceToday));

  const handleReceipt = async (type: 'accessory' | 'maintenance') => {
    if (!userId) return;
    const todayStr = new Date().toISOString().slice(0, 10);
    try {
      const amountToAck = type === 'accessory' ? effectiveAccessoryToday : effectiveMaintenanceToday;
      if (amountToAck <= 0) {
        toast({ title: '    ', variant: 'default' });
        return;
      }
      const newCash = Math.max(0, Number(localCashBalance) - amountToAck);
      setLocalCashBalance(newCash);
      if (type === 'accessory') {
        setAckAccessoryToday(prev => prev + amountToAck);
      } else {
        setAckMaintenanceToday(prev => prev + amountToAck);
      }
      await userDataService.updateUserData(userId, {
        cashBalance: newCash,
        dailyReceipts: {
          date: todayStr,
          accessory: type === 'accessory' ? (ackAccessoryToday + amountToAck) : ackAccessoryToday,
          maintenance: type === 'maintenance' ? (ackMaintenanceToday + amountToAck) : ackMaintenanceToday,
        }
      });
      try {
        const sid = (shopIdCtx || (profile as any)?.shopId || 'main');
        await updateDoc(doc(db, 'dashboardData', sid), { cashBalance: newCash, lastUpdated: new Date().toISOString() } as any);
      } catch { }
      try {
        localStorage.setItem(`cashBalance_${userId}`, String(newCash));
        localStorage.setItem(getCashBalanceKey(), String(newCash));
      } catch {}
      try {
        window.dispatchEvent(new CustomEvent('cashBalanceChanged', { detail: { value: newCash } }));
      } catch {}
      toast({ title: ' ', description: `  ${amountToAck.toFixed(2)}     ${type === 'accessory' ? '' : ''}` });
    } catch (e) {
      toast({ title: '', description: '    ', variant: 'destructive' });
    }
  };

  const startDeductionFlow = () => {
    setDeductReason('');
    setDeductAmount('');
    setIsDeductReasonOpen(true);
  };

  const confirmDeductReason = () => {
    if (!deductReason.trim()) {
      toast({ title: '  ', variant: 'destructive' });
      return;
    }
    setIsDeductReasonOpen(false);
    setIsDeductPinOpen(true);
  };

  const openDeductAmount = () => {
    setIsDeductPinOpen(false);
    setIsDeductAmountOpen(true);
  };

  const performDeduction = async () => {
    try {
      if (!userId) return;
      const amt = Math.max(0, Number(deductAmount || 0));
      if (!Number.isFinite(amt) || amt <= 0) {
        toast({ title: '  ', description: '      ', variant: 'destructive' });
        return;
      }
      const newCash = Math.max(0, Number(localCashBalance) - amt);
      setLocalCashBalance(newCash);
      await userDataService.updateUserData(userId, { cashBalance: newCash });
      try {
        const sid = (shopIdCtx || (profile as any)?.shopId || 'main');
        await updateDoc(doc(db, 'dashboardData', sid), { cashBalance: newCash, lastUpdated: new Date().toISOString() } as any);
      } catch { }
      try { localStorage.setItem(`cashBalance_${userId}`, String(newCash)); } catch {}
      try {
        const timestamp = new Date().toISOString();
        localStorage.setItem(getCashBalanceKey(), String(newCash));
        localStorage.setItem(getCashBalanceKey() + '_lastUpdated', timestamp);
      } catch {}

      const tx = {
        title: '    ',
        type: 'cash_deduction',
        amount: amt,
        status: 'completed',
        createdAt: new Date(),
        note: deductReason,
        cashierName: ' '
      };
      try { await userDataService.saveUserTransaction(userId, tx); } catch {}

      toast({ title: ' ', description: `  ${amt.toFixed(2)}  ` });
      setIsDeductAmountOpen(false);
      setDeductAmount('');
      setDeductReason('');
    } catch (e) {
      toast({ title: ' ', description: '   ', variant: 'destructive' });
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-lg animate-in fade-in-90 zoom-in-95 slide-in-from-top-4" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-right">
            <DollarSign className="h-5 w-5 text-emerald-600" />
              
          </DialogTitle>
        </DialogHeader>

        <div className="space-y-3">
          <div className="flex items-center justify-between bg-amber-50 border border-amber-200 rounded-lg p-2">
            <div className="flex items-center gap-2 text-amber-800 font-bold text-sm">
              <DollarSign className="h-4 w-4" />
                 ()
            </div>
            <div className="text-amber-700 font-extrabold text-sm">
              {formatCurrency(localCashBalance)}
            </div>
          </div>

          <div className="flex items-center justify-between bg-blue-50 border border-blue-200 rounded-lg p-2">
            <div className="flex items-center gap-2 text-blue-800 font-bold text-sm">
              <Package className="h-4 w-4" />
               
            </div>
            <div className="flex items-center gap-2">
              <div className="text-blue-700 font-extrabold text-sm">
                {formatCurrency(effectiveAccessoryToday)}
              </div>
              <Button
                size="sm"
                variant="ghost"
                className="h-6 px-2 rounded-lg bg-gradient-to-r from-blue-100 to-blue-200 text-blue-700 hover:from-blue-200 hover:to-blue-300 hover:text-blue-800"
                onClick={() => { setPendingReceiptType('accessory'); setIsReceiptPinOpen(true); }}
              >
                 
              </Button>
            </div>
          </div>

          <div className="flex items-center justify-between bg-emerald-50 border border-emerald-200 rounded-lg p-2">
            <div className="flex items-center gap-2 text-emerald-800 font-bold text-sm">
              <ArrowRightLeft className="h-4 w-4" />
               
            </div>
            <div className="flex items-center gap-2">
              <div className="text-emerald-700 font-extrabold text-sm">
                {formatCurrency(effectiveMaintenanceToday)}
              </div>
              <Button
                size="sm"
                variant="ghost"
                className="h-6 px-2 rounded-lg bg-gradient-to-r from-emerald-100 to-emerald-200 text-emerald-700 hover:from-emerald-200 hover:to-emerald-300 hover:text-emerald-800"
                onClick={() => { setPendingReceiptType('maintenance'); setIsReceiptPinOpen(true); }}
              >
                 
              </Button>
            </div>
          </div>

          

          <div className="flex items-center justify-between bg-red-50 border border-red-200 rounded-lg p-2">
            <div className="flex items-center gap-2 text-red-800 font-bold text-sm">
                  
            </div>
            <div className="flex items-center gap-2">
              <Button
                size="sm"
                variant="ghost"
                className="h-6 px-2 rounded-lg bg-gradient-to-r from-red-100 to-red-200 text-red-700 hover:from-red-200 hover:to-red-300 hover:text-red-800"
                onClick={startDeductionFlow}
              >
                
              </Button>
            </div>
          </div>
        </div>

        {/*       */}
        <MasterPinDialog
          open={isReceiptPinOpen}
          onOpenChange={setIsReceiptPinOpen}
          mode="verify"
          title="     "
          description="       " 
          onSuccess={() => {
            if (pendingReceiptType) {
              handleReceipt(pendingReceiptType);
              setPendingReceiptType(null);
            }
            setIsReceiptPinOpen(false);
          }}
        />

        <Dialog open={isDeductReasonOpen} onOpenChange={setIsDeductReasonOpen}>
          <DialogContent className="sm:max-w-sm">
            <DialogHeader>
              <DialogTitle> </DialogTitle>
            </DialogHeader>
            <div className="space-y-2">
              <Label htmlFor="deduct-reason">  </Label>
              <Input id="deduct-reason" value={deductReason} onChange={(e) => setDeductReason(e.target.value)} placeholder=" " />
            </div>
            <div className="flex justify-end gap-2 mt-3">
              <Button variant="outline" onClick={() => setIsDeductReasonOpen(false)}></Button>
              <Button onClick={confirmDeductReason} className="bg-red-600 hover:bg-red-700"></Button>
            </div>
          </DialogContent>
        </Dialog>

        <MasterPinDialog
          open={isDeductPinOpen}
          onOpenChange={setIsDeductPinOpen}
          mode="verify"
          title="    "
          description="      "
          onSuccess={openDeductAmount}
        />

        <Dialog open={isDeductAmountOpen} onOpenChange={setIsDeductAmountOpen}>
          <DialogContent className="sm:max-w-sm">
            <DialogHeader>
              <DialogTitle> </DialogTitle>
            </DialogHeader>
            <div className="space-y-2">
              <Label htmlFor="deduct-amount">  </Label>
              <Input id="deduct-amount" type="number" value={deductAmount} onChange={(e) => setDeductAmount(e.target.value)} placeholder="0.00" />
            </div>
            <div className="flex justify-end gap-2 mt-3">
              <Button variant="outline" onClick={() => setIsDeductAmountOpen(false)}></Button>
              <Button onClick={performDeduction} className="bg-red-600 hover:bg-red-700"> </Button>
            </div>
          </DialogContent>
        </Dialog>
      </DialogContent>
    </Dialog>
  );
};

export default CashSummaryDialog;
