import { walletDailyLimitsService } from './walletDailyLimitsService';

export interface WalletBalance {
  [walletId: string]: number;
}

export interface TransactionResult {
  success: boolean;
  newCashBalance: number;
  newWalletBalances: WalletBalance;
  message: string;
  error?: string;
}

export interface TransferOperation {
  amount: number;
  currentCashBalance: number;
  currentWalletBalances: WalletBalance;
  wallets: Array<{ id: string; name: string; isDefault?: boolean }>;
  selectedWalletId?: string; //    
  //  :         
  skipRemoteLimitsCheck?: boolean;
  nonBlockingUsageUpdate?: boolean;
}

export interface WithdrawOperation {
  amount: number;
  currentCashBalance: number;
  currentWalletBalances: WalletBalance;
  wallets: Array<{ id: string; name: string; isDefault?: boolean }>;
  selectedWalletId?: string; //    
  //  :         
  skipRemoteLimitsCheck?: boolean;
  nonBlockingUsageUpdate?: boolean;
}

export class WalletCashService {
  
  /**
   *  :       
   *      
   * @param operation   
   * @returns  
   */
  static async processTransfer(operation: TransferOperation): Promise<TransactionResult> {
    const { amount, currentCashBalance, currentWalletBalances, wallets, selectedWalletId } = operation;
    
    //    
    if (amount <= 0) {
      return {
        success: false,
        newCashBalance: currentCashBalance,
        newWalletBalances: currentWalletBalances,
        message: "      ",
        error: "INVALID_AMOUNT"
      };
    }

    //       
    if (selectedWalletId && !operation.skipRemoteLimitsCheck) {
      try {
        const limitCheck = await walletDailyLimitsService.canPerformOperation(selectedWalletId, amount, 'transfer');
        if (!limitCheck.canPerform) {
          return {
            success: false,
            newCashBalance: currentCashBalance,
            newWalletBalances: currentWalletBalances,
            message: limitCheck.message || "   ",
            error: "LIMIT_EXCEEDED"
          };
        }
      } catch (error) {
        console.error('     :', error);
        return {
          success: false,
          newCashBalance: currentCashBalance,
          newWalletBalances: currentWalletBalances,
          message: "     ",
          error: "LIMIT_CHECK_ERROR"
        };
      }
    }
    
    //    
    if (amount <= 0) {
      return {
        success: false,
        newCashBalance: currentCashBalance,
        newWalletBalances: currentWalletBalances,
        message: "      ",
        error: "INVALID_AMOUNT"
      };
    }

    //         
    if (selectedWalletId) {
      const available = Math.max(currentWalletBalances[selectedWalletId] || 0, 0);
      if (available < amount) {
        const target = wallets.find(w => w.id === selectedWalletId);
        return {
          success: false,
          newCashBalance: currentCashBalance,
          newWalletBalances: currentWalletBalances,
          message: `    ${target ? ` ${target.name}` : ''}. : ${available}  : ${amount} `,
          error: "INSUFFICIENT_WALLET_BALANCE"
        };
      }
    } else {
      const totalAvailable = WalletCashService.calculateTotalWalletBalance(currentWalletBalances);
      if (totalAvailable < amount) {
        return {
          success: false,
          newCashBalance: currentCashBalance,
          newWalletBalances: currentWalletBalances,
          message: `    .  : ${totalAvailable}   : ${amount} `,
          error: "INSUFFICIENT_WALLET_BALANCE"
        };
      }
    }

    //       
    const newWalletBalances = { ...currentWalletBalances };

    if (selectedWalletId) {
      const currentBalance = newWalletBalances[selectedWalletId] || 0;
      newWalletBalances[selectedWalletId] = currentBalance - amount;
    } else {
      let remainingAmount = amount;
      for (const wallet of wallets) {
        if (remainingAmount <= 0) break;
        const currentBalance = Math.max(newWalletBalances[wallet.id] || 0, 0);
        const deductAmount = Math.min(currentBalance, remainingAmount);
        if (deductAmount > 0) {
          newWalletBalances[wallet.id] = (newWalletBalances[wallet.id] || 0) - deductAmount;
          remainingAmount -= deductAmount;
        }
      }
      //   :      
    }

    const newCashBalance = currentCashBalance + amount;

    

    //         
    let message = "  ";
    if (selectedWalletId) {
      const afterBalance = newWalletBalances[selectedWalletId] || 0;
      if (afterBalance < 0) {
        message = `    :   ${selectedWalletId}  ${afterBalance} .`;
      }
    }

    return {
      success: true,
      newCashBalance,
      newWalletBalances,
      message
    };
  }

  /**
   *  :       
   * @param operation   
   * @returns  
   */
  static processDeposit(operation: TransferOperation): TransactionResult {
    const { amount, currentCashBalance, currentWalletBalances, wallets } = operation;
    
    //    
    if (amount <= 0) {
      return {
        success: false,
        newCashBalance: currentCashBalance,
        newWalletBalances: currentWalletBalances,
        message: "      ",
        error: "INVALID_AMOUNT"
      };
    }

    //       
    const totalWalletBalance = this.calculateTotalWalletBalance(currentWalletBalances);
    if (totalWalletBalance < amount) {
      return {
        success: false,
        newCashBalance: currentCashBalance,
        newWalletBalances: currentWalletBalances,
        message: `    .  : ${totalWalletBalance}   : ${amount} `,
        error: "INSUFFICIENT_WALLET_BALANCE"
      };
    }

    //    
    const newCashBalance = currentCashBalance + amount;

    //    
    const newWalletBalances = { ...currentWalletBalances };
    let remainingAmount = amount;

    //     
    const defaultWallet = wallets.find(w => w.isDefault);
    
    if (defaultWallet && newWalletBalances[defaultWallet.id] >= remainingAmount) {
      //        
      newWalletBalances[defaultWallet.id] -= remainingAmount;
      remainingAmount = 0;
    } else {
      //     
      for (const wallet of wallets) {
        if (remainingAmount <= 0) break;
        
        const balance = newWalletBalances[wallet.id] || 0;
        if (balance > 0) {
          const deductAmount = Math.min(balance, remainingAmount);
          newWalletBalances[wallet.id] = balance - deductAmount;
          remainingAmount -= deductAmount;
        }
      }
    }

    const message = remainingAmount > 0 
      ? `  .   ${amount - remainingAmount}    `
      : `  .   ${amount}    `;

    return {
      success: true,
      newCashBalance,
      newWalletBalances,
      message
    };
  }

  /**
   *  :       
   * @param operation   
   * @returns  
   */
  static async processWithdraw(operation: WithdrawOperation): Promise<TransactionResult> {
    const { amount, currentCashBalance, currentWalletBalances, wallets, selectedWalletId } = operation;
    
    //    
    if (amount <= 0) {
      return {
        success: false,
        newCashBalance: currentCashBalance,
        newWalletBalances: currentWalletBalances,
        message: "      ",
        error: "INVALID_AMOUNT"
      };
    }

    //       
    if (selectedWalletId && !operation.skipRemoteLimitsCheck) {
      try {
        const limitCheck = await walletDailyLimitsService.canPerformOperation(selectedWalletId, amount, 'withdraw');
        if (!limitCheck.canPerform) {
          return {
            success: false,
            newCashBalance: currentCashBalance,
            newWalletBalances: currentWalletBalances,
            message: limitCheck.message || "   ",
            error: "LIMIT_EXCEEDED"
          };
        }
      } catch (error) {
        console.error('     :', error);
        return {
          success: false,
          newCashBalance: currentCashBalance,
          newWalletBalances: currentWalletBalances,
          message: "     ",
          error: "LIMIT_CHECK_ERROR"
        };
      }
    }

    //        
    if (currentCashBalance < amount) {
      return {
        success: false,
        newCashBalance: currentCashBalance,
        newWalletBalances: currentWalletBalances,
        message: `     .  : ${currentCashBalance}   : ${amount} `,
        error: "INSUFFICIENT_CASH_BALANCE"
      };
    }

    //     
    const newCashBalance = currentCashBalance - amount;

    //     
    const newWalletBalances = { ...currentWalletBalances };
    const targetWallet = selectedWalletId ? wallets.find(w => w.id === selectedWalletId) : (wallets.find(w => w.isDefault) || wallets[0]);
    
    if (targetWallet) {
      const currentBalance = newWalletBalances[targetWallet.id] || 0;
      newWalletBalances[targetWallet.id] = currentBalance + amount;
    } else {
      return {
        success: false,
        newCashBalance: currentCashBalance,
        newWalletBalances: currentWalletBalances,
        message: "      ",
        error: "NO_WALLETS_AVAILABLE"
      };
    }

    

    return {
      success: true,
      newCashBalance,
      newWalletBalances,
      message: `  .   ${amount}    ${targetWallet?.name || ''}`
    };
  }

  /**
   *       
   * @param amount 
   * @param wallets  
   * @returns true    
   */
  static validateOperation(amount: number, wallets: Array<{ id: string; name: string }>): { valid: boolean; error?: string } {
    if (amount <= 0) {
      return { valid: false, error: "      " };
    }

    if (!wallets || wallets.length === 0) {
      return { valid: false, error: "   " };
    }

    return { valid: true };
  }

  /**
   *    
   * @param walletBalances  
   * @returns  
   */
  static calculateTotalWalletBalance(walletBalances: WalletBalance): number {
    return Object.values(walletBalances).reduce((total, balance) => total + Math.max(balance, 0), 0);
  }

  /**
   *         ()
   * @param amount   
   * @param currentCashBalance    
   * @param currentWalletBalances   
   * @param wallets  
   * @returns  
   */
  static deductFromWalletsAddToCash(
    amount: number,
    currentCashBalance: number,
    currentWalletBalances: WalletBalance,
    wallets: Array<{ id: string; name: string; isDefault?: boolean }>
  ): TransactionResult {
    return this.processTransfer({
      amount,
      currentCashBalance,
      currentWalletBalances,
      wallets
    });
  }

  /**
   *         ()
   * @param amount   
   * @param currentCashBalance    
   * @param currentWalletBalances   
   * @param wallets  
   * @returns  
   */
  static deductFromWalletsAddToCashDeposit(
    amount: number,
    currentCashBalance: number,
    currentWalletBalances: WalletBalance,
    wallets: Array<{ id: string; name: string; isDefault?: boolean }>
  ): TransactionResult {
    return this.processDeposit({
      amount,
      currentCashBalance,
      currentWalletBalances,
      wallets
    });
  }

  /**
   *         ()
   * @param amount   
   * @param currentCashBalance    
   * @param currentWalletBalances   
   * @param wallets  
   * @returns  
   */
  static deductFromCashAddToWallets(
    amount: number,
    currentCashBalance: number,
    currentWalletBalances: WalletBalance,
    wallets: Array<{ id: string; name: string; isDefault?: boolean }>
  ): TransactionResult {
    return this.processWithdraw({
      amount,
      currentCashBalance,
      currentWalletBalances,
      wallets
    });
  }

  /**
   *        
   * @param result  
   * @param updateCashBalance    
   * @param updateWalletBalances    
   */
  static applyTransactionResult(
    result: TransactionResult,
    updateCashBalance: (newBalance: number) => void,
    updateWalletBalances: (newBalances: WalletBalance) => void
  ): void {
    if (result.success) {
      updateCashBalance(result.newCashBalance);
      updateWalletBalances(result.newWalletBalances);
    }
  }

  /**
   *      
   * @param amount 
   * @param operationType   (transfer  withdraw  deposit)
   * @param currentCashBalance    
   * @param currentWalletBalances   
   * @returns  
   */
  static validateTransaction(
    amount: number,
    operationType: 'transfer' | 'withdraw' | 'deposit',
    currentCashBalance: number,
    currentWalletBalances: WalletBalance
  ): { isValid: boolean; warning?: string; error?: string } {
    if (amount <= 0) {
      return {
        isValid: false,
        error: "      "
      };
    }

    if (operationType === 'transfer') {
      const totalWalletBalance = this.calculateTotalWalletBalance(currentWalletBalances);
      if (totalWalletBalance < amount) {
        return {
          isValid: false,
          error: `    (${totalWalletBalance} )     (${amount} )`
        };
      }
    } else if (operationType === 'withdraw') {
      if (currentCashBalance < amount) {
        return {
          isValid: false,
          error: `     (${currentCashBalance} )     (${amount} )`
        };
      }
    } else if (operationType === 'deposit') {
      const totalWalletBalance = this.calculateTotalWalletBalance(currentWalletBalances);
      if (totalWalletBalance < amount) {
        return {
          isValid: false,
          error: `    (${totalWalletBalance} )     (${amount} )`
        };
      }
    }

    return { isValid: true };
  }

  /**
   *        ( )
   * @param amount   
   * @param currentWalletBalances   
   * @param wallets  
   * @returns  
   */
  static getDeductionPreview(
    amount: number, 
    currentWalletBalances: WalletBalance, 
    wallets: Array<{ id: string; name: string; isDefault?: boolean }>
  ): Array<{ walletId: string; walletName: string; currentBalance: number; deductedAmount: number; newBalance: number }> {
    const preview: Array<{ walletId: string; walletName: string; currentBalance: number; deductedAmount: number; newBalance: number }> = [];
    let remainingAmount = amount;

    //    
    for (const wallet of wallets) {
      if (remainingAmount <= 0) break;
      
      const currentBalance = currentWalletBalances[wallet.id] || 0;
      const deductAmount = Math.min(Math.max(currentBalance, 0), remainingAmount);
      
      if (deductAmount > 0) {
        preview.push({
          walletId: wallet.id,
          walletName: wallet.name,
          currentBalance,
          deductedAmount: deductAmount,
          newBalance: currentBalance - deductAmount
        });
        remainingAmount -= deductAmount;
      }
    }

    //       

    return preview;
  }
}

export default WalletCashService;
