import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Progress } from '@/components/ui/progress';
import { Wallet, Phone, DollarSign, Star, CreditCard, TrendingUp, TrendingDown, ArrowLeft, IdCard, Building, Edit, Trash2 } from 'lucide-react';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { merchantWalletService, transactionService } from '@/integrations/firebase/services';
import walletDailyLimitsService from '@/utils/walletDailyLimitsService';
import { useToast } from '@/hooks/use-toast';

interface Wallet {
  id: string;
  name: string;
  phone: string;
  nationalId?: string;
  isDefault: boolean;
  isActive?: boolean;
  monthlyWithdrawalLimit: number;
  monthlyTransferLimit: number;
  monthlyWithdrawalUsage?: number;
  monthlyTransferUsage?: number;
  lastResetDate?: string;
  walletProvider?: string;
}

interface WalletsViewPageProps {
  isOpen: boolean;
  onClose: () => void;
  onWalletSelect: (wallet: Wallet) => void;
  onEditWallet?: (wallet: Wallet) => void;
  onDeleteWallet?: (walletId: string) => void;
}

//   
const walletProviders = [
  {
    id: 'vodafone',
    name: ' ',
    ownerName: '  ',
    nationalId: '29012345678901'
  },
  {
    id: 'etisalat',
    name: ' ',
    ownerName: '  ',
    nationalId: '28506789012345'
  },
  {
    id: 'orange',
    name: ' ',
    ownerName: '  ',
    nationalId: '29309876543210'
  },
  {
    id: 'we_pay',
    name: ' ',
    ownerName: '  ',
    nationalId: '29112345678901'
  },
  {
    id: 'smart_wallet',
    name: ' ',
    ownerName: '   ',
    nationalId: '28809876543210'
  },
  {
    id: 'access_pay',
    name: ' ',
    ownerName: '  ',
    nationalId: '29456789012345'
  },
  {
    id: 'insta_pay',
    name: ' ',
    ownerName: '  ',
    nationalId: '29012345678901'
  }
];

const WalletsViewPage: React.FC<WalletsViewPageProps> = ({ isOpen, onClose, onWalletSelect, onEditWallet, onDeleteWallet }) => {
  const { user } = useFirebaseAuth();
  const [wallets, setWallets] = useState<Wallet[]>([]);
  const [selectMode, setSelectMode] = useState<boolean>(false);
  const [selectedIds, setSelectedIds] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();

  //     localStorage  
  const getWalletsKey = () => `wallets_${user?.uid || 'default'}`;

  //          
  const loadWalletsFromLocalWithMigration = (): any[] | null => {
    try {
      const currentKey = getWalletsKey();
      const current = localStorage.getItem(currentKey);
      if (current) {
        const parsed = JSON.parse(current);
        if (Array.isArray(parsed) && parsed.length > 0) return parsed;
      }
      const legacyDefaultKey = 'wallets_default';
      const legacyDefault = localStorage.getItem(legacyDefaultKey);
      if (legacyDefault) {
        const parsed = JSON.parse(legacyDefault);
        if (Array.isArray(parsed) && parsed.length > 0) {
          localStorage.setItem(currentKey, legacyDefault);
          try { localStorage.removeItem(legacyDefaultKey); } catch {}
          return parsed;
        }
      }
      const keys = Object.keys(localStorage).filter(k => k.startsWith('wallets_') && k !== currentKey);
      let best: any[] | null = null;
      let bestKey: string | null = null;
      for (const k of keys) {
        try {
          const s = localStorage.getItem(k);
          if (!s) continue;
          const parsed = JSON.parse(s);
          if (Array.isArray(parsed) && parsed.length > 0) {
            if (!best || parsed.length > (best?.length || 0)) {
              best = parsed;
              bestKey = k;
            }
          }
        } catch {}
      }
      if (best && bestKey) {
        localStorage.setItem(currentKey, JSON.stringify(best));
        try { localStorage.removeItem(bestKey); } catch {}
        return best;
      }
    } catch (e) {
      console.warn('WalletsViewPage migration failed:', e);
    }
    return null;
  };

  //     
  const calculateWalletMonthlyUsage = async (walletId: string, transactions: any[]) => {
    const currentMonth = new Date().getMonth();
    const currentYear = new Date().getFullYear();
    
    const monthlyTransactions = transactions.filter(t => {
      const transactionDate = new Date(t.createdAt);
      return (
        transactionDate.getMonth() === currentMonth && 
        transactionDate.getFullYear() === currentYear &&
        //     (   )
        (t.lineId === walletId || t.fromWallet === walletId)
      );
    });

    //           
    const isWithdrawTx = (t: any) => {
      const type = t.type;
      const xn = (t as any).txnType;
      return (
        xn === 'receive' ||
        type === 'withdraw' ||
        type === 'withdrawal' ||
        type === 'receive'
      );
    };

    const isSendTx = (t: any) => {
      const type = t.type;
      const xn = (t as any).txnType;
      return (
        xn === 'send' ||
        type === 'send' ||
        type === 'transfer'
      );
    };

    const withdrawalUsage = monthlyTransactions
      .filter(isWithdrawTx)
      .reduce((sum, t) => {
        const amount = (t as any).withdrawnAmount !== undefined ? (t as any).withdrawnAmount : t.amount;
        return sum + (amount || 0);
      }, 0);

    const transferUsage = monthlyTransactions
      .filter(isSendTx)
      .reduce((sum, t) => {
        const amount = (t as any).sentAmount !== undefined ? (t as any).sentAmount : t.amount;
        return sum + (amount || 0);
      }, 0);

    return { withdrawalUsage, transferUsage };
  };

  //      
  const checkAndResetMonthlyUsage = (wallet: Wallet): Wallet => {
    const today = new Date().toISOString().split('T')[0];
    const lastReset = wallet.lastResetDate;
    
    if (!lastReset) {
      return {
        ...wallet,
        monthlyWithdrawalUsage: 0,
        monthlyTransferUsage: 0,
        lastResetDate: today
      };
    }

    const lastResetDate = new Date(lastReset);
    const currentDate = new Date();
    
    //      
    if (lastResetDate.getMonth() !== currentDate.getMonth() || 
        lastResetDate.getFullYear() !== currentDate.getFullYear()) {
      return {
        ...wallet,
        monthlyWithdrawalUsage: 0,
        monthlyTransferUsage: 0,
        lastResetDate: today
      };
    }

    return wallet;
  };

  //  
  useEffect(() => {
    if (!user?.uid || !isOpen) return;

    const loadWallets = async () => {
      setIsLoading(true);
      try {
        const firebaseWallets = await merchantWalletService.getByMerchantId(user.uid);
        const transactions = await transactionService.getByUserId(user.uid);
        
        const formattedWallets = await Promise.all(firebaseWallets.map(async w => {
          const { withdrawalUsage, transferUsage } = await calculateWalletMonthlyUsage(w.id, transactions);
          const limits = await walletDailyLimitsService.getWalletLimits(w.id);
          return {
            id: w.id,
            name: w.label || '  ',
            phone: w.msisdn,
            nationalId: w.nationalId || '',
            isDefault: false,
            isActive: (w as any).isActive === true,
            monthlyWithdrawalLimit: (limits?.monthlyWithdrawLimit ?? w.withdrawLimit ?? 200000),
            monthlyTransferLimit: (limits?.monthlyTransferLimit ?? w.transferLimit ?? 200000),
            monthlyWithdrawalUsage: (limits?.monthlyWithdrawUsed ?? withdrawalUsage ?? 0),
            monthlyTransferUsage: (limits?.monthlyTransferUsed ?? transferUsage ?? 0),
            lastResetDate: new Date().toISOString().split('T')[0],
            walletProvider: (w.walletProvider || '')
          };
        }));
        
        const walletsWithResetCheck = formattedWallets.map(checkAndResetMonthlyUsage);
        setWallets(walletsWithResetCheck);
        
      } catch (error) {
        console.error('Error loading wallets:', error);
        //          Firestore
        toast({
          title: '   ',
          description: '      .      .           .',
          variant: 'destructive'
        });
        
        //              
     } finally {
       setIsLoading(false);
     }
    };

    loadWallets();
  }, [user?.uid, isOpen]);

  //      
  const getProviderInfo = (providerId: string) => {
    return walletProviders.find(p => p.id === providerId);
  };

  //     
  const getUsagePercentage = (used: number, limit: number) => {
    return limit > 0 ? Math.min((used / limit) * 100, 100) : 0;
  };

  //   
  const formatNumber = (num: number) => {
    return new Intl.NumberFormat('ar-EG').format(num);
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-6xl max-h-[90vh] overflow-y-auto">
        <DialogHeader className="pb-4">
          <DialogTitle className="flex items-center gap-2 text-lg">
            <Wallet className="h-5 w-5" />
               
            <Badge variant="secondary" className="mr-2">
              {wallets.length} 
            </Badge>
          </DialogTitle>
          <div className="flex items-center gap-2 mt-2">
            {!selectMode ? (
              <Button
                variant="outline"
                size="sm"
                onClick={() => setSelectMode(true)}
                className="h-7"
              >
                 
              </Button>
            ) : (
              <>
                <Button
                  size="sm"
                  onClick={async () => {
                    try {
                      if (!user?.uid) return;
                      const ops = wallets.map(w => merchantWalletService.update(w.id, { isActive: selectedIds.includes(w.id) }));
                      await Promise.all(ops);
                      setWallets(prev => prev.map(w => ({ ...w, isActive: selectedIds.includes(w.id) })));
                      setSelectMode(false);
                      toast({ title: '  ', description: '      ' });
                      try { window.dispatchEvent(new CustomEvent('wallets:activation-updated')); } catch {}
                    } catch (e) {
                      console.error('Error activating wallets:', e);
                      toast({ title: ' ', description: '   ', variant: 'destructive' });
                    }
                  }}
                  className="h-7"
                >
                   
                </Button>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => { setSelectMode(false); setSelectedIds([]); }}
                  className="h-7"
                >
                  
                </Button>
              </>
            )}
          </div>
        </DialogHeader>

        {isLoading ? (
          <div className="flex justify-center items-center py-8">
            <div className="text-center">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-2"></div>
              <p className="text-sm text-gray-600">  ...</p>
            </div>
          </div>
        ) : wallets.length === 0 ? (
          <div className="text-center py-8">
            <Wallet className="h-12 w-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-600">     </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {wallets.map((wallet) => {
              const providerInfo = getProviderInfo(wallet.walletProvider || '');
              const withdrawalPercentage = getUsagePercentage(
                wallet.monthlyWithdrawalUsage || 0,
                wallet.monthlyWithdrawalLimit
              );
              const transferPercentage = getUsagePercentage(
                wallet.monthlyTransferUsage || 0,
                wallet.monthlyTransferLimit
              );

              const isSelected = selectedIds.includes(wallet.id);
              const cardClasses = selectMode
                ? `cursor-pointer transition-shadow border-2 ${isSelected ? 'border-blue-500 shadow-lg' : 'hover:border-blue-300'}`
                : 'cursor-pointer hover:shadow-lg transition-shadow border-2 hover:border-blue-300';

              return (
                <Card 
                  key={wallet.id} 
                  className={cardClasses}
                  onClick={() => {
                    if (selectMode) {
                      setSelectedIds(prev => prev.includes(wallet.id) ? prev.filter(id => id !== wallet.id) : [...prev, wallet.id]);
                    } else {
                      onWalletSelect(wallet);
                    }
                  }}
                >
                  <CardHeader className="pb-3">
                    <CardTitle className="flex items-center justify-between text-sm">
                      <div className="flex items-center gap-2">
                        <CreditCard className="h-4 w-4" />
                        <span>{wallet.name}</span>
                      </div>
                      <div className="flex items-center gap-2">
                        {wallet.isActive && (
                          <Badge variant="default" className="text-xs"></Badge>
                        )}
                        {wallet.isDefault && (
                          <Badge variant="default" className="text-xs">
                            <Star className="h-3 w-3 mr-1" />
                            
                          </Badge>
                        )}
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={(e) => {
                            e.stopPropagation();
                            onEditWallet?.(wallet);
                          }}
                          className="h-7 px-2"
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={(e) => {
                            e.stopPropagation();
                            onDeleteWallet?.(wallet.id);
                          }}
                          className="h-7 px-2 text-destructive hover:text-destructive"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </CardTitle>
                  </CardHeader>
                  
                  <CardContent className="space-y-3">
                    {/*   */}
                    <div className="space-y-2">
                      <div className="flex items-center gap-2 text-sm">
                        <Phone className="h-3 w-3 text-gray-500" />
                        <span className="font-mono">{wallet.phone}</span>
                      </div>
                      
                      {wallet.nationalId && (
                        <div className="flex items-center gap-2 text-sm">
                          <IdCard className="h-3 w-3 text-gray-500" />
                          <span className="font-mono">{wallet.nationalId}</span>
                        </div>
                      )}
                      
                      {providerInfo && (
                        <div className="flex items-center gap-2 text-sm">
                          <Building className="h-3 w-3 text-gray-500" />
                          <span>{providerInfo.name}</span>
                        </div>
                      )}
                    </div>

                    {/*        */}
                    {(() => {
                      const monthlyWithdrawalLimit = wallet.monthlyWithdrawalLimit;
                      const monthlyTransferLimit = wallet.monthlyTransferLimit;
                      const monthlyWithdrawUsed = wallet.monthlyWithdrawalUsage || 0;
                      const monthlyTransferUsed = wallet.monthlyTransferUsage || 0;
                      const remainingWithdraw = Math.max(monthlyWithdrawalLimit - monthlyWithdrawUsed, 0);
                      const remainingTransfer = Math.max(monthlyTransferLimit - monthlyTransferUsed, 0);

                      return (
                        <div className="bg-gradient-to-r from-gray-50 to-gray-100 p-2 rounded-md border border-gray-200 shadow-sm space-y-2">
                          <div className="text-xs text-gray-600 font-medium text-center">
                             : {providerInfo?.name || wallet.walletProvider} - {wallet.phone}
                          </div>
                          <div className="w-full bg-gray-200 rounded-full h-1 overflow-hidden flex">
                            <div className="w-1/2 bg-red-200 relative">
                              <div
                                className="bg-gradient-to-r from-red-400 to-red-600 h-1 transition-all duration-200 ease-out shadow-sm"
                                style={{ width: `${Math.min(((monthlyWithdrawUsed / Math.max(monthlyWithdrawalLimit, 1)) * 100), 100)}%` }}
                              ></div>
                            </div>
                            <div className="w-1/2 bg-blue-200 relative">
                              <div
                                className="bg-gradient-to-r from-blue-400 to-blue-600 h-1 transition-all duration-200 ease-out shadow-sm"
                                style={{ width: `${Math.min(((monthlyTransferUsed / Math.max(monthlyTransferLimit, 1)) * 100), 100)}%` }}
                              ></div>
                            </div>
                          </div>
                          <div className="flex justify-between items-center mt-1">
                            <div className="text-xs text-red-600 font-medium">
                               : {formatNumber(remainingWithdraw)} 
                            </div>
                            <div className="text-xs text-blue-600 font-medium">
                               : {formatNumber(remainingTransfer)} 
                            </div>
                          </div>
                        </div>
                      );
                    })()}

                    {/*    */}
                    <Button 
                      variant="outline" 
                      size="sm" 
                      className="w-full text-xs"
                      onClick={(e) => {
                        e.stopPropagation();
                        onWalletSelect(wallet);
                      }}
                    >
                        
                    </Button>
                  </CardContent>
                </Card>
              );
            })}
          </div>
        )}

        <div className="flex justify-end pt-4 border-t">
          <Button onClick={onClose} variant="outline">
            <ArrowLeft className="h-4 w-4 mr-2" />
            
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default WalletsViewPage;