import { merchantWalletService } from '@/integrations/firebase/services';
import { db, auth } from '@/firebase';
import { doc, setDoc, getDoc, collection, query, where, getDocs, serverTimestamp, Timestamp } from 'firebase/firestore';

// Runtime guard to prevent invalid Firestore document paths
const isValidId = (id: any): id is string => typeof id === 'string' && id.trim().length > 0;

//    
interface WalletUsage {
  walletId: string;
  used_transfer: number;
  used_withdraw: number;
  monthly_limit: number;
  last_reset: Timestamp | null;
  updatedAt: Timestamp;
}

//   
interface Transaction {
  id: string;
  uid: string;
  type: 'transfer' | 'withdraw';
  amount: number;
  createdAt: Timestamp;
  status: string;
}

//    
export const walletLimitsService = {
  //     
  getCurrentMonthId(): string {
    const now = new Date();
    return `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, '0')}`;
  },

  //       Firebase
  async getWalletUsage(walletId: string): Promise<WalletUsage | null> {
    try {
      if (!isValidId(walletId)) {
        console.error('Error getting wallet usage: invalid walletId', walletId);
        return null;
      }
      //        walletLimits
      const limitsRef = doc(db, 'walletLimits', walletId);
      const limitsSnap = await getDoc(limitsRef);
      
      if (limitsSnap.exists()) {
        const data = limitsSnap.data();
        return {
          walletId,
          used_transfer: (data.monthlyTransferUsed ?? data.used_transfer ?? 0),
          used_withdraw: (data.monthlyWithdrawUsed ?? data.used_withdraw ?? 0),
          monthly_limit: (data.monthlyLimit ?? data.monthlyTransferLimit ?? data.monthlyWithdrawLimit ?? 200000),
          last_reset: (data.lastMonthlyReset ?? data.last_reset ?? null),
          updatedAt: data.updatedAt || Timestamp.now()
        };
      }
      
      //       
      const newUsage: WalletUsage = {
        walletId,
        used_transfer: 0,
        used_withdraw: 0,
        monthly_limit: 200000,
        last_reset: null,
        updatedAt: Timestamp.now()
      };
      
      //   
      await setDoc(limitsRef, {
        monthlyTransferUsed: 0,
        monthlyWithdrawUsed: 0,
        monthlyTransferLimit: 200000,
        monthlyWithdrawLimit: 200000,
        lastMonthlyReset: null,
        updatedAt: serverTimestamp(),
        ownerId: auth.currentUser?.uid
      });
      
      return newUsage;
    } catch (error) {
      console.error('Error getting wallet usage:', error);
      return {
        walletId,
        used_transfer: 0,
        used_withdraw: 0,
        monthly_limit: 200000,
        last_reset: null,
        updatedAt: Timestamp.now()
      };
    }
  },

  //        (   )
  shouldResetLimits(lastReset: Timestamp | null): boolean {
    const now = new Date();
    const currentDay = now.getDate();
    
    if (!lastReset) {
      return currentDay === 1;
    }
    
    //           
    if (currentDay === 1) {
      const lastResetDate = lastReset.toDate();
      const currentMonth = now.getMonth();
      const currentYear = now.getFullYear();
      
      //         
      return (lastResetDate.getMonth() !== currentMonth || lastResetDate.getFullYear() !== currentYear);
    }
    
    return false;
  },

  //      
  async autoResetLimitsIfNeeded(walletId: string): Promise<boolean> {
    try {
      if (!isValidId(walletId)) {
        console.error('    : walletId  ', walletId);
        return false;
      }
      // Debug
      if (typeof import.meta !== 'undefined' && (import.meta as any).env && (import.meta as any).env.DEV) {
        console.debug('autoResetLimitsIfNeeded -> walletId:', walletId);
      }
      const usage = await this.getWalletUsage(walletId);
      if (!usage) return false;
      
      if (this.shouldResetLimits(usage.last_reset)) {
        console.log(`     ${walletId} -    `);
        return await this.resetWalletUsage(walletId);
      }
      
      return false;
    } catch (error) {
      console.error('    :', error);
      return false;
    }
  },
  
  //     serverTimestamp
  async resetWalletUsage(walletId: string): Promise<boolean> {
    try {
      if (!isValidId(walletId)) {
        console.error('    : walletId  ', walletId);
        return false;
      }
      const limitsRef = doc(db, 'walletLimits', walletId);
      
      //       serverTimestamp
      await setDoc(limitsRef, {
        monthlyTransferUsed: 0,
        monthlyWithdrawUsed: 0,
        lastMonthlyReset: serverTimestamp(),
        updatedAt: serverTimestamp()
      }, { merge: true });
      
      console.log(`     ${walletId} `);
      return true;
    } catch (error) {
      console.error('    :', error);
      return false;
    }
  },

  //        last_reset
  async calculateUsageFromTransactions(walletId: string): Promise<{ used_transfer: number; used_withdraw: number }> {
    try {
      if (!isValidId(walletId)) {
        console.error('     : walletId  ', walletId);
        return { used_transfer: 0, used_withdraw: 0 };
      }
      const limitsRef = doc(db, 'walletLimits', walletId);
      const now = new Date();
      let lastReset: Timestamp | null = Timestamp.fromDate(new Date(now.getFullYear(), now.getMonth(), 1));
      try {
        const limitsSnap = await getDoc(limitsRef);
        lastReset = limitsSnap.exists() ? (limitsSnap.data()?.lastMonthlyReset ?? limitsSnap.data()?.last_reset ?? lastReset) : lastReset;
      } catch {}
      
      //   
      const txQuery = query(
        collection(db, 'tx'),
        where('uid', '==', walletId),
        ...(lastReset ? [where('createdAt', '>=', lastReset)] : [])
      );
      
      const txSnap = await getDocs(txQuery);
      let used_transfer = 0;
      let used_withdraw = 0;
      
      txSnap.forEach((doc) => {
        const tx = doc.data() as Transaction;
        if (tx.status === 'completed') {
          if (tx.type === 'transfer') {
            used_transfer += tx.amount;
          } else if (tx.type === 'withdraw') {
            used_withdraw += tx.amount;
          }
        }
      });
      
      return { used_transfer, used_withdraw };
    } catch (error) {
      console.error('     :', error);
      return { used_transfer: 0, used_withdraw: 0 };
    }
  },

  //          (Optimistic UI)
  async getWalletUsageWithRefresh(walletId: string): Promise<WalletUsage | null> {
    try {
      if (!isValidId(walletId)) {
        console.error('      : walletId  ', walletId);
        return null;
      }
      // Debug
      if (typeof import.meta !== 'undefined' && (import.meta as any).env && (import.meta as any).env.DEV) {
        console.debug('getWalletUsageWithRefresh -> walletId:', walletId);
      }
      //     
      const usage = await this.getWalletUsage(walletId);
      if (!usage) {
        const calculatedUsage = await this.calculateUsageFromTransactions(walletId);
        const limitsRef = doc(db, 'walletLimits', walletId);
        try {
          await setDoc(limitsRef, {
            monthlyTransferUsed: calculatedUsage.used_transfer,
            monthlyWithdrawUsed: calculatedUsage.used_withdraw,
            updatedAt: serverTimestamp(),
            ownerId: auth.currentUser?.uid
          }, { merge: true });
        } catch {}
        return {
          walletId,
          used_transfer: calculatedUsage.used_transfer,
          used_withdraw: calculatedUsage.used_withdraw,
          monthly_limit: 200000,
          last_reset: null,
          updatedAt: Timestamp.now()
        };
      }
      
      //        
      const calculatedUsage = await this.calculateUsageFromTransactions(walletId);
      
      //      
      if (usage.used_transfer !== calculatedUsage.used_transfer || 
          usage.used_withdraw !== calculatedUsage.used_withdraw) {
        
        const limitsRef = doc(db, 'walletLimits', walletId);
        
        try {
          await setDoc(limitsRef, {
            monthlyTransferUsed: calculatedUsage.used_transfer,
            monthlyWithdrawUsed: calculatedUsage.used_withdraw,
            updatedAt: serverTimestamp(),
            ownerId: auth.currentUser?.uid
          }, { merge: true });
        } catch {}
        
        //    
        return await this.getWalletUsage(walletId);
      }
      
      return usage;
    } catch (error) {
      console.error('      :', error);
      return {
        walletId,
        used_transfer: 0,
        used_withdraw: 0,
        monthly_limit: 200000,
        last_reset: null,
        updatedAt: Timestamp.now()
      };
    }
  },

  //  : remaining = monthly_limit - used_*
  calculateRemaining(usage: WalletUsage): { remainingTransfer: number; remainingWithdraw: number } {
    return {
      remainingTransfer: Math.max(0, usage.monthly_limit - usage.used_transfer),
      remainingWithdraw: Math.max(0, usage.monthly_limit - usage.used_withdraw)
    };
  },

  //          
  async canPerformOperation(walletId: string, amount: number, type: 'transfer' | 'withdraw'): Promise<{ canPerform: boolean; message?: string; remaining?: number }> {
    try {
      if (!isValidId(walletId)) {
        console.error('Error checking operation limits: invalid walletId', walletId);
        return { canPerform: false, message: 'walletId  ' };
      }
      //     
      const usage = await this.getWalletUsageWithRefresh(walletId);
      if (!usage) {
        return { canPerform: false, message: '      ' };
      }

      const remaining = this.calculateRemaining(usage);
      
      if (type === 'transfer') {
        if (amount > remaining.remainingTransfer) {
          return { 
            canPerform: false, 
            message: `   . : ${remaining.remainingTransfer.toLocaleString()} .   ${usage.monthly_limit.toLocaleString()} .`,
            remaining: remaining.remainingTransfer
          };
        }
      }

      if (type === 'withdraw') {
        if (amount > remaining.remainingWithdraw) {
          return { 
            canPerform: false, 
            message: `   . : ${remaining.remainingWithdraw.toLocaleString()} .   ${usage.monthly_limit.toLocaleString()} .`,
            remaining: remaining.remainingWithdraw
          };
        }
      }

      return { canPerform: true };
    } catch (error) {
      console.error('Error checking operation limits:', error);
      return { canPerform: false, message: '    ' };
    }
  },
  
  //       Optimistic UI
  async updateUsage(walletId: string, amount: number, type: 'transfer' | 'withdraw'): Promise<WalletUsage> {
    try {
      if (!isValidId(walletId)) {
        throw new Error('walletId  ');
      }
      //      
      const currentUsage = await this.getWalletUsageWithRefresh(walletId);
      if (!currentUsage) {
        throw new Error('      ');
      }

      //     
      const canPerform = await this.canPerformOperation(walletId, amount, type);
      if (!canPerform.canPerform) {
        throw new Error(canPerform.message || '   ');
      }

      //  
      const newUsedTransfer = type === 'transfer' ? currentUsage.used_transfer + amount : currentUsage.used_transfer;
      const newUsedWithdraw = type === 'withdraw' ? currentUsage.used_withdraw + amount : currentUsage.used_withdraw;

      //    Firebase  await
      const ref = doc(db, 'walletLimits', currentUsage.walletId);
      
      try {
        await setDoc(ref, {
          monthlyTransferUsed: newUsedTransfer,
          monthlyWithdrawUsed: newUsedWithdraw,
          updatedAt: serverTimestamp(),
          ownerId: auth.currentUser?.uid
        }, { merge: true });
      } catch {}

      //      
      let updatedUsage = await this.getWalletUsageWithRefresh(walletId);
      if (!updatedUsage) {
        updatedUsage = {
          walletId,
          used_transfer: newUsedTransfer,
          used_withdraw: newUsedWithdraw,
          monthly_limit: currentUsage.monthly_limit,
          last_reset: currentUsage.last_reset,
          updatedAt: Timestamp.now()
        } as WalletUsage;
      }

      return updatedUsage;
    } catch (error) {
      console.error('Error updating wallet usage:', error);
      throw error;
    }
  },

  //       Firebase
  async resetWalletUsageManually(walletId: string): Promise<WalletUsage> {
    try {
      if (!isValidId(walletId)) {
        throw new Error('walletId  ');
      }
      const ref = doc(db, 'walletLimits', walletId);
      
      //       
      const resetUsage: WalletUsage = {
        walletId,
        used_transfer: 0,
        used_withdraw: 0,
        monthly_limit: 0,
        last_reset: null,
        updatedAt: new Date() as any
      };

      //    Firebase  
      setDoc(ref, {
        monthlyTransferUsed: 0,
        monthlyWithdrawUsed: 0,
        lastMonthlyReset: serverTimestamp(),
        updatedAt: serverTimestamp()
      }, { merge: true }).catch(error => {
        console.error('      Firebase:', error);
      });

      console.log(`     ${walletId} `);
      return resetUsage;
    } catch (error) {
      console.error('Error resetting wallet usage:', error);
      throw error;
    }
  },

  //      
  async getAllWalletsUsage(merchantUserId: string): Promise<WalletUsage[]> {
    try {
      //     
      const wallets = await merchantWalletService.getByMerchantId(merchantUserId);
      const usagePromises = wallets.map(wallet => this.getWalletUsageWithRefresh(wallet.id));
      const usageResults = await Promise.all(usagePromises);
      
      return usageResults.filter(usage => usage !== null) as WalletUsage[];
    } catch (error) {
      console.error('Error getting all wallets usage:', error);
      return [];
    }
  },




};
