import React, { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Lock, Eye, EyeOff, AlertCircle, DollarSign } from 'lucide-react';
import { MasterPinService } from '@/utils/masterPinService';

interface EditBalanceDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onSuccess: (newAmount: number) => void;
  title: string;
  description: string;
  currentBalance: number;
  balanceLabel: string;
  userId?: string;
}

export function EditBalanceDialog({ 
  open, 
  onOpenChange, 
  onSuccess, 
  title, 
  description,
  currentBalance,
  balanceLabel,
  userId
}: EditBalanceDialogProps) {
  const [pin, setPin] = useState('');
  const [newAmount, setNewAmount] = useState(currentBalance.toString());
  const [showPin, setShowPin] = useState(false);
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  //           

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    try {
      //    
      const amount = parseFloat(newAmount);
      if (isNaN(amount) || amount < 0) {
        setError('   ');
        setIsLoading(false);
        return;
      }

      const hasSecretPin = await MasterPinService.hasMasterPin(userId);
      if (hasSecretPin) {
        //     
        const isValid = await MasterPinService.verifyMasterPin(pin, userId);
        if (isValid) {
          onSuccess(amount);
          onOpenChange(false);
          setPin('');
          setNewAmount('');
        } else {
          setError('   ');
        }
      } else {
        setError('     .         ');
      }
    } catch (error) {
      setError('      ');
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setPin('');
    setNewAmount(currentBalance.toString());
    setError('');
    onOpenChange(false);
  };

  //      
  React.useEffect(() => {
    if (open) {
      setNewAmount(currentBalance.toString());
    }
  }, [currentBalance, open]);

  return (
    <Dialog open={open} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-md" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-right">
            <DollarSign className="h-5 w-5" />
            {title}
          </DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="text-sm text-muted-foreground text-right">
            {description}
          </div>

          {/*    */}
          <div className="bg-gray-50 p-3 rounded-lg">
            <Label className="text-sm font-medium text-gray-700">
              {balanceLabel} 
            </Label>
            <div className="text-lg font-bold text-gray-900 mt-1">
              {currentBalance.toLocaleString()} 
            </div>
          </div>

          {/*    */}
          <div className="space-y-2">
            <Label htmlFor="newAmount" className="text-right">
               
            </Label>
            <Input
              id="newAmount"
              type="number"
              step="0.01"
              min="0"
              value={newAmount}
              onChange={(e) => setNewAmount(e.target.value)}
              placeholder="  "
              className="text-right"
              required
            />
          </div>

          {/*    */}
          <div className="space-y-2">
            <Label htmlFor="pin" className="text-right">
               
            </Label>
            <div className="relative">
              <Input
                id="pin"
                type={showPin ? 'text' : 'password'}
                value={pin}
                onChange={(e) => setPin(e.target.value)}
                placeholder="  "
                className="text-right pr-10"
                required
              />
              <Button
                type="button"
                variant="ghost"
                size="sm"
                className="absolute left-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                onClick={() => setShowPin(!showPin)}
              >
                {showPin ? (
                  <EyeOff className="h-4 w-4" />
                ) : (
                  <Eye className="h-4 w-4" />
                )}
              </Button>
            </div>
          </div>

          {error && (
            <div className="flex items-center gap-2 text-sm text-red-600 bg-red-50 p-2 rounded">
              <AlertCircle className="h-4 w-4" />
              {error}
            </div>
          )}

          <div className="flex gap-2 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={handleClose}
              className="flex-1"
            >
              
            </Button>
            <Button
              type="submit"
              disabled={isLoading || !pin || !newAmount}
              className="flex-1"
            >
              {isLoading ? (
                <div className="flex items-center gap-2">
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
                   ...
                </div>
              ) : (
                <>
                  <Lock className="h-4 w-4 mr-2" />
                   
                </>
              )}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}