/**
 *       
 * Egyptian wallet number validation utilities
 */

//    
export type WalletProvider = 'vodafone' | 'orange' | 'etisalat' | 'we';

//   
export type ValidationErrorCode = 
  | 'INVALID_MSISDN'
  | 'PROVIDER_MISMATCH'
  | 'ALREADY_EXISTS'
  | 'UNAUTHENTICATED'
  | 'RLS_DENIED'
  | 'UNKNOWN';

//  
export interface ValidationResult {
  isValid: boolean;
  normalizedMsisdn?: string;
  errorCode?: ValidationErrorCode;
  errorMessage?: string;
}

//      
const PROVIDER_PREFIXES: Record<WalletProvider, string> = {
  vodafone: '010',
  orange: '012',
  etisalat: '011',
  we: '015'
};

/**
 *   MSISDN    E.164
 * @param rawMsisdn     
 * @returns    E.164  null    
 */
export function normalizeEgyptianMsisdn(rawMsisdn: string): string | null {
  if (!rawMsisdn) return null;

  //      
  let cleaned = rawMsisdn.replace(/[^0-9-]/g, '');
  
  //     
  cleaned = cleaned.replace(/[-]/g, (match) => {
    const arabicDigits = '';
    const englishDigits = '0123456789';
    return englishDigits[arabicDigits.indexOf(match)];
  });

  //     11    01
  if (cleaned.length !== 11 || !cleaned.startsWith('01')) {
    return null;
  }

  //   E.164: 01XXXXXXXXX  +201XXXXXXXXX
  return `+20${cleaned.substring(1)}`;
}

/**
 *        
 * @param provider   
 * @param normalizedMsisdn    E.164
 * @returns true       
 */
export function validateProviderMatch(provider: WalletProvider, normalizedMsisdn: string): boolean {
  if (!normalizedMsisdn.startsWith('+201')) return false;
  
  //      ( +201)
  const prefix = normalizedMsisdn.substring(3, 6); // +201XXXXXXXXX  0XX
  const expectedPrefix = PROVIDER_PREFIXES[provider];
  
  return prefix === expectedPrefix;
}

/**
 *      
 * @param provider  
 * @param rawMsisdn  
 * @returns        
 */
export function validateWalletNumber(provider: WalletProvider, rawMsisdn: string): ValidationResult {
  //  
  const normalizedMsisdn = normalizeEgyptianMsisdn(rawMsisdn);
  
  if (!normalizedMsisdn) {
    return {
      isValid: false,
      errorCode: 'INVALID_MSISDN',
      errorMessage: '   .  11    01'
    };
  }

  //    
  if (!validateProviderMatch(provider, normalizedMsisdn)) {
    const expectedPrefix = PROVIDER_PREFIXES[provider];
    return {
      isValid: false,
      errorCode: 'PROVIDER_MISMATCH',
      errorMessage: `  ${getProviderDisplayName(provider)}     ${expectedPrefix}`
    };
  }

  return {
    isValid: true,
    normalizedMsisdn
  };
}

/**
 *      
 * @param provider  
 * @returns   
 */
export function getProviderDisplayName(provider: WalletProvider): string {
  const names: Record<WalletProvider, string> = {
    vodafone: ' ',
    orange: ' ',
    etisalat: ' ',
    we: ' '
  };
  return names[provider];
}

/**
 *       
 * @param msisd  
 * @returns true     
 */
export function quickValidateMsisdn(msisdn: string): boolean {
  if (!msisdn) return false;
  
  //   
  const cleaned = msisdn.replace(/[^0-9-]/g, '');
  
  //   
  const normalized = cleaned.replace(/[-]/g, (match) => {
    const arabicDigits = '';
    const englishDigits = '0123456789';
    return englishDigits[arabicDigits.indexOf(match)];
  });

  //   : 11    01
  return /^01[0-9]{9}$/.test(normalized);
}

/**
 *  : 11     010  011  012  015
 *      /
 */
export function strictValidateMsisdn(msisdn: string): boolean {
  if (!msisdn) return false;
  const cleaned = msisdn.replace(/[^0-9-]/g, '');
  const normalized = cleaned.replace(/[-]/g, (match) => {
    const arabicDigits = '';
    const englishDigits = '0123456789';
    return englishDigits[arabicDigits.indexOf(match)];
  });
  return /^(010|011|012|015)[0-9]{8}$/.test(normalized);
}

/**
 *      
 * @param errorCode  
 * @returns   
 */
export function getErrorMessage(errorCode: ValidationErrorCode): string {
  const messages: Record<ValidationErrorCode, string> = {
    INVALID_MSISDN: '   .  11    01',
    PROVIDER_MISMATCH: '      ',
    ALREADY_EXISTS: '   ',
    UNAUTHENTICATED: '   ',
    RLS_DENIED: '      ',
    UNKNOWN: '      '
  };
  return messages[errorCode];
}